<template>
    <div class="receivables-warp">
        <query-list @query="query" :timeType="timeType" :timeSearch="true"></query-list>
        <div class="receivables-btn-warp">
            <span :class="{isactive:isActive}" @click="changeActive(0)"></span>
            <span :class="{isactive:!isActive}" @click="changeActive(1)"></span>
        </div>
        <!---->
        <div v-if="isActive">
            <el-table border :data="tableData1"  highlight-current-row  style="width: 100%"  height="700">
                <el-table-column property="fphone" label="" align="center">
                </el-table-column>
                <el-table-column property="fcertificateName" label="" align="center">
                </el-table-column>
                <el-table-column property="famount" label="()" align="center">
                </el-table-column>
                <el-table-column  label="" align="center">
                    <template scope="scope">
                        <span v-show="scope.row.ftype ==1"></span>
                        <span v-show="scope.row.ftype ==2"></span>
                        <span v-show="scope.row.ftype ==3"></span>
                        <span v-show="scope.row.ftype ==4"></span>
                        <span v-show="scope.row.ftype ==5"></span>
                        <span v-show="scope.row.ftype ==6"></span>
                        <span v-show="scope.row.ftype ==7"></span>
                        <span v-show="scope.row.ftype ==8"></span>
                        <span v-show="scope.row.ftype ==9"></span>
                        <span v-show="scope.row.ftype ==10"></span>
                        <span v-show="scope.row.ftype ==11"></span>
                    </template>
                </el-table-column>
                <el-table-column label="" align="center">
                    <template scope="scope">
                        <span v-show="scope.row.ffundType ==='0'"></span>
                        <span v-show="scope.row.ffundType ==1"></span>
                        <span v-show="scope.row.ffundType ==2"></span>
                        <span v-show="scope.row.ffundType ==3"></span>
                        <span v-show="scope.row.ffundType ==4"></span>
                        <span v-show="scope.row.ffundType ==5"></span>
                        <span v-show="scope.row.ffundType ==6"></span>
                        <span v-show="scope.row.ffundType ==7">SDK</span>
                        <span v-show="scope.row.ffundType ==8">SDK</span>
                        <span v-show="scope.row.ffundType ==9"></span>
                    </template>
                </el-table-column>
                <el-table-column property="fremark" label="" align="center">
                </el-table-column>
                <el-table-column property="fupdateTime" label="" align="center">
                </el-table-column>
            </el-table>
        </div>
        <!---->
        <div v-if="!isActive">
            <el-table border  :data="tableData2"  highlight-current-row  style="width: 100%"  height="700"
            v-loading="loading"
            element-loading-text="">
                <el-table-column property="phone" label="" align="center">
                </el-table-column>
                <el-table-column property="fcertificateName" label="" align="center">
                </el-table-column>
                <el-table-column property="famountprice" label="()" align="center">
                </el-table-column>
                <el-table-column  label="" align="center">
                    <template scope="scope">
                        <span v-if="scope.row.fpaytype ===0"></span>
                        <span v-else-if="scope.row.fpaytype===1"></span>
                        <span v-else-if="scope.row.fpaytype===2"></span>
                        <span v-else-if="scope.row.fpaytype===3"></span>
                        <span v-else-if="scope.row.fpaytype===10"></span>
                        <span v-else-if="scope.row.fpaytype===20"></span>
                        <span v-else-if="scope.row.fpaytype===30"></span>
                    </template>
                </el-table-column>
                <el-table-column  label="" align="center">
                    <template scope="scope">
                        <span v-if="scope.row.fpaytype ===0"></span>
                        <span v-else-if="scope.row.fpaytype===1"></span>
                        <span v-else-if="scope.row.fpaytype===2"></span>
                        <span v-else-if="scope.row.fpaytype===3"></span>
                        <span v-else-if="scope.row.fpaytype===10">SDK</span>
                        <span v-else-if="scope.row.fpaytype===20">SDK</span>
                        <span v-else-if="scope.row.fpaytype===30"></span>
                    </template>
                </el-table-column>
                <el-table-column label="" property="fpaymenttime" align="center">
                    <!--<template scope="scope">-->
                        <!--<span>{{scope.row.fpaymenttime | useTime}}</span>-->
                    <!--</template>-->
                </el-table-column>
            </el-table>
        </div>
        <div class="receivables-pagination">
            <el-pagination layout="total, sizes,prev, pager, next, jumper" :total="total"  :small="false" @current-change="handleCurrentChange"
                           :current-page="currentPage +1"
                           :page-size="size"
                           @size-change="handleSizeChange"
                           :page-sizes="[15, 20, 30, 100]">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import queryList from './queryWarp.vue'
    import {VERSION} from '@/util/constant.js'
    export default{
        created(){
            this.getListData();
        },
        components:{
            queryList
        },
        data(){
            return {
                timeType:'',
                total:0,
                size:15,
                currentPage:0,
                tableData1:[], // 
                tableData2:[], // 
                startTime:'',
                endTime:'',
                userId:'',
                isActive:true,
                loading:true
            }
        },
        methods:{
            handleSizeChange(size){
                this.size = size;
                this.currentPage = 0;
                this.getListData();
            },
            handleCurrentChange(page){
                this.currentPage = page - 1;
                this.getListData();
            },
            getListData(){
                this.loading = true;
                this.$http.post(this.isActive?`/finance/balanceCollectionForms.do`:`/finance/consumerForms.do`,{
                    pageSize:this.size,
                    pageNo:this.currentPage,
                    userId:this.userId==''?null:this.userId,
                    startTime:this.startTime==''?null:this.startTime,
                    endTime:this.endTime==''?null:this.endTime,
                    version:VERSION
                }).then(res => {
                    res = res.data;
                    if(res.success){
                        if(this.isActive){
                            this.tableData1 = res.data.list;
                            this.tableData2 = [];
                        }else{
                            this.tableData2 = res.data.list;
                            this.tableData1 = [];
                        }
                        this.total = res.data.total;
                        this.loading = false;
                    }else{
                        this.$message({
                          message: res.msg,
                          type: 'error'
                        });
                        this.total = 0 ;
                        this.loading = false;
                    }
                })/*.catch(e => {
                    this.loading = false;
                })*/
            },
            query(queryJson){
                this.startTime = queryJson.startTime;
                this.endTime =queryJson.endTime;
                this.userId = queryJson.userId;
                this.currentPage = 0;
                this.getListData();
            },
            changeActive(index){
                if(this.loading){
                    this.$message({
                        message: '!',
                        type: 'error'
                    });
                    return;
                }
                if(index === 0){
                    this.isActive = true;
                }else{
                    this.isActive = false;
                }
                //this.total = 0;
                this.currentPage = 0;
                this.getListData();
            }
        }
    }
</script>
<style>
    .receivables-warp{
        position:relative;
    }
    .receivables-pagination{
        text-align: center;
        position:absolute;
        bottom:-200px;
        width:100%;
        left:0px;
    }
    .receivables-pagination .el-pagination button,
    .receivables-pagination .el-pagination li{
        width:38px;
        height:38px;
        margin-right: 5px;
        line-height: 38px;
    }
    .receivables-pagination .el-pager li.btn-quicknext,
    .receivables-pagination .el-pager li.btn-quickprev
    {
        line-height: 38px;
    }
    .receivables-pagination .el-pagination .el-select .el-input input,
    .receivables-pagination .el-pagination span{
        height: 38px;
        line-height: 38px;
    }
    .receivables-pagination .el-pagination__editor{
        line-height: 26px;
        width: 38px;
        height: 36px;
    }
    .receivables-btn-warp{
        padding: 20px;
        border-top:2px solid #c6c6c6;
    }
    .receivables-btn-warp span{
        display: inline-block;
        border: 1px solid #c6c6c6;
        color:#8c8c8c;
        width: 90px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        cursor:pointer;
        border-radius: 2px;
    }
    .receivables-btn-warp span:nth-child(2){
        margin-left: 20px;
    }
    .receivables-btn-warp span.isactive{
        width: 92px;
        height: 32px;
        background: #38adff;
        line-height: 32px;
        color:#fff;
        border: 0px;
    }
</style>



