<template>
    <div class="partnerManage">
        <tHeader></tHeader>
        <div class='commodity-bg'>
            <div class='commodity-select-top'>
                <regional-choice
                    @getSalePlease='getPlease'
                    @getRegionSale='getRegion'
                    :parterCode='-2'
                    :defaultPlease='defaultPlease'
                    :defaultRegion='defaultRegion'
                    regionNameDf=':'
                >
                
                </regional-choice>
                <div class='parter-select-top'>
                    <partner
                        @getSelect='getSelectParter'
                        :codeName='codeName'
                        :partName='partnerName'
                        :isCommday='true'
                    ></partner>
                </div>
                <div class='partner-select'>
                    <carouse-select
                        textName=':'
                        :region='region'
                        @getSelect='getSelect'
                    ></carouse-select>
                </div>
                <div class='commodity-btn'>
                    <el-button type="primary" icon="search" @click='search'></el-button>
                </div>
                <div class='commodity-updata-btn'>
                    <el-button type="primary" @click='updataSetting'></el-button>
                </div>
            </div>
            <div class='partner-table'>
                <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" :row-class-name="tableRowClassName" style="width: 100%">
                    <el-table-column prop="number" label="" width="60">
                    </el-table-column>
                    <el-table-column prop="fgrouparea" label="" width="120">
                    </el-table-column>
                    <el-table-column prop="fsuppliername" label="" width="150">
                    </el-table-column>
                    <el-table-column prop="fproductname" label="" width="150">
                    </el-table-column>
                    <el-table-column prop="type" label="" >
                    </el-table-column>
                    <el-table-column prop="moneyType" label="" width="150">
                    </el-table-column>
                    <el-table-column label="" width="150">
                        <template scope="scope">
                            <div class='commodity-icon'>
                                <i class='el-icon-minus' @click='reduceNum(scope.row.forder, scope.row)'></i>
                                <input class='commodity-number' v-model="scope.row.forder" maxlength="4"/>
                                <i class='el-icon-plus' @click='addNum(scope.row.forder, scope.row)'></i>
                            </div>
                        </template>  
                    </el-table-column>
                    <el-table-column label="" width="170">
                        <template scope="scope">
                            <el-checkbox v-model="scope.row.checked" @change='showPartName(scope.row)'></el-checkbox>
                        </template>
                    </el-table-column>
                    <el-table-column prop="fauditor" label="" >
                    </el-table-column>
                    <el-table-column prop="fupdatetime" label="" width="150">
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
import carouseSelect from '@/components/public/select'
import tHeader from '@/components/public/tHeader'
import regionalChoice from '@/components/public/regionalChoice'
import partner from '@/components/public/Partner'
import {VERSION} from '@/util/constant.js' 
import {clippingProvice, clippingCity} from '@/util/common.js'
export default {
    created(){
        this.admin = this.$store.getters.doneAdmin;
        this.fsupplierid =  this.admin.ucompany;   //id
        // //
        // if(this.admin.ucompany != 100){
        //    this.defaultRegion = clippingProvice(this.admin.oprovince);
        //    this.defaultPlease = clippingCity(this.admin.ocity);
        //    this.partnerName =  this.admin.oname;      //
        //    this.search();
        // }
        //
        this.fkeyarea = this.admin.ocode;
    },
	data () {
		return {
            partnerName:'',
            defaultPlease:'',
            defaultRegion:'',
            admin:{},              //
            region:[
                {regionName:'', value:-1},
                {regionName:'', value:1},
                {regionName:'', value:2}
            ],
            fgroupareaid:'',       //
            fkeyarea:'',            //
            fsupplierid:'',        //ID
            codeName:'',           //
            fgrouptype:'',         //
            tableData:[],
            typeArry:{
                '0':'',
                '1':'',
                '2':'',
                '3':''
            }
		}
	},
	methods:{
        //
        tableRowClassName(row, index) {
            if (((index + 1) % 2) === 0) {
                return 'info-row';
            }
            return '';
        },
        //code
        getPlease(please){
            this.fgroupareaid = please.saleCode;
            this.codeName = please.name;
        },
        //
        getRegion(){
            this.codeName = '';
        },
        //
        getSelectParter(data){
            this.fsupplierid = data.id;
            this.fkeyarea = data.code;
            this.partnerName = data.name;
        },
        //
        addNum(num, data){
            data.forder = Number(num) + 1;
            if(data.forder > 9999){
                data.forder = 9999;
            }
        },
        //
        reduceNum(num, data){
            data.forder = Number(num) - 1;
            if(data.forder < 0){
                data.forder = 0;
            }
        },
        //
        getSelect(name){
           for(let i=0; i<this.region.length; i++){
               if(name == this.region[i].regionName){
                   this.fgrouptype = this.region[i].value;
               }
            }
            if(this.fgrouptype == -1){
                this.fgrouptype = '';
            }
        },
        //
        searchList(data){
            this.$http.post('/product/loadProducts.do', data).then(res => {
                if(res.data.success) {
                    var region = res.data.data;
                    for(let i=0; i < region.length; i++){
                        region[i].number = i + 1;
                        region[i].type = this.typeArry[region[i].fonoffstatus];
                        region[i].moneyType = region[i].fgrouptype == 1 ? '' : region[i].fgrouptype ==  2 ? '' : '/';
                        region[i].checked = region[i].fisdiplaysn == 0 ? false : true;
                    }
                    this.tableData = region;
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error',
                        duration: 2000
                    });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //
        search(){
            let data = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.fsupplierid,
                fonoffstatus:'2',
                fgroupareaid:this.fgroupareaid,
                pagenum:0,
                pagesize:1000,
                fgrouptype:this.fgrouptype
            }
            if(this.partnerName == ''){
                this.$message({
                    message: '',
                    type: 'warning',
                    duration: 2000
                });
                return
            }
            this.searchList(data);
        },
        //
        updataSetting(){
            var fid=[];
            var forder=[];
            var fisdiplaysn = [];
            for(let i=0; i<this.tableData.length; i++){
                fid.push(this.tableData[i].fid);
                forder.push(this.tableData[i].forder);
                fisdiplaysn.push(this.tableData[i].fisdiplaysn);
                if(!Number(this.tableData[i].forder) && this.tableData[i].forder != 0){
                    this.$message({
                        message: '0-9999',
                        type: 'error',
                        duration: 2000
                    });
                    return
                }
            }
            let data = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fid:fid.join('#'),
                forders:forder.join('#'),
                fisdiplaysn:fisdiplaysn.join('#')
            }
            if(data.fid == '' && data.forders == '' && data.fisdiplaysn == ''){
                this.$message({
                    message: '',
                    type: 'warning',
                    duration: 2000
                });
                return
            }
            this.$http.post('/product/updateForder.do', data).then(res => {
                  if(res.data.success){
                        this.$message({
                            message: '',
                            type: 'success',
                            duration: 2000
                        });
                        this.search();
                  }else{
                      this.$message({
                        message: res.data.msg,
                        type: 'error',
                        duration: 2000
                      });
                  }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //
        showPartName(data){
            if(data.checked){
                data.fisdiplaysn = '1'
            }else{
                data.fisdiplaysn = '0'
            }
        }
    },
    computed:{
        
    },
    components:{
        carouseSelect,
        tHeader,
        regionalChoice,
        partner
    }  
}
</script>


