<template>
    <div id="partner">
        <tHeader></tHeader>
        <div class="main">
            <ul class="clearfix search">
                <li>
                    &nbsp;:&nbsp;
                    <el-select v-model="province" filterable clearable :loading="pLoading" @change="getArea(province)" placeholder="">
                        <el-option
                        v-for="(item, index) in provinceTable"
                        :key="index"
                        :label="item.oprovince"
                        :value="item.oprovince">
                        </el-option>
                    </el-select>
                    <span class="line"></span>
                    <el-select v-model="city" filterable clearable :loading="cLoading" placeholder="">
                        <el-option
                        v-for="(item, index) in cityTable"
                        :key="index"
                        :label="item.ocity"
                        :value="item.ocity">
                        </el-option>
                    </el-select>
                </li>
                <li>
                    &nbsp;:&nbsp;
                </li>
                <li>
                    <el-input v-model.trim="company" placeholder=""></el-input>
                </li>
                <li>
                    <el-button type="primary" icon="search" @click="search"></el-button>
                </li>
            </ul>
            <div class="btns">
                <el-button type="primary" @click="addPartner"></el-button>
            </div>
            <div class="table">
                <el-table
                    v-loading="tableLoading"
                    :data="tableData"
                    stripe
                    border
                    style="width: 100%">
                    <el-table-column
                        type="index"
                        label=""
                        align="center"
                        width="65">
                    </el-table-column>
                    <el-table-column
                        prop="oname"
                        label=""
                        align="center"
                        width="230">
                    </el-table-column>
                    <el-table-column
                        prop="uname"
                        label=""
                        align="center"
                        width="130">
                    </el-table-column>
                    <el-table-column
                        prop="uphone"
                        label=""
                        align="center"
                        width="140">
                    </el-table-column>
                    <el-table-column
                        label=""
                        align="center"
                        width="150">
                        <template scope="scope">
                            {{scope.row.oprovince}}-{{scope.row.ocity}}
                        </template>
                    </el-table-column>
                    <el-table-column
                        label=""
                        align="center"
                        width="100">
                        <template scope="scope">
                            {{scope.row.effect == 1 ? '' : ''}}
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="updatetime"
                        label=""
                        align="center"
                        width="180">
                        <template scope="scope">
                            {{scope.row.updatetime | time}}
                        </template>
                    </el-table-column>
                    <el-table-column
                        label=""
                        align="center"
                        width="220">
                        <template scope="scope">
                            <el-button type="text" @click="see(scope.row)"></el-button><span class="sss">|</span>
                            <el-button type="text" @click="editPartner(scope.row)"></el-button><span class="sss">|</span>
                            <el-button type="text" @click="state(scope.row)" :style="{color: scope.row.effect == 1 ? '#f75555':'#4fbd61'}">{{scope.row.effect == 1 ? '' : ''}}</el-button><span class="sss">|</span>
                            <el-button type="text" @click="areaConfig(scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div class='prchase-list'>
            <Paging :showPageArry="showPageArry" :showPagelist="showPagelist" :listLength="listLength" :currentPage="currentPage" @changePage="changePage" @changSize="changSize"></Paging>
        </div>
    </div>
</template>

<script>
    import tHeader from '@/components/public/tHeader'
    import Paging from '@/components/public/paging'
    export default {
        name: 'partner',
        components: {
            tHeader,
            Paging
        },
        data() {
            return {
                province: '',               //code
                city: '',                   //code
                provinceName: '',           //name
                cityName: '',               //name
                company: '',                //name
                /**
                 * loading
                 */
                pLoading: true,
                cLoading: false,
                tableLoading: true,

                accountState: '',

                provinceTable: [],
                cityTable: [],
                tableData: [],

                showPageArry: [15, 30, 45, 60],     //
                showPagelist: 15,                   //15
                listLength: 0,                      //
                currentPage: 1                      //
            }
        },
        created() {
            this.$http.post('/partners/findPartnersProvince.do', { code: "0", status: 1 }).then( res => {
                if(res.data.success) {
                    this.provinceTable = res.data.data
                }
            }).catch( error => {
                this.$message({
                    message: '',
                    duration: 1000,
                    type: 'error'
                });
                console.log(':' + error)
            })
            // 
            this.$http.post('partners/findPartnersPage.do', { page: this.currentPage, pageSize: this.showPagelist }).then( res => {
                let info = res.data.data
                if(res.data.success) {
                    this.tableData = info.partnersList.content
                    this.listLength = info.partnersList.totalSize
                }
                else {
                    this.tableData = []
                    this.tableLoading = false
                }
            }).catch( error => {
                this.tableLoading = false
                this.$message({
                    message: '',
                    duration: 1000,
                    type: 'error'
                });
                console.log(':' + error)
            })
        },
        updated() {
            let table1 = document.querySelector('.el-table__header');
            let table2 = document.querySelector('.el-table__body');
            table1.style.width = '100%'
            table2.style.width = '100%'
        },
        filters: {
            time(value) {
                let t = new Date(value)
                let year = t.getFullYear();
                let month = t.getMonth()+1;
                let date = t.getDate();
                let hour = t.getHours();
                let minute = t.getMinutes();
                let second = t.getSeconds();
                if(hour < 10) {
                    hour  = '0' + hour
                }
                if(minute < 10) {
                    minute  = '0' + minute
                }
                if(second < 10) {
                    second  = '0' + second
                }
                return year + "-" + month + "-" + date + ' ' + hour + ':' + minute + ':' + second;
            }
        },
        methods: {
            //router
            addPartner() {
                this.$router.push({name: 'addPartner'})
            },
            editPartner(row) {
                let query = {
                    pid: row.pid,
                    fkeyarea: row.ocode
                }
                this.$router.push({ name: 'editPartner', query })
            },
            //router
            see(row) {
                let query = {
                    pid: row.pid,
                    fkeyarea: row.ocode
                }
                this.$router.push({name: 'seePartner', query })
            },
            //router
            areaConfig(row) {
                let query = {
                    pid: row.pid,
                    oname: row.oname,
                    keyArea: row.ocode,
                    oaid: row.id
                }
                this.$router.push({name: 'areaConfig', query })
            },
            //
            state(scope) {   //1   0
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    if(scope.effect == 0) {
                        this.accountState = 1
                    }
                    else {
                        this.accountState = 0
                    }
                    this.$http.post('partners/updateStatus.do', {companyID: scope.id, crm: this.accountState }).then( res => {
                        if(res.data.success) {
                            if(scope.effect == 0) {
                                scope.effect = 1
                            }
                            else {
                                scope.effect = 0
                            }
                            this.$message({
                                type: 'success',
                                message: '!'
                            });
                        }
                        else {
                            this.$message.error('');
                            this.$message({
                                message: '',
                                duration: 1000,
                                type: 'warning'
                            });
                        }
                    }).catch( error => {
                        this.$message({
                            message: '',
                            duration: 1000,
                            type: 'error'
                        });
                        console.log(error)
                    })
                }).catch(() => {});
            },
            //select
            getArea(value) {
                if(value != '') {
                    this.$http.post('/partners/findPartnersCity.do ', { province: this.province }).then(res => {
                        if(res.data.success) {
                            this.cityTable = res.data.data
                        }
                    }).catch(function (error) {
                        this.$message({
                            message: '',
                            duration: 1000,
                            type: 'error'
                        });
                        console.log(':' + error);
                    });
                }
            },

            //, 
            getTableData() {
                this.$http.post('partners/findPartnersPage.do', {oprovince: this.province, ocity: this.city, oname: this.company, page: this.currentPage, pageSize: this.showPagelist}).then( res => {
                    let info = res.data.data
                    if(res.data.success) {
                        if(info.partnersList) {
                            this.tableData = info.partnersList.content
                            this.listLength = info.partnersList.totalSize
                        }
                        else {
                            this.tableData = []
                            this.listLength = 0
                            this.tableLoading = false
                        }
                    }
                }).catch( error => {
                    this.tableLoading = false
                    this.$message({
                        message: '',
                        duration: 1000,
                        type: 'error'
                    });
                    console.log(':' + error)
                })
            },
            search() {
                this.getTableData()
            },
            changePage(page) {
                this.currentPage = page
                this.getTableData()
            },
            //
            changSize(page) {
                this.showPagelist = page
                this.getTableData()
            }
        },

        watch: {
            provinceTable(val) {
                this.pLoading = false
            },
            province(val) {
                this.city = ''
                if(val === '') {
                    this.cityTable = []
                }
                else {
                    this.cLoading = true
                }
            },
            cityTable(val) {
                this.cLoading = false
            },
            tableData(val) {
                this.tableLoading = false
            }
        }
    }
</script>

<style scoped lang="scss">
    #partner {
        padding: 30px 19px 0 40px;
        box-sizing: border-box;
        .main {
            background-color: #fff;
            padding: 0 10px 10px;
            box-sizing: border-box;
            ul.search {
                line-height: 32px;
                padding-top: 21px;
                padding-bottom: 19px;
                li {
                    float: left;
                }
                li:nth-child(2) {
                    margin-left: 39px;
                }
                li:last-child {
                    margin-left: 30px;
                }
                .line {
                    display: inline-block;
                    width: 13px;
                    background: #8c8c8c;
                    height: 1px;
                    margin-bottom: 5px;
                }
                /*element search change*/
                .el-select {
                    width: 150px;
                    height: 32px;
                }
                .el-input {
                    width: 200px;
                    height: 32px;
                }
                .el-button {
                    width: 110px;
                    height: 36px;
                }
            }
            .btns {
                margin-bottom: 19px;
                .el-button {
                    width: 110px;
                    height: 36px;
                    background: url("../../../../../static/images/add.png") #20a0ff no-repeat 20px 10px;
                }
            }
            .table {
                .el-button {
                    font-size: 12px;
                }
                .el-button+.el-button {
                    margin-left: 0;
                }
                .sss {
                    padding: 0 2px 0 4px;
                    color: #cccccc;
                }
            }
        }
        .prchase-list .el-pagination {
            top: 0;
            margin-top: 38px;
            margin-bottom: 40px;
        }
    }
</style>



