<template>
    <div class="partnerManage partner-propt">
        <tHeader></tHeader>
        <div class='commodity-bg'>
            <div class='commodity-select-top'>
                <div class='commodity-select-three'>
                    <regional-choice
                        @getPleaseProvice='getPlease'
                        @getRegionProvice='getRegion'
                        :parterCode='-1'
                        :defaultPlease='defaultPlease'
                        :defaultRegion='defaultRegion'
                        :isDisableds='admin.ucompany == 100 ? false : true'
                        :isProduce='true'
                    >
                    </regional-choice>
                    <div class='parter-select-top'>
                        <partner
                            @getSelect='getPatnerId'
                            :codeName='codeName'
                            :partName='partnerName'
                            :isDisableds='admin.ucompany == 100 ? false : true'
                        ></partner>
                    </div>
                    <!-- <div class='partner-select'>
                        <carouse-select
                            textName=':'
                            :region='produceType'
                            @getSelect='getSelect'
                        ></carouse-select>
                    </div> -->
                </div>
                <div class='partner-input'>
                    <span>:</span>
                    <el-input v-model="fproductname" placeholder=""></el-input>
                </div>
                <el-button type="primary" icon="search" @click='search'></el-button>
            </div>
            <div class='partner-table  prchase-list'>
                <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" :row-class-name="tableRowClassName" style="width: 100%">
                    <el-table-column prop="number" label="" width="60">
                    </el-table-column>
                    <el-table-column prop="type" label="" width='155'>
                    </el-table-column>
                    <el-table-column prop="fgrouparea" label="" width='170'>
                    </el-table-column>
                    <el-table-column prop="fsuppliername" label="" width='210'>
                    </el-table-column>
                    <el-table-column prop="fproductname" label="" width='260'>
                    </el-table-column>
                    <el-table-column prop="fcreatetime" label="" width='210'>
                    </el-table-column>
                    <el-table-column prop="fauditor" label="" width='130'>
                    </el-table-column>
                    <el-table-column prop="fupdatetime" label="" width='210'>
                    </el-table-column>
                    <el-table-column label="" fixed="right">
                        <template scope="scope">
                            <el-button  type="text" size="small" @click='viewDetails(scope.row)' v-if="scope.row.fauditstatus == '1' && admin.ucompany == 100 ?  true : false">
                                
                            </el-button>
                            <el-button type="text" size="small"  @click='viewDetails(scope.row)' v-if="scope.row.fauditstatus !='1' || admin.ucompany != 100 ?  true : false">
                                
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class='partner-page'>
                    <pageing v-if='listLength > 10'
                            @changePage="changePage"
                            @changSize='changSize'
                            :showPageArry="listLength > 15 ? [10,15,20] : [10,15]"
                            :showPagelist="showPagelist"
                            :listLength="listLength"
                            :currentPage='currentPage'
                        >
                    </pageing>
                </div>
            </div>
        </div>
        <div class='commodity-propt partner-product-propt'>
            <el-dialog title="" :visible.sync="addNewProduce" :show-close='false' 
             @open='openDialog' @close='closeDialog' :modal-append-to-body='false' :close-on-click-modal='false'>
                <img src='../../../../../static/images/publicImages/close.png' class='partaner-product-close' @click='addNewProduce = false'>
                <div class='partner-product-bg'>
                    <div class='partner-product-text'>
                        <div class='partner-product-title'>
                            <span>:</span><span>{{parterDetal.fsuppliername}}</span><span>|</span><span>{{parterDetal.fgrouparea}}</span>
                        </div>
                        <div>
                            <p class='parter-maxLngth-css'><span>:</span><span>{{parterDetal.fmarketproductname}}</span></p><p class='parter-maxLngth-css'><span class='partner-product-span'>:</span><span>{{parterDetal.fproductname}}</span></p>
                            <p class='parter-maxLngth-css'><span class='partner-product-span'>:</span><span>{{parterDetal.fmaterialid}}</span></p>
                        </div>
                        <div>
                            <p class='parter-maxLngth-css'><span>:</span><span>{{parterDetal.frecommendindustry}}</span></p><p class='parter-maxLngth-css'><span class='partner-product-span'>:</span><span>{{parterDetal.fsuitindustry}}</span></p>
                            <p class='parter-maxLngth-css'><span class='partner-product-span'>:</span><span>{{parterDetal.flogistics}}</span></p>
                        </div> 
                        <div>
                            <p class='parter-maxLngth'><span>:</span><span>{{parterDetal.fdeliveryday + ''}}</span></p><p class='parter-maxLngth'><span class='partner-product-span'>:</span><span>{{parterDetal.fdeliverytime}}</span></p>
                        </div> 
                        <div>
                            <p class='parter-maxLngth'><span>:</span><span>{{fluteType[parterDetal.fflutetype]}}</span></p>
                        </div>
                        <div>
                            <p class='parter-maxLngth'><span >:</span><span>{{parterDetal.flayer}}</span></p>
                        </div>
                        <div>
                             <p class='parter-maxLngth'><span>:</span><span>{{parterDetal.fmaxorderlength}}</span><span>CM</span></p><span >:</span><span>{{parterDetal.fminorderlength}}</span><span>CM</span>
                        </div> 
                        <div>
                            <p class='parter-maxLngth'><span>:</span><span>{{parterDetal.fmaxorderwidth}}</span><span>CM</span></p><span >:</span><span>{{parterDetal.fminorderwidth}}</span><span>CM</span>
                        </div> 
                        <div>
                            <p class='parter-maxLngth'><span>:</span><span>{{parterDetal.fmateriallengthplus}}</span><span>CM</span></p><span >:</span><span>{{parterDetal.fmaterialwidthplus}}</span><span>CM</span>
                        </div>   
                    </div>
                    <p>
                        :
                    </p>
                    <img :src='parterDetal.url' class='partner-product-shelf'>
                    <p class='partner-add-img'>
                        :
                    </p>
                    <div v-for='imgList in parterDetal.detailsfigure' :key='imgList.url' style='display:inline-block'>
                        <img :src='imgList.url' class='partner-product-list'>
                    </div>
                </div>
                <div class='partner-product-isPass'>
                       <p></p>
                       <div class='partner-product-radio' v-if="parterDetal.fauditstatus == '1' && admin.ucompany == 100 ? true: false">
                            <el-radio class="radio" v-model="radio" label="1"></el-radio>
                            <el-radio class="radio" v-model="radio" label="2"></el-radio>
                            <div class='partner-product-input'>
                                <el-input v-model="reject" placeholder="20" :maxlength='20' v-if="radio == '2'"></el-input>
                            </div>
                       </div>
                       <div class='partner-product-btn' v-if="parterDetal.fauditstatus == '1' && admin.ucompany == 100 ? true: false">
                            <el-button @click='addNewProduce = false'></el-button>
                            <el-button type="primary" @click='saveView'></el-button>
                       </div>
                        <div class='partner-product-radio' v-if="parterDetal.fauditstatus == '2' ? true: false">
                           <div class='partner-check-pass'></div>
                           <el-button @click='addNewProduce = false'></el-button>
                       </div>
                       <div class='partner-product-radio' v-if="admin.ucompany != 100 &&parterDetal.fauditstatus != '3' &&parterDetal.fauditstatus != '2' ? true: false">
                           <div class='partner-check-pass'></div>
                           <el-button @click='addNewProduce = false'></el-button>
                       </div>  
                       <div class='partner-product-radio' v-if="parterDetal.fauditstatus == '3' ? true: false">
                           <div class='partner-check-pass'>:
                               <span>{{parterDetal.fcomment}}</span>
                           </div>
                           <el-button @click='addNewProduce = false'></el-button>
                       </div>  
                </div>
            </el-dialog>
        </div>
    </div>
</template>

<script>
import carouseSelect from '@/components/public/select'
import tHeader from '@/components/public/tHeader'
import regionalChoice from '@/components/public/regionalChoice'
import partner from '@/components/public/Partner'
import pageing from '@/components/public/paging'
import {VERSION, IMGPATH} from '@/util/constant.js'
import {clippingProvice, clippingCity} from '@/util/common.js'
export default {
    created(){
        this.admin = this.$store.getters.doneAdmin;
        this.fauditorid = this.admin.ucompany
        this.fauditor = this.admin.uname;
        this.parterId = this.admin.ucompany;      //id
         //
        this.fkeyarea = this.admin.ocode;
        //
        if(this.admin.ucompany != 100){
           this.defaultRegion = clippingProvice(this.admin.oprovince);
           this.defaultPlease = clippingCity(this.admin.ocity);
           setTimeout(()=> {
               this.partnerName = this.admin.oname;    //
           }, 1);
           this.searchFunc();
        }
    },
	data () {
		return {
            partnerName:'',
            defaultPlease:'',
            defaultRegion:'',
            admin:{},
            produceType:[
                {regionName:'', value:'-1'},
                {regionName:'', value:'1'},
                {regionName:'', value:'2'},
                {regionName:'', value:'3'}
            ],
            listLength:0,
            showPagelist:10,
            currentPage:1,
            radio:'1',
            fkeyarea:'',           //
            fgroupareaid:'',       // 
            fauditstatus:'-1',     //
            reject:'',             //
            codeName:'',           //
            parterId:'',           //ID
            fproductname:'',       //
            fauditorid:'',         //Id
            fauditor:'',           //
            addNewProduce:false,
            typeArry:{
                '0':'',
                '1':'',
                '2':'',
                '3':''
            },
            tableData:[],
            parterDetal:{},       //
            fluteType:{
                '1': 'BC',
                '2': 'BE',
                '3': 'C',
                '4': 'B',
                '5': 'E',
                '6': 'EBC'
            }
		}
	},
	methods:{
        //
        tableRowClassName(row, index) {
            if (((index + 1) % 2) === 0) {
                return 'info-row';
            }
            return '';
        },
        //code
        getPlease(please){
            //  this.fgroupareaid = please.code;
             this.codeName = please;
        },
        //
        getRegion(){
            this.codeName = '';
            this.tableData = [];
        },
        //
        getPatnerId(data){
            if(data.id == undefined && data.code == undefined){
                return
            }
            this.parterId = data.id;
            this.fkeyarea = data.code;
            this.partnerName = data.name;
        },
        //
        changePage(page){
            this.currentPage = page;
            this.searchFunc();
        },
        //
        changSize(pageLength){
            this.showPagelist = pageLength;
            this.searchFunc();
        },
        //
        searchFunc(){
            let data = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.parterId,
                fauditstatus:this.fauditstatus,
                fonoffstatus:'-3',
                fproductname:this.fproductname,
                pagenum:this.currentPage - 1,
                pagesize:this.showPagelist,
                sort:'2'
            }
            this.searchList(data);
        },
        //
        searchList(data){
            this.$http.post('/product/loadProducts.do', data).then(res => {
                if(res.data.success) {
                    this.tableData=[];
                    this.tableData = res.data.data;
                    this.listLength = res.data.total;
                    for(let i=0; i<this.tableData.length; i++){
                        this.tableData[i].number = this.currentPage * this.showPagelist - this.showPagelist + i + 1;
                        this.tableData[i].type = this.typeArry[this.tableData[i].fauditstatus];
                        if(this.tableData[i].fauditstatus == 1){
                            this.tableData[i].fupdatetime = '/'
                            this.tableData[i].fauditor = '/';
                        }
                    }
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //
        getSelect(name){
            for(let i=0; i<this.produceType.length; i++){
               if(name == this.produceType[i].regionName){
                    this.fauditstatus = this.produceType[i].value;
               }
            }
        },
        //
        search(){
            let data = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.parterId,
                fauditstatus:this.fauditstatus,
                fonoffstatus:'-3',
                fproductname:this.fproductname,
                pagenum:0,
                pagesize:this.showPagelist
            }
            if(this.partnerName == ''){
                this.$message({
                    message: '',
                    type: 'warning',
                    duration: 2000
                });
                return
            }
            this.searchList(data);
        },
        //
        viewDetails(data){
            this.addNewProduce = true; 
            this.reject = '';
            this.radio = '1'
            let datas = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.parterId,
                fid:data.fid
            }
            this.$http.post('/product/loadProducts.do', datas).then(res => {
                if(res.data.success){
                   this.parterDetal = res.data.data[0];
                   this.parterDetal.url = IMGPATH +  res.data.data[0].figure.furl;
                   var data = res.data.data[0].detailsfigure;
                   for(let i=0; i<data.length; i++){
                        data[i].url = IMGPATH + data[i].furl;
                   }
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //
        saveView(){
            let data ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fid:this.parterDetal.fid,
                fcomment:this.reject,
                operate:this.radio == '1' ? true : false,
                fauditorid:this.fauditorid,
                fauditor:this.fauditor
            }
            if(this.radio == '2' && this.reject ==''){
                this.$message({
                        message: '',
                        duration: 2000
                     });
                return
            }
            this.$http.post('/product/examineProduct.do', data).then(res => {
                if(res.data.success) {
                   this.$message({
                        message: '',
                        type: 'success',
                        duration: 2000
                     });
                     this.addNewProduce = false; 
                     this.searchFunc();
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        openDialog(){
            this.$store.commit('openZindexModel');
        },
        closeDialog(){
            this.$store.commit('closeZindexModel');
        }
    },
    components:{
        carouseSelect,
        tHeader,
        regionalChoice,
        partner,
        pageing
    }  
}
</script>


