<template>
    <div class="ab-query-warp">
        <ul class="clearfix">
            <li>
                 :
                <el-date-picker v-model="startTime" type="datetime" placeholder=""  :picker-options="pickerStart">
                </el-date-picker>
                
                <el-date-picker v-model="endTime" type="datetime" placeholder=""   :picker-options="pickerEnd">
                </el-date-picker>
            </li>
            <li> : <input type="text" v-model="phone" @input="phoneCheck" placeholder=""></li>
            <li>
                 : <input type="text" v-model="name" placeholder="">
            </li>
            <li>
                <el-button type="primary" icon="search" @click="query"></el-button>
            </li>
        </ul>
    </div>
</template>
<script>
    /**
     * Created by qxl on 2017/8/28.
     */
    import {VERSION} from '@/util/constant.js'
    import { get3MonthBefor } from '@/util/common.js'
    export default{
        data(){
            return {
                pickerStart:{
                    disabledDate(time) {
                        return time.getTime() < get3MonthBefor() || time.getTime() > Date.now() - 8.64e7 + 86400000;
                    }
                },
                pickerEnd:{
                    disabledDate(time) {
                        return time.getTime() < get3MonthBefor() || time.getTime() > Date.now() - 8.64e7 + 86400000;
                    }
                },
                endTime:'', // 
                startTime:'', //
                startGetTime:-999, //
                endGetTime:-999, //
                phone:'',  //
                name:''
            }
        },
        methods:{
            phoneCheck(){
                this.phone = this.phone.replace(/\D/g,'');
            },
              //
            getTime(val){
                    return new Date(val).getTime();
            },
            //
            msg(msg,type){
                this.$message({
                    message: msg,
                    type: type
                });
            },
            useTime(value){
                if(value == '' || value == undefined){
                    return '';
                }
                var time = new Date(value);
                var year = time.getFullYear();
                var month = time.getMonth() + 1<10? '0'+ (time.getMonth() + 1):time.getMonth() + 1;
                var date = time.getDate() <10 ? '0'+ time.getDate():time.getDate();
                var hour = time.getHours() < 10 ? '0' + time.getHours() : time.getHours();
                var min = time.getMinutes() < 10 ? '0' + time.getMinutes() : time.getMinutes();
                var sec = time.getSeconds() < 10 ? '0' + time.getSeconds() : time.getSeconds();
                return `${year}-${month}-${date} ${hour}:${min}:${sec}`
            },
            // 
            query(){
                if(this.getTime(this.endTime)<this.getTime(this.startTime)){
                    this.msg('','error');
                    return;
                }
                if(this.phone.length > 11){
                    this.msg('11!','error');
                    return;
                }
                if(this.startTime=='' && this.endTime !=''){
                    this.msg('','error');
                     return;
                }
                if(this.startTime !='' && this.endTime ==''){
                    this.msg('','error');
                     return;
                }
                var queryJson ={
                    startTime:this.useTime(this.startTime),
                    endTime:this.useTime(this.endTime),
                    phone:this.phone,
                    name:this.name
                }
                this.$emit('query', queryJson)
            }
        }
    }
</script>
<style>
    .ab-query-warp ul li{
        float:left;
        margin-right:15px;
    }
    .ab-query-warp ul li:nth-child(1){
        margin-bottom: 5px;
    }
    .ab-query-warp ul{
        padding:24px 0;
    }
    .ab-query-warp ul li:nth-child(2) input,
    .ab-query-warp ul li:nth-child(3) input{
        width:175px;
        height:33px;
        border:1px solid #c6c6c6;
        padding-left: 5px;
        border-radius:4px;
    }

</style>



