<template>
    <div>
        <div class="partnerManage partner-propt" v-show="currentPages === 'productManger'">
            <tHeader></tHeader>
            <div class='commodity-bg'>
                <div class='commodity-select-top'>
                    <regional-choice
                        @getPleaseProvice='getPlease'
                        @getRegionProvice='getRegion'
