                            <img :src='scope.row.fimg' class='partner-table-img'>
                            </template>
                        </el-table-column>
                        <el-table-column prop="fproductname" label="" >
                        </el-table-column>
                        <el-table-column prop="type" label="" width="100">
                        </el-table-column>
                        <el-table-column prop="moneyType" label="" width="150">
                        </el-table-column>
                        <el-table-column prop="fgroupprice" label="" width="150">
                        </el-table-column>
                        <el-table-column prop="fmaxarea" label="" width="110">
                        </el-table-column>
                        <el-table-column prop="flimitedtime" label="" width="110">
                        </el-table-column>
                        <el-table-column label="" width="230" fixed="right">
