                                <div class='partner-btn' @click='applayShelves(scope.row, 0)' v-show='scope.row.fonoffstatus == 0 || scope.row.fonoffstatus == 3 && scope.row.fauditstatus != 1 ? true : false'></div><div class='partner-btn' @click="changePage(scope.row)" v-show='scope.row.fonoffstatus == 0 || scope.row.fonoffstatus == 2 || scope.row.fonoffstatus == 3 && scope.row.fauditstatus != 1 ? true : false'></div>
                                <div class='partner-btn' @click='editProduct(scope.row)' v-show='scope.row.fonoffstatus == 0 || scope.row.fonoffstatus == 3 && scope.row.fauditstatus != 1 ? true : false'></div><div class='partner-btn' @click="delPropt(scope.row)" v-show='scope.row.fonoffstatus == 0 || scope.row.fonoffstatus == 3 && scope.row.fauditstatus != 1 ? true : false'></div>
                                <div class='partner-btn' @click='applayShelves(scope.row, 1)' v-show='scope.row.fonoffstatus == 2 ? true : false'></div><div class='partner-btn ' @click='revoke(scope.row)' v-show='scope.row.fonoffstatus == 1 || scope.row.fonoffstatus == 3 && scope.row.fauditstatus == 1 ? true : false'></div>
                            </template>
                        </el-table-column>
                        <el-table-column label="" width="150" fixed="right">
                            <template scope="scope">
                                <div class='partner-btn' @click='seeDetal(scope.row)'></div>
