                            ></common-input>
                            <common-input textName=':' @setValue='getValue' ref='fmaterialid' :defaultValue='fmaterialid' :maxLength='40'></common-input>
                            <div class='partner-input-small'>
                            <i>*</i>&nbsp;<carouse-select textName=':' :region='fluteType' @getSelect='getFlute' :regionName='fflutetypeDf'></carouse-select>
                            </div>
                            <div class='partner-input-bottom'>
                                <common-input textName=':' 
