                                defaultName='0-500'
                                :error='errorAry[3].error' 
                                :errorPropt='errorAry[3].errorPropt'
                                ></common-input><span class='partner-input-bottom-span2'>CM</span>
                            </div>
                            <common-input textName=':'
                             @setValue='getValue' 
