                             :defaultValue='fmateriallengthplus' 
                             :maxlength='6' defaultName='0-100'
                             :error='errorAry[5].error' 
                             :errorPropt='errorAry[5].errorPropt'
                             ></common-input><span class='partner-input-bottom-span3'>CM</span>
                        </div>
                        <div class='partner-input-right'>
                            <common-input textName=':' @setValue='getValue' :maxlength='50' ref='frecommendindustry' :defaultValue='frecommendindustry'></common-input>
                            <common-input textName=':' @setValue='getValue' :maxlength='50' ref='fsuitindustry' :defaultValue='fsuitindustry'></common-input>
                            <div class="block partner-selectTime" >
                                    <i class='partner-icon'>*</i>
                                    <span class="demonstration">:</span>
