                            </div>
                            <div class='partner-input-small'>
                                <common-input textName=':' @setValue='getValue' :maxlength='20' ref='flogistics' :defaultValue='flogistics'></common-input>
                                <i>*</i>&nbsp;<carouse-select textName=':' :region='numberOf' @getSelect='getNumber' :regionName='flayer'></carouse-select>
                            </div>
                            <div class='partner-input-bottom'>
                                <common-input textName=':' 
                                @setValue='getValue' 
                                :maxlength='6'
                                ref='fminorderlength'
                                :defaultValue='fminorderlength' 
                                defaultName='0-500'
                                :error='errorAry[2].error' 
                                :errorPropt='errorAry[2].errorPropt'
                                ></common-input><span class='partner-input-bottom-span'>CM</span>
                                <common-input textName=':'
                                 @setValue='getValue' 
