                            :defaultValue='fmaterialwidthplus' 
                            :maxlength='6' 
                            defaultName='0-100'
                            :error='errorAry[6].error' 
                            :errorPropt='errorAry[6].errorPropt'
                            ></common-input><span class='partner-input-bottom-span3'>CM</span>
                        </div>
