                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                            </div>
                            <div class='IE-upload' v-if='IE'>
                                <upload-ie :checkData='checkIEData' 
                                :uploadDate='uploadDate' 
                                :uploadSrc='uploadSrc' 
                                @addImgSuccess='addSuccess' 
                                action='djcrm/product/uploadFigure.do'
                                ></upload-ie>
                                <div class='partner-upload-font-first'>
                                    <p>jpg/png</p>
                                    <p>RGB < 2M </p>
                                    <p>1:1,360</p>
                                </div>
                                <i class="el-icon-plus avatar-uploader-icon"></i>
                            </div>
                        </div>
                        <p class='partner-add-img'>
