                            </div>
                            <div>
                                <p class='parter-maxLngth-css'><span>:</span><span>{{parterDetal.fmarketproductname}}</span></p><p class='parter-maxLngth-css'><span class='partner-product-span'>:</span><span>{{parterDetal.fproductname}}</span></p>
                                <p class='parter-maxLngth-css'><span class='partner-product-span'>:</span><span>{{parterDetal.fmaterialid}}</span></p>
                            </div>
                            <div>
                                <p class='parter-maxLngth-css'><span>:</span><span>{{parterDetal.frecommendindustry}}</span></p><p class='parter-maxLngth-css'><span class='partner-product-span'>:</span><span>{{parterDetal.fsuitindustry}}</span></p>
                                <p class='parter-maxLngth-css'><span class='partner-product-span'>:</span><span>{{parterDetal.flogistics}}</span></p>
                            </div> 
                            <div>
                                <p class='parter-maxLngth'><span>:</span><span>{{parterDetal.fdeliveryday + ''}}</span></p><p class='parter-maxLngth'><span class='partner-product-span'>:</span><span>{{parterDetal.fdeliverytime}}</span></p>
                            </div> 
                            <div>
                                <p class='parter-maxLngth'><span>:</span><span>{{flutetypeArry[parterDetal.fflutetype]}}</span></p>
                            </div>
                            <div>
                                <p class='parter-maxLngth'><span >:</span><span>{{parterDetal.flayer}}</span></p>
                            </div>
                            <div>
                                <p class='parter-maxLngth'><span>:</span><span>{{parterDetal.fmaxorderlength}}</span><span>CM</span></p><span >:</span><span>{{parterDetal.fminorderlength}}</span><span>CM</span>
                            </div> 
                            <div>
                                <p class='parter-maxLngth'><span>:</span><span>{{parterDetal.fmaxorderwidth}}</span><span>CM</span></p><span >:</span><span>{{parterDetal.fminorderwidth}}</span><span>CM</span>
                            </div> 
                            <div>
                                <p class='parter-maxLngth'><span>:</span><span>{{parterDetal.fmateriallengthplus}}</span><span>CM</span></p><span >:</span><span>{{parterDetal.fmaterialwidthplus}}</span><span>CM</span>
                            </div>   
                        </div>
                        <p>
                            :
                        </p>
                        <img :src='parterDetal.url' class='partner-product-shelf'>
                        <p class='partner-add-img'>
                            :
                        </p>
                        <div v-for='imgList in parterDetal.detailsfigure' :key='imgList.url' style='display:inline-block'>
                            <img :src='imgList.url' class='partner-product-list'>
                        </div>
                        <div class='partnerManger-isPass'>
                            <p></p>
                            <div class='partner-product-radio' v-if="parterDetal.fauditstatus == '1' ? true: false">
                                <div class='partner-check-pass'></div>
                                <el-button @click='produceDetail = false'></el-button>
                            </div>
                            <div class='partner-product-radio' v-if="parterDetal.fauditstatus == '2' ? true: false">
                                <div class='partner-check-pass'></div>
                                <el-button @click='produceDetail = false'></el-button>
                            </div>
                            <div class='partner-product-radio' v-if="parterDetal.fauditstatus == '3' && parterDetal.fonoffstatus != '2' ? true: false">
                                <div class='partner-check-pass'>:
                                    <span>{{parterDetal.fcomment}}</span>
                                </div>
                                <el-button @click='produceDetail = false'></el-button>
                            </div>
                            <div class='partner-product-radio' v-if="parterDetal.fauditstatus == '3' && parterDetal.fonoffstatus == '2' ? true: false">
                                <div class='partner-check-pass'>
                                    <span></span>
                                </div>
                                <el-button @click='produceDetail = false'></el-button>
                            </div>   
                        </div>
                    </div>
                </el-dialog>
