        </div>
        <div v-if="currentPages === 'optionPricesCheme'">
            <optionPrices :getProductData='setOptionData'></optionPrices>
        </div>
        <propt
            :dialogVisible = 'isDel'
            text=''
            :needCancel = 'true'
            @closeVisible='isDel = false'
            @sure='delData'
        ></propt>
        <propt
            :dialogVisible = 'delProptImg'
            :text='textName'
            :needCancel = 'false'
            @closeVisible='delProptImg = false'
            @sure='delProptImg = false'
        ></propt>
    </div>
</template>

<script>
import carouseSelect from '@/components/public/select'
import tHeader from '@/components/public/tHeader'
import regionalChoice from '@/components/public/regionalChoice'
import partner from '@/components/public/Partner'
import pageing from '@/components/public/paging'
import commonInput from './input'
import {VERSION, IMGPATH} from '@/util/constant.js' 
import {simpleClone} from '@/util/common.js'
import optionPrices from './optionPricesCheme'
import propt from '@/components/public/propt'
import uploadIe from '@/components/public/uploadIE'
import {clippingProvice, clippingCity, checkNumber, checkDecimal, getBranch, parserDate} from '@/util/common.js'
export default {
    created(){
        this.admin = this.$store.getters.doneAdmin;
        this.fcreatorid = this.admin.ucompany; 
        this.fsupplierid = this.admin.ucompany;       //id
        //
        if(this.admin.ucompany != 100){
           this.defaultRegion = clippingProvice(this.admin.oprovince);
           this.defaultPlease = clippingCity(this.admin.ocity);
           setTimeout(()=> {
               this.partnerName = this.admin.oname;    //
           }, 1);
           this.fparterId = this.admin.ucompany;    //ID
           this.searchFunc();
        }
        //
        this.pleaseCode = this.admin.ocode;
        if(navigator.appName == "Microsoft Internet Explorer" && 
            navigator.appVersion .split(";")[1].replace(/[ ]/g,"") == "MSIE9.0"){
            this.IE = true;  
            return
        } 
    },
	data () {
		return {
            textName:'',
            delProptImg:false,
            IE:false,
            uploadSrc:'',
            checkIEData:{
                size:2,
                Proportion:1,
                minWidth:360,
                minHeight:360,
                needPropt:false
            },
            checkIEDataList:{
                size:2,
                width:1100,
                needPropt:false
            },
            uploadDate:[],
            imgListUpload:[              //IE9
                {number:0, src:'', isShow:false, mask:false},
            ],
            produceTypeDefault:'',
            partnerName:'',
            defaultPlease:'',
            defaultRegion:'',
            addDefaultRegion:'',
            addDefaultPlease:'',
            setOptionData:{},
            admin:{},
            produceType:[
                {regionName:'',  number:-1},
                {regionName:'', number:0},
                {regionName:'', number:1},
                {regionName:'', number:2},
                {regionName:'', number:3},
                {regionName:'', number:4}
            ],
            fluteType:[
                {regionName: 'BC', value: 1},
                {regionName: 'BE', value: 2},
                {regionName: 'C', value: 3},
                {regionName: 'B', value: 4},
                {regionName: 'E', value: 5},
                {regionName: 'EBC', value: 6}
            ],
            numberOf:[
                {regionName: '7', value: 7},
                {regionName: '6', value: 6},
                {regionName: '5', value: 5},
                {regionName: '4', value: 4},
                {regionName: '3', value: 3},
                {regionName: '2', value: 2}
            ],
            isDel:false,
            listLength:0,
            showPagelist:5,
            currentPage:1,
            codeName:'',
            pleaseCode:3303,                //
            produceDetail:false,
            addNewProduce:false,
            currentPages:'productManger',
            imageUrl:'',
            fmarketproductname:'',          //
            fproductname:'',                //
            fmaterialid:'',                 //
            fmaxorderlength:'',             //
            fmaxorderwidth:'',              //
            fmateriallengthplus:'0.0',        //
            frecommendindustry:'',          //
            fsuitindustry:'',               //
            flogistics:'',                  //
            fminorderlength:'',             //
            fminorderwidth:'',              //
            fmaterialwidthplus:'2.0',         //
            flayer:'',                      //
            fflutetype:'',                  //
            fflutetypeDf:'',                //
            fsupplierid:'',                 //ID
            fparterId:'',                   //-ID
            fid:'',                         //ID
            editPartName:'',                //
            delFid:'',                      //Fid
            fgroupareaProvice:'',           //
            fgroupareaCity:'',              //
            fgroupareaid:'',                //ID
            imgFid:'',                      //fid
            fcreatorid:'',                  //ID
            endTime:'',                     //
            fdeliveryday:'',                //
            uuId:'',
            key:'',
            delImgAry:[],
            figures:{
                ftype: 1
            },
            detailsfigure:[],
            fileList:[],
            tableData:[],
            flutetypeArry:{
                '1':'BC',
                '2':'BE',
                '3':'C',
                '4':'B',
                '5':'E',
                '6':'EBC'
            },
            typeArry:{
                '0':'',
                '1':'',
                '2':'',
                '3':''
            },
            checkAry:[
                'fsupplierid',                   //-ID
                'fsuppliername',                 //-
                'fmarketproductname',            //
                'fproductname',                  //
                'fmaterialid',                   //
                'fmaxorderlength' ,              //
                'fmaxorderwidth',                //
                'fmateriallengthplus',           //
                'frecommendindustry',            //
                'fsuitindustry'  ,               //
                'flogistics'  ,                   //
                'fminorderlength',               //
                'fminorderwidth'  ,              //
                'fmaterialwidthplus' ,           //
                'flayer'       ,                     //
                'fflutetype'  ,                   //
                'fgrouparea'   ,                 //
                'fgroupareaid'  ,                //ID
                'fdeliverytime'   ,                //
