                    errorPropt:'0-500',
                },
                {
                    error:false,
                    errorPropt:'0-500',
                },
                {
                    error:false,
                    errorPropt:'0-500',
                },
                {
                    error:false,
                    errorPropt:'0-100',
                },
                {
                    error:false,
                    errorPropt:'0-100',
                }
            ],
            parterDetal:{}               //
		}
	},
	methods:{
        //
        tableRowClassName(row, index) {
            if (((index + 1) % 2) === 0) {
                return 'info-row';
            }
            return '';
        },
        //name
        getPlease(please){
            this.codeName = please;
        },
        //
        getRegion(){
            this.codeName = ''
            this.tableData = [];
        },
        //
        selectParter(datas){
            if(datas.id == undefined && datas.code == undefined){
                return
            }
            this.fsupplierid = datas.id;
            this.pleaseCode = datas.code;
            this.partnerName = datas.name;
            this.currentPage = 1;
            this.searchFunc();
            this.tableData = [];
            this.produceTypeDefault = '';
        },
        //
        selectAddParter(data){
           if(data.id == undefined || data.code == undefined){
               return
           }
           this.fparterId = data.id;
           this.editPartName = data.name;
           this.uuId = data.id;
           this.key = '';
           this.addDefaultRegion = '';
           this.addDefaultPlease = '';
           setTimeout(()=>{
                this.key = data.code;
           },10)
        },
        //
        getType(name){
           this.produceTypeDefault = name;
           if(name == ''){
               return
           }
           for(let i=0; i<this.produceType.length; i++){
               if(name === this.produceType[i].regionName){
                   var type = this.produceType[i].number;
               }
           }
           let data = {
                version:VERSION,
                fkeyarea:this.pleaseCode + '',
                fsupplierid:this.fsupplierid + '',
                pagenum:this.currentPage - 1 + '',
                pagesize:this.showPagelist + ''
            }
            if(this.partnerName == ''){
                this.$message({
                    message: '',
                    type: 'warning',
                    duration: 2000
                });
                return
            }
            //
            if(type == 4){
               data.fauditstatus = '3'
               data.fonoffstatus = '-1'
               this.searchList(data);
               return
            }
            if(type !== -1){
               data.fonoffstatus = type + ''
               this.searchList(data);
               return
            }
            this.searchList(data);  
        },
        //
        changePages(page){
            this.currentPage = page;
            this.searchFunc()
        },
        //
        changSize(pageLength){
            this.showPagelist = pageLength;
            this.searchFunc()
        },
        //
        getValue(){
            let ref = this.$refs;
            this.fmarketproductname = ref.fmarketproductname.input; //
            this.fproductname = ref.fproductname.input;//
            this.fmaterialid = ref.fmaterialid.input; //
            this.frecommendindustry = ref.frecommendindustry.input; //
            this.fsuitindustry = ref.fsuitindustry.input; //
            this.flogistics = ref.flogistics.input; //

            this.errorAry[0].error = checkNumber(ref.fdeliveryday.input);
            if(!this.errorAry[0].error){
                if(Number(ref.fdeliveryday.input) > 999){
                    this.errorAry[0].error = true;
                }else{
                    this.errorAry[0].error = false;
                }
            }
            this.fdeliveryday = ref.fdeliveryday.input//

            this.errorAry[1].error = checkDecimal(ref.fmaxorderlength.input, 500, false);
            this.fmaxorderlength = ref.fmaxorderlength.input; //

            this.errorAry[2].error = checkDecimal(ref.fminorderlength.input, 500, false);
            this.fminorderlength = ref.fminorderlength.input; //

            this.errorAry[3].error = checkDecimal(ref.fmaxorderwidth.input, 500, false);
            this.fmaxorderwidth = ref.fmaxorderwidth.input; //

            this.errorAry[4].error = checkDecimal(ref.fminorderwidth.input, 500, false);
            this.fminorderwidth =  ref.fminorderwidth.input; //

            this.errorAry[5].error = checkDecimal(ref.fmateriallengthplus.input, 100, false);
            this.fmateriallengthplus = ref.fmateriallengthplus.input; //

            this.errorAry[6].error = checkDecimal(ref.fmaterialwidthplus.input, 100, false);
            this.fmaterialwidthplus = ref.fmaterialwidthplus.input; //
            
            
        },
        //
        beforeAvatarUpload(files) {
            var file = files.raw;
            var img = new Image();
            img.src = files.url;
            const isJPG = file.type === 'image/jpeg';
            const isPNG = file.type === 'image/png';
            const isLt2M = file.size / 1024 / 1024 < 2;
            if (!isJPG && !isPNG) {
                this.$message.error(' JPG  PNG !');
                return 
            }
            if (!isLt2M) {
                this.$message.error(' 2MB!');
                return
            }
            var q = this;
            img.onload = function(){
                if(img.width < 360 || img.height < 360 || img.width !== img.height){
                    q.$message.error('360! 1:1');
                    return
                }
                var imgData = new FormData();
                imgData.append('file', file);
                imgData.append('moduleid', 'P11000');
                imgData.append('version', VERSION);
                imgData.append('fkeyarea', q.pleaseCode);
                q.figures = {};
                q.$http.post('/product/uploadFigure.do', imgData).then(res => {
                    if(res.data.success){
                        q.imageUrl = files.url;
                        q.figures.ffileid = res.data.data.fileid;
                        q.figures.furl = res.data.data.fileurl;
                        q.figures.fuserid = q.fsupplierid;
                        q.figures.fkeyarea = q.pleaseCode;
                        q.figures.ftype = 1;
                    }else{
                        this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                    }
                }).catch(function (error) {
                        console.log(error);
                });
            }
        },
        //
        addNewProduct(){
            for(let i=0; i<this.errorAry.length; i++){
                this.errorAry[i].error = false;
            }
            //IE9
            this.imgListUpload = [              //IE9
                {number:0, src:'', isShow:false, mask:false}
            ];
            this.uploadSrc = '';
            
            this.addNewProduce = true;
            this.fmarketproductname = '';          //
            this.fproductname = '';                //
            this.fmaterialid =  '';                //
            this.fmaxorderlength = '';             //
            this.fmaxorderwidth = '';              //
            this.fmateriallengthplus = '0.0';          //
            this.frecommendindustry = '';          //
            this.fsuitindustry = '';               //
            this.flogistics = '';                  //
            this.fminorderlength = '';             //
            this.fminorderwidth = '';              //
            this.fmaterialwidthplus = '2.0';           //
            this.flayer = '';                      //
            this.fflutetype = '';                  //
            this.fflutetypeDf = '';                //
            this.addDefaultRegion = '';
            this.addDefaultPlease = '';
            this.endTime = '';                     //
            this.fdeliveryday = '';                //
            if(this.admin.ucompany == 100){
                this.editPartName = '';
                 this.uuId = '';
                 this.key = '';
                 setTimeout(()=>{
                    this.editPartName = this.partnerName;
                    this.uuId = this.fsupplierid;
                    this.key = this.pleaseCode;
                 },100)
                this.fparterId = this.fsupplierid; //-Id
            }else{
                this.key = '';             //
                this.uuId = '';
                this.editPartName = '';
                setTimeout(()=>{
                    this.key = this.admin.ocode;             //
                    this.uuId = this.admin.ucompany;
                    this.editPartName = this.admin.oname;
                },10)
            }
            this.detailsfigure = [];
            this.delImgAry = [];
            this.figures = {
                ftype: 1
            }
            this.imageUrl = '';
            this.fileList = [];
            this.fid = '';
            this.imgFid = '';
            
        },
        //
        checkPicture(files, fileList){
            if(fileList.length == 6){
               fileList.pop();
               this.$message.warning('5!');
               return
            }
            let file = files.raw;
            var img = new Image();
            img.src = files.url;
            const isJPG = file.type === 'image/jpeg';
            const isPNG = file.type === 'image/png';
            const isLt2M = file.size / 1024 / 1024 < 2;
            if (!isJPG && !isPNG) {
                this.$message.error(' JPG  PNG !');
                fileList.pop();
                return 
            }
            if (!isLt2M) {
                this.$message.error(' 2MB!');
                fileList.pop();
                return
            }
            var q = this;
            img.onload = function(){
                if(img.width !== 1100){
                    q.$message.error('1100');
                    fileList.pop();
                    return
                }
                var imgData = new FormData();
                imgData.append('file', file);
                imgData.append('moduleid', 'P11000');
                imgData.append('version', VERSION);
                imgData.append('fkeyarea', q.pleaseCode);
                q.$http.post('/product/uploadFigure.do', imgData).then(res => {
                    if(res.data.success){
                        let imgObj={
                            ftype:2,
                            fuserid:q.fsupplierid,
                            fkeyarea:q.pleaseCode
                        }
                        imgObj.ffileid = res.data.data.fileid;
                        imgObj.furl = res.data.data.fileurl;
                        q.detailsfigure.push(simpleClone(imgObj));
                      
                        imgObj.uid = files.uid;
                        q.delImgAry.push(simpleClone(imgObj))
                    }else{
                        this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                    }
                }).catch(function (error) {
                        console.log(error);
                });
            }
        },
        //
        applayShelves(data, num){
            if(num === 1){
                let imgData ={
                    fkeyarea:this.pleaseCode,
                    fproductid:data.fid
                }
                this.$http.post('/banner/queryBannerOfproduct.do', imgData).then(res => {
                    if(res.data.success){
                        if(res.data.data.length > 0){
                            this.delProptImg = true;
                        }else{
                            let datas = {
                                version:VERSION,
                                fkeyarea:this.pleaseCode,
                                fid:data.fid
                            }
                            this.$http.post('/product/applyeExamineProduct.do', datas).then(res => {
                                if(res.data.success){
                                        this.$message({
                                            message: '',
                                            type: 'success',
                                            duration: 2000
                                        });
                                    this.searchFunc()
                                }else{
                                    this.$message({
                                            message: res.data.msg,
                                            type: 'error',
                                            duration: 2000
                                        });
                                }
                            }).catch(function (error) {
                                    console.log(error);
                            });
                        }
                    }
                }).catch(function (error) {
                        console.log(error);
                });
            }else{
                let datas = {
                    version:VERSION,
                    fkeyarea:this.pleaseCode,
                    fid:data.fid
                }
                this.$http.post('/product/applyeExamineProduct.do', datas).then(res => {
                    if(res.data.success){
                        this.$message({
                                message: '',
                                type: 'success',
                                duration: 2000
                            });
                        this.searchFunc()
                    }else{
                        this.$message({
                                message: res.data.msg,
                                type: 'error',
                                duration: 2000
                            });
                    }
                }).catch(function (error) {
                        console.log(error);
                });
            }
        },
        //
        revoke(data){
           let datas = {
                version:VERSION,
                fkeyarea:this.pleaseCode,
                fid:data.fid
            }
            this.$http.post('/product/revokeApplyProduct.do', datas).then(res => {
                if(res.data.success){
                    this.$message({
                        message: '',
                        type: 'success',
                        duration: 2000
                    });
                    this.searchFunc()
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                    console.log(error);
            });
        },
        //
        delData(){
            let data = {
                version:VERSION,
                fkeyarea:this.pleaseCode,
                fid:this.delFid
            }
            this.$http.post('/product/deleteProduct.do', data).then(res => {
                    if(res.data.success){
                        this.isDel = false;
                        this.$message({
                            message: '',
                            type: 'success',
                            duration: 1500
                        });
                        this.searchFunc()
                    }else{
                        this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                    }
                }).catch(function (error) {
                    console.log(error);
                });
        },
        //
        delPropt(data){
          this.isDel = true;
          this.delFid = data.fid;
        },
        //
        save(num){
            var data = {
                version:VERSION,
                fkeyarea:this.pleaseCode + '',                  //
                fattributename:'',                            //
                forder:'9999',                                   //
                fonoffstatus:num,                               //01
                fid:this.fid,                                   //
                fsupplierid:this.fparterId,                     //-ID
                fsuppliername:this.editPartName,                //-
                fmarketproductname:this.fmarketproductname,     //
                fproductname:this.fproductname,                 //
                fmaterialid:this.fmaterialid,                   //
                fmaxorderlength:this.fmaxorderlength,           //
                fmaxorderwidth:this.fmaxorderwidth,             //
                fmateriallengthplus:this.fmateriallengthplus,   //
                frecommendindustry:this.frecommendindustry,     //
                fsuitindustry:this.fsuitindustry,               //
                flogistics:this.flogistics,                     //
                fminorderlength:this.fminorderlength,           //
                fminorderwidth:this.fminorderwidth,             //
                fmaterialwidthplus:this.fmaterialwidthplus,     //
                flayer:this.flayer,                             //
                fflutetype:this.fflutetype,                     //
                figure:this.figures,                            //
                detailsfigure:this.detailsfigure,               //
                fgrouparea:this.fgroupareaProvice + '-' + this.fgroupareaCity,//
                fgroupareaid:this.fgroupareaid,                 //ID
                fcreatorid:this.fcreatorid,                     // 
                fdeliverytime:getBranch(this.endTime),          //
                fdeliveryday:this.fdeliveryday                  // 
           }
           for(let i=0; i<this.checkAry.length; i++){
               if(data[this.checkAry[i]].toString() == ''){
                   this.$message({
                        message: '',
                        type: 'error',
                        duration: 2000
                    });
                    return
               }
           }
           for(let i=0; i<this.errorAry.length; i++){
               if(this.errorAry[i].error){
                   this.$message({
                        message: '',
                        type: 'error',
                        duration: 2000
                    });
                    return
               }
           }
           if(!this.figures.ffileid || this.detailsfigure.length == 0){
               this.$message({
                    message: '',
                    type: 'error',
                    duration: 2000
                });
               return
           }
           if(Number(data.fmaxorderlength) <= Number(data.fminorderlength)){
               this.$message({
                    message: '',
                    type: 'warning',
                    duration: 2000
               });
               return
           }
           if(Number(data.fmaxorderwidth) <= Number(data.fminorderwidth)){
               this.$message({
                    message: '',
                    type: 'warning',
                    duration: 2000
               });
               return
           }
           this.$http.post('/product/addOrEditProducts.do', data).then(res => {
               if(res.data.success){
                    this.addNewProduce = false;
                    if(data.fid == ''){
                            this.$message({
                                message: '',
                                type: 'success',
                                duration: 2000
                            });
                    }else{
                            this.$message({
                                message: '',
                                type: 'success',
                                duration: 2000
                            });
                    }
                    this.detailsfigure = [];
                    this.searchFunc();
               }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error',
                        duration: 2000
                    });
               }
           }).catch(function (error) {
                console.log(error);
           });
        },
        //
        getNumber(name){
           for(let i=0; i < this.numberOf.length; i++){
               if(name === this.numberOf[i].regionName){
                   this.flayer = this.numberOf[i].value;
               }
           }
        },
        //
        getFlute(name){
            for(let i=0; i < this.fluteType.length; i++){
               if(name === this.fluteType[i].regionName){
                   this.fflutetype = this.fluteType[i].value;
                   this.fflutetypeDf = name;
               }
            }
        },
        //
        removeImg(file){
            //
            if(file.fid == undefined){
                for(let i=0; i<this.delImgAry.length; i++){
                    if(file.uid === this.delImgAry[i].uid){
                        var delId = this.delImgAry[i].ffileid;
                    }
                }
                for(let i=0; i<this.detailsfigure.length; i++){
                    if(delId === this.detailsfigure[i].ffileid){
                        this.detailsfigure.splice(i,1);
                    }
                }
                return
            }
            //
            for(let i=0; i<this.detailsfigure.length; i++){
                if(file.ffileid == this.detailsfigure[i].ffileid){
                    this.detailsfigure.splice(i,1);
                }
            }
        },
        //IE9
        removeImgIe(num){
            this.detailsfigure = [];
            this.imgListUpload = [];
            setTimeout(()=> {
                this.imgListUpload = [
                    {number:0 , src:'', isShow:false, mask:false}
                ];
            }, 1);
        },
        //
        searchList(data){
            this.$http.post('/product/loadProducts.do', data).then(res => {
                if(res.data.success) {
                    this.tableData = res.data.data;
                    this.listLength = res.data.total;
                    for(let i=0; i<this.tableData.length; i++){
                        this.tableData[i].number = this.currentPage * this.showPagelist - this.showPagelist + i + 1;
                        if(this.tableData[i].figure == undefined || this.tableData[i].figure == '' || this.tableData[i].figure == {} ){
                            this.tableData[i].fimg = '';
                        }else{
                            this.tableData[i].fimg = IMGPATH + this.tableData[i].figure.furl;
                        }
                        this.tableData[i].type = this.tableData[i].fauditstatus == 1 ?  '' :  this.typeArry[this.tableData[i].fonoffstatus];
                        // if(this.tableData[i].fauditstatus == 3 && this.tableData[i].fonoffstatus == 2){
                        //     this.tableData[i].type = ''
                        // }
                        // if(this.tableData[i].fauditstatus == 3 && this.tableData[i].fonoffstatus == 3 ||
                        //  this.tableData[i].fauditstatus == 3 && this.tableData[i].fonoffstatus == 0){
                        //     this.tableData[i].type = ''
                        // }
                        this.tableData[i].moneyType = this.tableData[i].fgrouptype == 1 ? '' : this.tableData[i].fgrouptype ==  2 ? '' : '';
                        this.tableData[i].flimitedtime = this.tableData[i].fgrouptype == 1 ? '/' : this.tableData[i].flimitedtime;
                        this.tableData[i].fmaxarea = this.tableData[i].fgrouptype ==  2 ? '/' : this.tableData[i].fmaxarea;
                        if(this.tableData[i].moneyType == ''){
                            this.tableData[i].fmaxarea = '/';
                            this.tableData[i].moneyType = '/';
                            this.tableData[i].flimitedtime = '/';
                            this.tableData[i].fgroupprice = '/';
                        }
                    }
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //
        editProduct(data){
            this.fileList = [];
            for(let i=0; i<this.errorAry.length; i++){
                this.errorAry[i].error = false;
            }
            this.imgFid = data.figure.fid;
            this.addNewProduce = true;
            this.fmarketproductname = data.fmarketproductname;     //
            this.fproductname = data.fproductname;                 //
            this.fmaterialid =  data.fmaterialid;                  //
            this.fmaxorderlength = data.fmaxorderlength;           //
            this.fmaxorderwidth = data.fmaxorderwidth;             //
            this.fmateriallengthplus = data.fmateriallengthplus;   //
            this.frecommendindustry = data.frecommendindustry;     //
            this.fsuitindustry = data.fsuitindustry;               //
            this.flogistics = data.flogistics;                     //
            this.fminorderlength = data.fminorderlength;           //
            this.fminorderwidth = data.fminorderwidth;             //
            this.fmaterialwidthplus = data.fmaterialwidthplus;     //
            this.flayer = data.flayer;                             //
            this.fflutetype = data.fflutetype;                     //
            this.fflutetypeDf = this.flutetypeArry[data.fflutetype];//
            this.fparterId = data.fsupplierid;                      //-Id
            this.imageUrl = data.fimg;                             //
            // IE9
            this.uploadSrc = data.fimg;
            this.editPartName = '';
            this.imgListUpload = [];
            setTimeout(()=> {
                this.editPartName = data.fsuppliername;                //
                this.fileList = data.detailsfigure;                    //
                for(let i=0; i<this.fileList.length; i++){
                    //IE9
                    let ieObj = {number: i, src:IMGPATH + this.fileList[i].furl , isShow:true, mask:false}
                    this.imgListUpload.push(simpleClone(ieObj));
                    //
                    this.fileList[i].url = IMGPATH + this.fileList[i].furl;
                }
                //IE9
                if(data.detailsfigure.length < 5){
                    this.imgListUpload.push({number:data.detailsfigure.length, src:'', isShow:false, mask:false})
                }
            }, 10);
            this.fid = data.fid;
            this.fgroupareaid = data.fgroupareaid;                 //
            this.uuId = '';
            this.key = '';
            this.fdeliveryday = data.fdeliveryday;                 //
            this.endTime = parserDate("1992-08-22" + ' ' + data.fdeliverytime);//
            var index = data.fgrouparea.indexOf('-');              //
            setTimeout(()=> {
                 this.uuId = data.fsupplierid;
                 this.key = data.fkeyarea;
                 this.addDefaultRegion = data.fgrouparea.slice(0,index);
            }, 1);
            setTimeout(()=> {
                this.addDefaultRegion = '';
            }, 10);
            setTimeout(()=> {
                this.addDefaultRegion = data.fgrouparea.slice(0,index);
            }, 200);
            this.addDefaultPlease = data.fgrouparea.slice(index + 1, data.fgrouparea.length);
            this.fgroupareaProvice = this.addDefaultRegion;
            this.fgroupareaCity = this.addDefaultPlease;
            this.figures = data.figure;
            this.detailsfigure = [];
            for(let i=0; i<data.detailsfigure.length; i++){
                this.detailsfigure.push(data.detailsfigure[i]);  
            }
        },
        //
        getPleaseName(data){
           this.fgroupareaCity = data.name;
           this.fgroupareaid = data.saleCode;
           this.addDefaultPlease = data.name;
        },
        //
        getRegionName(data){
           this.fgroupareaProvice = data;
           this.addDefaultRegion = data;
        },
        //
        searchFunc(){
            let data = {
                version:VERSION,
                fkeyarea:this.pleaseCode + '',
                fsupplierid:this.fsupplierid + '',
                pagenum:this.currentPage - 1 + '',
                pagesize:this.showPagelist + ''
            }
            if(this.pleaseCode == undefined){
                return
            }
            if(this.produceTypeDefault != ''){
                for(let i=0; i<this.produceType.length; i++){
                    if(this.produceTypeDefault === this.produceType[i].regionName){
                        var type = this.produceType[i].number;
                    }
                }
                //
                if(type == 4){
                    data.fauditstatus = '3'
                    data.fonoffstatus = '-1'
                    this.searchList(data);
                    return
                }
                if(type !== -1){
                    data.fonoffstatus = type + ''
                    this.searchList(data);
                    return
                }
            }
            this.searchList(data);
        },
        //
        changePage(data){
            this.$router.push({name: 'productManger', query: { plan: 'otherPage' }})
            this.currentPages = 'optionPricesCheme';
            this.setOptionData = data;
        },
        //
        seeDetal(data){
            this.produceDetail = true;
            this.parterDetal = data;
            this.parterDetal.url = IMGPATH +  this.parterDetal.figure.furl;
            var datas = this.parterDetal.detailsfigure;
            for(let i=0; i<datas.length; i++){
                datas[i].url = IMGPATH + datas[i].furl;
            }
        },
        openDialog(){
            this.$store.commit('openZindexModel');
        },
        closeDialog(){
            this.$store.commit('closeZindexModel');
        },
        //IE9
        addSuccess(){
            this.uploadDate=[
                {name:'moduleid', data:'P11000'},
                {name:'version', data:VERSION},
                {name:'fkeyarea', data:this.pleaseCode}
            ]
            setTimeout(()=> {
                document.getElementById("uploadForm").submit();
            }, 200);
            setTimeout(()=> {
                var str = document.getElementById("uploadFrame").contentWindow.document.body.textContent;
                var res = JSON.parse(str);
                if(res.success){
                    this.figures.ffileid = res.data.fileid;
                    this.figures.furl = res.data.fileurl;
                    this.figures.fuserid = this.fsupplierid;
                    this.figures.fkeyarea = this.pleaseCode;
                }else{
                    this.$message.error('!');
                }
            }, 500);
        },
        //IE9
        addSuccessList(index){
            this.imgListUpload[index].isShow = true;
            this.$set(this.imgListUpload, this.imgListUpload[index].isShow, true);
            this.uploadDate=[
                {name:'moduleid', data:'P11000'},
                {name:'version', data:VERSION},
                {name:'fkeyarea', data:this.pleaseCode}
            ]
            setTimeout(()=> {
                document.getElementById("uploadForm" + index).submit();
            }, 200);
            setTimeout(()=> {
                var str = document.getElementById("uploadFrame").contentWindow.document.body.textContent;
                var res = JSON.parse(str);
                if(res.success){
                    let imgObj={
                        ftype:2,
                        fuserid:this.fsupplierid,
                        fkeyarea:this.pleaseCode
                    }
                    imgObj.ffileid = res.data.fileid;
                    imgObj.furl = res.data.fileurl;
                    this.detailsfigure.push(imgObj);
                }else{
                    this.$message.error('!');
                }
                if(this.imgListUpload.length == 5 ){
                        return
                }
                this.imgListUpload.push({number:index + 1, src:'', isShow:false, mask:false})
            }, 500);
        }
    },
    watch:{
        '$route' (to, from) {
             if(this.$route.fullPath == '/home/goods/productManger'){
                 this.currentPages = 'productManger';
             }
        }
    },
    components:{
        carouseSelect,
        tHeader,
        regionalChoice,
        partner,
        pageing,
        commonInput,
        optionPrices,
        propt,
        uploadIe
    }  
}
</script>



