<template>
    <div class="partnerManage partner-propt">
        <tHeader></tHeader>
        <div class='commodity-bg'>
            <div class='commodity-select-top'>
                <div class='commodity-select-three'>
                    <regional-choice
                        @getPleaseProvice='getPlease'
                        @getRegionProvice='getRegion'
                        :parterCode='-1'
                        :defaultPlease='defaultPlease'
                        :defaultRegion='defaultRegion'
                        :isDisableds='admin.ucompany == 100 ? false : true'
                        :isProduce='true'
                    >
                    </regional-choice>
                    <div class='parter-select-top'>
                        <partner
                            @getSelect='getPatnerId'
                            :codeName='codeName'
                            :partName='partnerName'
                            :isDisableds='admin.ucompany == 100 ? false : true'
                        ></partner>
                    </div>
                    <div class='partner-select'>
                        <carouse-select
                            textName=':'
                            :region='produceType'
                            @getSelect='getSelect'
                        ></carouse-select>
                    </div>
                </div>
                <div class='partner-input'>
                    <span>:</span>
                    <el-input v-model="fproductname" placeholder=""></el-input>
                </div>
                <el-button type="primary" icon="search" @click='searchFunc'></el-button>
            </div>
            <div class='partner-table prchase-list'>
                <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" :row-class-name="tableRowClassName" style="width: 100%">
                    <el-table-column prop="number" label="" width="60">
                    </el-table-column>
                    <el-table-column prop="viewType" label="" width='100'>
                    </el-table-column>
                    <el-table-column prop="fgrouparea" label="" width='130'>
                    </el-table-column>
                    <el-table-column prop="fsuppliername" label="" width='190'>
                    </el-table-column>
                    <el-table-column prop="fproductname" label="" width='150'>
                    </el-table-column>
                    <el-table-column prop="type" label="" width="120">
                    </el-table-column>
                    <el-table-column prop="fgroupprice" label="" width="150">
                    </el-table-column>
                    <el-table-column prop="fmaxarea" label="" width="110">
                    </el-table-column>
                    <el-table-column prop="flimitedtime" label="" width="110">
                    </el-table-column>    
                    <el-table-column prop="fcreatetime" label="" width='130'>
                    </el-table-column>
                    <el-table-column prop="fauditor" label="" width='100'>
                    </el-table-column>
                    <el-table-column prop="fupdatetime" label="" width='150'>
                    </el-table-column>
                    <el-table-column label="" fixed="right">
                        <template scope="scope" >
                            <el-button  type="text" size="small" @click='viewDetails(scope.row)' v-if="scope.row.fauditstatus == '1' && admin.ucompany == 100 ?  true : false">
                                
                            </el-button>
                            <el-button type="text" size="small" @click='viewDetails(scope.row)' v-if="scope.row.fauditstatus != '1' || admin.ucompany != 100 ?  true : false">
                                
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div class='commodity-propt partner-product-propt'>
            <el-dialog title="" :visible.sync="addNewProduce" :show-close='false' 
             @open='openDialog' @close='closeDialog' :modal-append-to-body='false' :close-on-click-modal='false'>
                <img src='../../../../../static/images/publicImages/close.png' class='partaner-product-close' @click='addNewProduce = false'>
                <div class='partner-product-bg'>
                    <div class='partner-plan-title'>
                        <p class='parter-maxLngth'><span>:</span><span>{{parterDetal.fmarketingname}}</span></p>
                        <p class='parter-maxLngth-type'><span class='partner-plan-span'>:</span><span>{{parterDetal.fgrouptype == '1' ? '' : parterDetal.fgrouptype == '2' ? '' : ''}}</span></p>
                        <p class='parter-maxLngth'><span class='partner-plan-span'>:</span><span>{{parterDetal.fproductname}}</span></p>
                    </div>
                    <div class='partner-plan-font'></div>
                    <div class='partner-plan-information'>
                        <p class='parter-maxLngth-price'><span>:</span><span>{{parterDetal.fminarea}}</span><span></span></p><span>:</span><span>{{parterDetal.fnormalarea}}</span><span></span>
                    </div>
                    <div class='partner-plan-information'>
                        <span v-if="parterDetal.fgrouptype == 2 ? true : false">:</span><span v-if="parterDetal.fgrouptype == 2 ? true : false">{{parterDetal.flimitedtime}}</span>
                        <span v-if="parterDetal.fgrouptype == 1 ? true : false">:</span><span v-if="parterDetal.fgrouptype == 1 ? true : false">{{parterDetal.fmaxarea}}</span><span v-if="parterDetal.fgrouptype == 1 ? true : false"></span>
                    </div>
                    <div class='partner-plan-information'>
                        <p class='parter-maxLngth-price'><span>:</span><span>{{parterDetal.funitprice}}</span><span>/</span></p><span>:</span><span>{{parterDetal.fgroupprice}}</span><span>/</span>
                    </div>
                    <div class='partner-plan-information'>
                        <span>:</span><span>{{parterDetal.fsmallprice}}</span><span>/</span>
                    </div>
                    <div class='partner-plan-font'></div>
                    <div class='partner-plan-img'>
                        <p class='partner-add-img'>
                            :
                        </p>
                        <img :src='parterDetal.startfigures' class='partner-product-list'>
                    </div>
                    <div class='partner-plan-img'>
                        <p class='partner-add-img'>
                            :
                        </p>
                        <img :src='parterDetal.endFurls' class='partner-product-list'>
                    </div>
                </div>
                <div class='partner-product-isPass'>
                       <p></p>
                       <div class='partner-product-radio' v-if="parterDetal.fauditstatus == '1'  && admin.ucompany == 100 ? true: false">
                            <el-radio class="radio" v-model="radio" label="1"></el-radio>
                            <el-radio class="radio" v-model="radio" label="2"></el-radio>
                            <div class='partner-product-input'>
                                <el-input v-model="reject" placeholder="20" :maxlength='20' v-if="radio == '2'"></el-input>
                            </div>
                       </div>
                       <div class='partner-product-btn' v-if="parterDetal.fauditstatus == '1'  && admin.ucompany == 100 ? true: false">
                            <el-button @click='addNewProduce = false'></el-button>
                            <el-button type="primary" @click='saveView'></el-button>
                       </div>
                        <div class='partner-product-radio' v-if="parterDetal.fauditstatus == '2' ? true: false">
                           <div class='partner-check-pass'></div>
                           <el-button @click='addNewProduce = false'></el-button>
                       </div>
                       <div class='partner-product-radio' v-if="admin.ucompany != 100 &&parterDetal.fauditstatus != '3' &&parterDetal.fauditstatus != '2' ? true: false">
                           <div class='partner-check-pass'></div>
                           <el-button @click='addNewProduce = false'></el-button>
                       </div>   
                       <div class='partner-product-radio' v-if="parterDetal.fauditstatus == '3' ? true: false">
                           <div class='partner-check-pass'>:
                               <span>{{parterDetal.fcomment}}</span>
                           </div>
                           <el-button @click='addNewProduce = false'></el-button>
                       </div>      
                </div>
            </el-dialog>
        </div>
    </div>
</template>

<script>
import carouseSelect from '@/components/public/select'
import tHeader from '@/components/public/tHeader'
import regionalChoice from '@/components/public/regionalChoice'
import partner from '@/components/public/Partner'
import pageing from '@/components/public/paging'
import {VERSION, IMGPATH} from '@/util/constant.js' 
import {clippingProvice, clippingCity} from '@/util/common.js'
export default {
    created(){
        this.admin = this.$store.getters.doneAdmin;
        this.fauditorid = this.admin.ucompany;
        this.fauditor = this.admin.uname;
        this.parterId = this.admin.ucompany;       //id
        //
        this.fkeyarea = this.admin.ocode;
        //
        if(this.admin.ucompany != 100){
           this.defaultRegion = clippingProvice(this.admin.oprovince);
           this.defaultPlease = clippingCity(this.admin.ocity);
           setTimeout(()=> {
               this.partnerName = this.admin.oname;    //
           }, 1);
           this.searchFunc();
        }
    },
	data () {
		return {
            partnerName:'',
            defaultPlease:'',
            defaultRegion:'',
            admin:{},
            produceType:[
                {regionName:'', value:'0'},
                {regionName:'', value:'1'},
                {regionName:'', value:'2'},
                {regionName:'', value:'3'}
            ],
            addNewProduce:false,
            codeName:'',               //
            fkeyarea:'',               //
            parterId:'',               //ID
            radio:'1',
            fauditstatus:'0',          //
            fproductname:'',           //
            reject:'',                 //
            fgroupareaid:'',           //ID
            fauditorid:'',             //ID
            fauditor:'',               //
            tableData:[],
            parterDetal:{},
            typeArry:{
                '0':'',
                '1':'',
                '2':'',
                '3':''
            }
		}
	},
	methods:{
        //
        tableRowClassName(row, index) {
            if (((index + 1) % 2) === 0) {
                return 'info-row';
            }
            return '';
        },
        //code
        getPlease(please){
            //  this.fgroupareaid = please.code;
             this.codeName = please;
        },
        //
        getRegion(){
            this.codeName = '';
            this.tableData = [];
        },
        //
        getPatnerId(data){
            if(data.id == undefined && data.code == undefined){
                return
            }
            this.parterId = data.id;
            this.fkeyarea = data.code;
            this.partnerName = data.name;
        },
        //
        getSelect(name){
            for(let i=0; i<this.produceType.length; i++){
               if(name == this.produceType[i].regionName){
                    this.fauditstatus = this.produceType[i].value;
               }
            }
        },
        //
        searchFunc(){
            let data={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.parterId,
                fauditstatus:this.fauditstatus,
                fproductname:this.fproductname,
                pagenum:0,
                pagesize:1000,
                sort:'2'
                // fgroupareaid:this.fgroupareaid
            }
            if(this.partnerName == '' && this.admin.ucompany == 100){
                this.$message({
                    message: '',
                    type: 'warning',
                    duration: 2000
                });
                return
            }
            this.$http.post('/product/loadListsPMarketing.do', data).then(res => {
                if(res.data.success){
                    this.tableData = res.data.data;
                    for(let i=0; i<this.tableData.length; i++){
                        this.tableData[i].number = i + 1;
                        this.tableData[i].type = this.tableData[i].fgrouptype == '1' ? '' : this.tableData[i].fgrouptype == '2' ? '' :'';
                        this.tableData[i].viewType = this.typeArry[this.tableData[i].fauditstatus];
                        this.tableData[i].flimitedtime = this.tableData[i].fgrouptype == 1 ? '/' : this.tableData[i].flimitedtime;
                        this.tableData[i].fmaxarea = this.tableData[i].fgrouptype ==  2 ? '/' : this.tableData[i].fmaxarea;
                        if(this.tableData[i].fauditstatus == 1){
                            this.tableData[i].fupdatetime = '/';
                            this.tableData[i].fauditor = '/';
                        }
                    }
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //
        viewDetails(data){
            this.addNewProduce = true;
            this.reject = '';
            this.radio = '1'
            let datas = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.parterId,
                fmarketingplanid:data.fmarketingplanid
            }
            this.$http.post('/product/loadListsPMarketing.do', datas).then(res => {
                if(res.data.success){
                   this.parterDetal = res.data.data[0];
                   this.parterDetal.endFurls = IMGPATH +  res.data.data[0].endfigure.furl;
                   this.parterDetal.startfigures = IMGPATH +  res.data.data[0].startfigure.furl;
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //
        saveView(){
           let data ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fmarketingplanid:this.parterDetal.fmarketingplanid,
                fcomment:this.reject,
                operate:this.radio == '1' ? true : false,
                fauditorid:this.fauditorid,
                fauditor:this.fauditor
            }
            if(this.radio == '2' && this.reject ==''){
                this.$message({
                        message: '',
                        duration: 2000
                     });
                return
            }
            this.$http.post('/product/examineProgrammes.do', data).then(res => {
                if(res.data.success) {
                   this.$message({
                        message: '',
                        type: 'success',
                        duration: 2000
                     });
                     this.addNewProduce = false; 
                     this.searchFunc();
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        openDialog(){
            this.$store.commit('openZindexModel');
        },
        closeDialog(){
            this.$store.commit('closeZindexModel');
        }
    },
    components:{
        carouseSelect,
        tHeader,
        regionalChoice,
        partner,
        pageing
    }  
}
</script>


