<template>
    <div id="partnerManage">
        <div class="main">
            <tHeader style="color: #000;">{{this.$route.name == 'addPartner' ? '' : ''}}</tHeader>
            <div class="form">
<!----------------------------------------------------->
                <div class="partner-name">
                    <div>
                        <span class="bold">:</span>
                        <!---->
                        <el-select v-model="oaid" placeholder="" :loading="partnerLoading" @change="selectPartner(oaid)" :disabled="partnerDisabled" v-if="this.$route.name == 'addPartner'">
                            <el-option
                              v-for="item in partnerList"
                              :key="item.id"
                              :label="item.oname"
                              :value="item.id">
                            </el-option>
                        </el-select>
                        <!---->
                        <el-select v-model="partnerInfo.id" placeholder="" :disabled="partnerDisabled" v-else>
                            <el-option
                                :label="partnerInfo.oname"
                                :value="partnerInfo.id">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="clearfix partner-info" v-show="oaid">
                        <div class="fl">
                            <h3></h3>
                            <div class="offset">
                                <p> : <span>{{partnerInfo.oname}}</span></p>
                                <p> : <span>{{getDate}}</span></p>
                                <p> : <span>{{partnerInfo.oprovince}}-{{partnerInfo.ocity}}-{{partnerInfo.oarea}}</span></p>
                            </div>
                        </div>
                        <div class="fl">
                            <h3></h3>
                            <div class="offset">
                                <p> : <span>{{partnerInfo.uname}}</span></p>
                                <p>&nbsp;&nbsp;&nbsp; : <span>{{partnerInfo.ucard}}</span></p>
                                <p> : <span>{{partnerInfo.uphone}}</span></p>
                                <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; : <span>{{partnerInfo.uemail}}</span></p>
                            </div>
                        </div>
                    </div>
                </div>
                <el-form class="info" :model="partnerData" :rules="rules" ref="partnerData">
<!----------------------------------------------------->
                    <div class="base-info">
                        <p class="info-title"><span class="bold"></span><span class="red">(*)</span></p>
                        <div class="el-form--inline">
                            <el-form-item label=":" prop="registercapital">
                                <el-input v-model.trim="partnerData.registercapital" :maxlength="9" class="aW"></el-input> 
                            </el-form-item>
                            <el-form-item label=":" prop="annualsales">
                                <el-input v-model.trim="partnerData.annualsales" :maxlength="9" class="bW"></el-input> 
                            </el-form-item>
                        </div>
                        <div class="el-form--inline">
                            <el-form-item label=":" prop="bankcard" >
                                <!--<span class="sure">*</span>-->
                                <el-input v-model.trim="partnerData.bankcard" :maxlength="20"></el-input>
                            </el-form-item>
                            <el-form-item label=":" prop="bank">
                                <!--<span class="sure">*</span>-->
                                <el-input v-model.trim="partnerData.bank" :maxlength="20"></el-input>
                            </el-form-item>
                            <el-form-item label=":" prop="accountholder">
                                <!--<span class="sure" style="left: -57px;">*</span>-->
                                <el-input v-model.trim="partnerData.accountholder" :maxlength="10"></el-input>
                            </el-form-item>
                        </div>
                        <div class="el-form--inline">
                            <el-form-item label=" " prop="fax">
                                <label class="label-change" style="left: -65px;" >&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</label>
                                <el-input v-model.trim="partnerData.fax" :maxlength="20"></el-input>
                            </el-form-item>
                            <el-form-item label=" " prop="website">
                                <label class="label-change" style="left: -65px;">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</label>
                                <el-input v-model.trim="partnerData.website" :maxlength="30"></el-input>
                            </el-form-item>
                        </div>
                    </div>
                    <div class="unfolded" @click="unfolded = !unfolded">
                        <p v-show="unfolded">
                            &nbsp;&nbsp;&nbsp;<i class="el-icon-d-arrow-left" style="transform: rotate(-90deg)"></i>
                        </p>
                        <p v-show="!unfolded">
                            &nbsp;&nbsp;&nbsp;<i class="el-icon-d-arrow-right" style="transform: rotate(-90deg)"></i>
                        </p>
                    </div>
                    <div class="hidden-info" :style="{height: unfolded ? 0 : '640px'}">
<!----------------------------------------------------->
                        <div class="produce-info">
                            <p class="info-title"><span class="bold"></span></p>
                            <div class="el-form--inline">
                                <el-form-item label=":">
                                    <el-select v-model="partnerData.enterprisetype" placeholder="">
                                        <el-option label="" disabled value="0"></el-option>
                                        <el-option label="" value="1"></el-option>
                                        <el-option label="" value="2"></el-option>
                                        <el-option label="" value="3"></el-option>
                                        <el-option label="" value="4"></el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label=":" prop="enterprisenumber">
                                    <el-input v-model.trim="partnerData.enterprisenumber" :maxlength="6"></el-input>
                                </el-form-item>
                                <el-form-item label=":" prop="plantarea">
                                    <el-input v-model.trim="partnerData.plantarea" :maxlength="5" class="cW"></el-input> 
                                </el-form-item>
                            </div>
                            <div class="el-form--inline">
                                <el-form-item label=":">
                                    <el-select v-model="partnerData.planttype" placeholder="">
                                        <el-option label="" disabled value="0"></el-option>
                                        <el-option label="" value="1"></el-option>
                                        <el-option label="" value="2"></el-option>
                                        <el-option label="+" value="3"></el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label=":" prop="linenumber">
                                    <el-input v-model.trim="partnerData.linenumber" :maxlength="2"></el-input>
                                </el-form-item>
                                <el-form-item label=":" prop="linedoor">
                                    <el-input v-model.trim="partnerData.linedoor" :maxlength="3" class="cW"></el-input> &nbsp;&nbsp;&nbsp;
                                </el-form-item>
                            </div>
                            <div class="el-form--inline">
                                <el-form-item label=":" prop="linebrand">
                                    <el-input v-model.trim="partnerData.linebrand":maxlength="20"></el-input>
                                </el-form-item>
                                <el-form-item label=":" prop="linespeed">
                                    <el-input v-model.trim="partnerData.linespeed" :maxlength="8" class="dW"></el-input> /
                                </el-form-item>
                                <el-form-item label=":" prop="opentime">
                                    <el-input v-model.trim="partnerData.opentime" :maxlength="4" class="cW" @blur="toFloat(partnerData.opentime, 'opentime', 1)"></el-input> &nbsp;&nbsp;&nbsp;
                                </el-form-item>
                            </div>
                            <div class="el-form--inline">
                                <el-form-item label=":">
                                    <el-select v-model="partnerData.productionshift" placeholder="">
                                        <el-option label="" disabled value="0"></el-option>
                                        <el-option label="" value="1"></el-option>
                                        <el-option label="" value="2"></el-option>
                                        <el-option label="" value="3"></el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label=":" prop="avgcapacity">
                                    <el-input v-model.trim="partnerData.avgcapacity" :maxlength="9" class="eW"></el-input> 
                                </el-form-item>
                                <el-form-item label=":" prop="maxcapacity">
                                    <el-input v-model.trim="partnerData.maxcapacity" :maxlength="9" class="cW"></el-input> 
                                </el-form-item>
                            </div>
                            <div class="el-form--inline">
                                <el-form-item label=":" prop="productquantity">
                                    <el-input v-model.trim="partnerData.productquantity" :maxlength="3"></el-input>
                                </el-form-item>
                                <el-form-item label=":" prop="maxthree">
                                    <el-input v-model.trim="partnerData.maxthree" :maxlength="20"></el-input>
                                </el-form-item>
                                <el-form-item label=":">
                                   <el-select v-model="partnerData.transporttype" placeholder="">
                                       <el-option label="" disabled value="0"></el-option>
                                        <el-option label="" value="1"></el-option>
                                        <el-option label="" value="2"></el-option>
                                        <el-option label="+" value="3"></el-option>
                                    </el-select>
                                </el-form-item>
                            </div>
                            <div class="el-form--inline">
                                <el-form-item label=":" prop="carnumber">
                                    <el-input v-model.trim="partnerData.carnumber" :maxlength="3" class="fW"></el-input> 
                                </el-form-item>
                            </div>
                        </div>
<!----------------------------------------------------->
                        <div class="people-info">
                            <p class="info-title"><span class="bold"></span></p>
                            <div class="el-form--inline">
                                <el-form-item label="/:" prop="managername">
                                    <el-input v-model.trim="partnerData.managername" :maxlength="10"></el-input>
                                </el-form-item>
                                <el-form-item label=":">
                                    <el-date-picker
                                      v-model="partnerData.age"
                                      align="right"
                                      type="year"
                                      placeholder="">
                                    </el-date-picker>
                                </el-form-item>
                                <el-form-item label=":">
                                   <el-select v-model="partnerData.educationbackground" placeholder="">
                                       <el-option label="" disabled value="0"></el-option>
                                        <el-option label="" value="1"></el-option>
                                        <el-option label="" value="2"></el-option>
                                        <el-option label="" value="3"></el-option>
                                        <el-option label="" value="4"></el-option>
                                        <el-option label="" value="5"></el-option>
                                        <el-option label="" value="6"></el-option>
                                        <el-option label="" value="7"></el-option>
                                    </el-select>
                                </el-form-item>
                            </div>
                            <div class="el-form--inline" prop="managenumber">
                                <el-form-item label=":">
                                    <el-input v-model.trim="partnerData.managenumber" :maxlength="3"></el-input>
                                </el-form-item>
                                <el-form-item label=":" prop="rdnumber">
                                    <el-input v-model.trim="partnerData.rdnumber" :maxlength="5"></el-input>
                                </el-form-item>
                                <el-form-item label=":" prop="fmassnumber">
                                   <el-input v-model.trim="partnerData.fmassnumber" :maxlength="5"></el-input>
                                </el-form-item>
                            </div>
                            <div class="el-form--inline">
                                <el-form-item label=":" prop="salesnumber">
                                    <el-input v-model.trim="partnerData.salesnumber" :maxlength="5"></el-input>
                                </el-form-item>
                                <el-form-item label=":" prop="logisticsnumber">
                                    <el-input v-model.trim="partnerData.logisticsnumber" :maxlength="5"></el-input>
                                </el-form-item>
                                <el-form-item label=":" prop="onelinenumber">
                                   <el-input v-model.trim="partnerData.onelinenumber" :maxlength="5"></el-input>
                                </el-form-item>
                            </div>
                        </div>
                    </div>
<!--------------------------------------------------------------->
                    <div class="order-config">
                        <p class="info-title"><span class="bold"></span><span class="red">()</span></p>
                        <div class="el-form--inline">
                            <el-form-item label=" " prop="configuration">
                               <el-select v-model="partnerData.configuration" placeholder="">
                                    <el-option label="" disabled value="0"></el-option>
                                    <el-option label="" value="1"></el-option>
                                    <el-option label="" value="2"></el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </div>
                </el-form>
            </div>
            <!---->
            <div class="submit">
                <el-button @click="cancel"></el-button>
                <el-button type="primary" @click="save('partnerData')"></el-button>
            </div>
        </div>
    </div>
</template>

<script>
    import qs from 'qs'
    import tHeader from '@/components/public/tHeader'
    import rules from './rules'
    import partnerData from './partnerData'

    export default {
        name: 'partnerManage',
        data() {
            return {
                //
                partnerDisabled: false,
                partnerLoading: true,
                oaid: '',
                partnerInfo: {},
                partnerList: [],
                //
                labelPosition: 'right',
                unfolded: true,
                partnerData: JSON.parse(JSON.stringify(partnerData)),        //  data
                rules               //
            }
        },
        computed: {
            getDate() {
                let t = new Date(this.partnerInfo.create_time)
                let   year = t.getFullYear();
                let   month = t.getMonth()+1;
                let   date = t.getDate();
                return year+"-"+month+"-"+date;
            }
        },
        components: {
            tHeader
        },
        mounted() {
            if(this.$route.name == 'addPartner') {
                //
                this.$http.post('partners/findOaPartners.do').then( res => {
                    if(res.data.success) {
                        if(res.data.data.length) {
                            this.partnerLoading = false
                            this.partnerList = res.data.data
                        }
                        else {
                            this.$message({
                                message: '',
                                duration: 1000,
                                type: 'info'
                            });
                        }
                    }
                 }).catch( error => {
                    this.$message({
                        message: '',
                        duration: 1000,
                        type: 'error'
                    });
                    console.log(':' + error)
                 })
            }
            else {
                //
                this.partnerDisabled = true
                this.$http.post('partners/findOnePartners.do', this.$route.query).then( res => {
                    let partnerData = res.data.data
                    if(partnerData.age == null) {
                        partnerData.age = ''
                    }
                    else {
                        let age = new Date(partnerData.age).toISOString()   // utc
                        partnerData.age = age
                    }
                    for(let item in partnerData) {
                        partnerData[item] = partnerData[item] + ''
                    }
                    partnerData.ocode = +partnerData.ocode
                    this.partnerData = partnerData

                    //
                    this.$http.post('partners/findPartnerByOaid.do', {oaid: partnerData.id }).then(res => {
                        if(res.data.success) {
                            this.partnerInfo = res.data.data[0]
                            this.oaid = this.partnerInfo.id
                        }
                    }).catch(error => {
                        console.log(':' + error)
                    })

                }).catch( error => {
                    this.$message({
                        message: '',
                        duration: 1000,
                        type: 'error'
                    });
                    console.log(':' + error)
                })
            }
        },
        methods: {
            //
            selectPartner(oaid) {
                this.$http.post('partners/findPartnerByOaid.do', {oaid:oaid }).then( res => {
                    if(res.data.success) {
                        this.partnerInfo = res.data.data[0]
                        this.partnerData.id = oaid
                        this.partnerData.oname = this.partnerInfo.oname
                        this.partnerData.uname = this.partnerInfo.uname
                        this.partnerData.oprovince = this.partnerInfo.oprovince
                        this.partnerData.ocity = this.partnerInfo.ocity
                        this.partnerData.uphone = this.partnerInfo.uphone
                        this.partnerData.ocode = this.partnerInfo.ocode
                    }
                    else {
                        this.$message({
                            message: '',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                }).catch( error => {
                    this.$message({
                        message: '',
                        duration: 1000,
                        type: 'error'
                    });
                    console.log(':' + error)
                })
            },
            //
            toFloat(val, key, num) {    //  
                if(val === '') return
                let float = (+val).toFixed(num)
                this.partnerData[key] = float
            },
            cancel() {
                this.$router.push({name: 'partner'})
            },
            save(formName) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: ''
                }).then(() => {
                    //
                    if(!this.oaid) {
                        this.$message({
                            message: '',
                            duration: 1000,
                            type: 'warning'
                        });
                        return false
                    }
                    this.$refs[formName].validate((valid) => {
                        if (valid) {
                            //url
                            let url = ''
                            if(this.$route.name == 'addPartner') {
                                url = 'partners/addPartners.do'
                            }
                            else {
                                url = 'partners/updatePartners.do'
                            }
                            this.$http.post(url, this.partnerData).then( res => {
                                if(res.data.success) {
                                    this.$router.push({name: 'partner'})
                                }
                                else {
                                    this.$message({
                                        message: '',
                                        duration: 1000,
                                        type: 'error'
                                    });
                                }
                            }).catch( error => {
                                console.log(':' + error)
                            });
                        } else {
                            this.$message({
                                message: '',
                                duration: 1000,
                                type: 'warning'
                            });
                            return false;
                        }
                    });
                });
            }
        }
    }
</script>

<style lang="scss">
    @import '../../../../assets/style/partnerManage.scss';
</style>



