<template>
    <div id="seePartner">
        <div class="main">
            <tHeader style="color: #000;"></tHeader>
            <div class="form">
<!----------------------------------------------------->
                <div class="partner-name">
                    <div>
                        <span class="bold">:</span>
                        <el-select v-model="partnerInfo.oname" placeholder="" disabled>
                            <el-option :label="partnerInfo.oname" :value="partnerInfo.id"></el-option>
                        </el-select>
                    </div>
                    <div class="clearfix partner-info">
                        <div class="fl">
                            <h3></h3>
                            <div class="offset">
                                <p> : <span>{{partnerInfo.oname}}</span></p>
                                <p> : <span>{{getDate}}</span></p>
                                <p> : <span>{{partnerInfo.oprovince}}-{{partnerInfo.ocity}}-{{partnerInfo.oarea}}</span></p>
                            </div>
                        </div>
                        <div class="fl">
                            <h3></h3>
                            <div class="offset">
                                <p> : <span>{{partnerInfo.uname}}</span></p>
                                <p>&nbsp;&nbsp;&nbsp; : <span>{{partnerInfo.ucard}}</span></p>
                                <p> : <span>{{partnerInfo.uphone}}</span></p>
                                <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; : <span>{{partnerInfo.uemail}}</span></p>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="info">
<!----------------------------------------------------->
                    <div class="base-info">
                        <p class="info-title"><span class="bold"></span></p>
                        <table>
                            <tr>
                                <td>
                                    <div>:</div>
                                    <div><span class="bd">{{partnerData.registercapital}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
                                </td>
                                <td>
                                    <div>:</div>
                                    <div><span class="bd">{{partnerData.annualsales}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div>:</div>
                                    <div><span class="bd">{{partnerData.bankcard}}</span></div>
                                </td>
                                <td>
                                    <div>:</div>
                                    <div><span class="bd">{{partnerData.bank}}</span></div>
                                </td>
                                <td>
                                    <div>:</div>
                                    <div><span class="bd">{{partnerData.accountholder}}</span></div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</div>
                                    <div><span class="bd">{{partnerData.fax}}</span></div>
                                </td>
                                <td>
                                    <div>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;:</div>
                                    <div><span class="bd">{{partnerData.website}}</span></div>
                                </td>
                            </tr>
                        </table>
                    </div>
                    <div class="unfolded" @click="unfolded = !unfolded">
                        <p v-show="unfolded">
                            &nbsp;&nbsp;&nbsp;<i class="el-icon-d-arrow-left" style="transform: rotate(-90deg)"></i>
                        </p>
                        <p v-show="!unfolded">
                            &nbsp;&nbsp;&nbsp;<i class="el-icon-d-arrow-right" style="transform: rotate(-90deg)"></i>
                        </p>
                    </div>
                    <div class="hidden-info" :style="{height: unfolded ? 0 : '640px'}">
                        <!----------------------------------------------------->
                        <div class="produce-info">
                            <p class="info-title"><span class="bold"></span></p>
                            <table>
                                <tr>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.enterprisetype}}</span></div>
                                    </td>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.enterprisenumber}}</span></div>
                                    </td>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.plantarea}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.planttype}}</span></div>
                                    </td>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.linenumber}}</span></div>
                                    </td>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.linedoor}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.linebrand}}</span></div>
                                    </td>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.linespeed}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;/</div>
                                    </td>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.opentime}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.productionshift}}</span></div>
                                    </td>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.avgcapacity}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
                                    </td>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.maxcapacity}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.productquantity}}</span></div>
                                    </td>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.maxthree}}</span></div>
                                    </td>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.transporttype}}</span></div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.carnumber}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
                                    </td>
                                </tr>
                            </table>
                        </div>
                        <!----------------------------------------------------->
                        <div class="people-info">
                            <p class="info-title"><span class="bold"></span></p>
                            <table>
                                <tr>
                                    <td>
                                        <div>/:</div>
                                        <div><span class="bd">{{partnerData.managername}}</span></div>
                                    </td>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{ partnerData.age }}</span></div>
                                    </td>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.educationbackground}}</span></div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.managenumber}}</span></div>
                                    </td>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.rdnumber}}</span></div>
                                    </td>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.fmassnumber}}</span></div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.salesnumber}}</span></div>
                                    </td>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.logisticsnumber}}</span></div>
                                    </td>
                                    <td>
                                        <div>:</div>
                                        <div><span class="bd">{{partnerData.onelinenumber}}</span></div>
                                    </td>
                                </tr>
                            </table>
                        </div>
                    </div>
<!--------------------------------------------------------------->
                    <div class="order-config">
                        <p class="info-title"><span class="bold"></span></p>
                        <p><span class="bd">{{partnerData.configuration}}</span></p>
                    </div>
                </div>
            </div>
            <div class="foot">
                <el-button @click="cancel"></el-button>
            </div>
        </div>
    </div>
</template>

<script>
    import tHeader from '@/components/public/tHeader'
    export default {
        name: 'partnerManage',
        data() {
            return {
                //
                partnerInfo: {},
                //
                unfolded: true,
                partnerData: {}
            }
        },
        computed: {
            getDate() {
                let t = new Date(this.partnerInfo.create_time)
                let   year = t.getFullYear();
                let   month = t.getMonth()+1;
                let   date = t.getDate();
                return year+"-"+month+"-"+date;
            }
        },
        components: {
            tHeader
        },
        created() {
            //
            this.$http.post('partners/findOnePartners.do', this.$route.query).then( res => {
                this.partnerData = res.data.data
                this.filterSelectValue()

                //
                this.$http.post('partners/findPartnerByOaid.do', {oaid: this.partnerData.id }).then(res => {
                    if(res.data.success) {
                        this.partnerInfo = res.data.data[0]
                    }
                }).catch(error => {
                    console.log(':' + error)
                })

            }).catch( error => {
                this.$message({
                    message: '',
                    duration: 1000,
                    type: 'error'
                });
                console.log(':' + error)
            })

        },
        methods: {
            cancel() {
                this.$router.push({name: 'partner'})
            },
            //selectint
            filterSelectValue() {
                //
                switch (this.partnerData.enterprisetype) {
                    case 0:
                        this.partnerData.enterprisetype = ''
                        break;
                    case 1:
                        this.partnerData.enterprisetype = ''
                        break;
                    case 2:
                        this.partnerData.enterprisetype = ''
                        break;
                    case 3:
                        this.partnerData.enterprisetype = ''
                        break;
                    case 4:
                        this.partnerData.enterprisetype = ''
                        break;
                }
                //
                switch (this.partnerData.planttype) {
                    case 0:
                        this.partnerData.planttype = ''
                        break;
                    case 1:
                        this.partnerData.planttype = ''
                        break;
                    case 2:
                        this.partnerData.planttype = ''
                        break;
                    case 3:
                        this.partnerData.planttype = '+'
                        break;
                }
                //
                switch (this.partnerData.productionshift) {
                    case 0:
                        this.partnerData.productionshift = ''
                        break;
                    case 1:
                        this.partnerData.productionshift = ''
                        break;
                    case 2:
                        this.partnerData.productionshift = ''
                        break;
                    case 3:
                        this.partnerData.productionshift = ''
                        break;
                }
                //
                switch (this.partnerData.transporttype) {
                    case 0:
                        this.partnerData.transporttype = ''
                        break;
                    case 1:
                        this.partnerData.transporttype = ''
                        break;
                    case 2:
                        this.partnerData.transporttype = ''
                        break;
                    case 3:
                        this.partnerData.transporttype = '+'
                        break;
                }
                //
                if('age' in this.partnerData) {
                    let t = new Date(this.partnerData.age)
                    this.partnerData.age = t.getFullYear()
                }
                else {
                    this.partnerData.age = ''
                }
                //
                switch (this.partnerData.educationbackground) {
                    case 0:
                        this.partnerData.educationbackground = ''
                        break;
                    case 1:
                        this.partnerData.educationbackground = ''
                        break;
                    case 2:
                        this.partnerData.educationbackground = ''
                        break;
                    case 3:
                        this.partnerData.educationbackground = ''
                        break;
                    case 4:
                        this.partnerData.educationbackground = ''
                        break;
                    case 5:
                        this.partnerData.educationbackground = ''
                        break;
                    case 6:
                        this.partnerData.educationbackground = ''
                        break;
                }
                //
                switch (this.partnerData.configuration) {
                    case 0:
                        this.partnerData.configuration = ''
                        break;
                    case 1:
                        this.partnerData.configuration = ''
                        break;
                    case 2:
                        this.partnerData.configuration = ''
                        break;
                }

            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../../../assets/style/seePartner.scss';
</style>



