<template>
    <div id="userManage-warp">
        <div class="userManage-warp-padding">
            <div class="useManage-radius">
                <tHeader></tHeader>
                <div class="userManage-tableQuery-padding">
                    <!--start-->
                    <ul class="clearfix userManage-Query">
                        <li>
                            
                            <el-select   v-model="userManageQuery.mainUser.feffect" placeholder="" clearable>
                                <el-option v-for="item in accountStateOptions" :key="item.value" :label="item.label" :value="item.value">
                                </el-option>
                            </el-select>
                        </li>
                        <li>
                            
                            <el-select  v-model="userManageQuery.mainUser.fcertification" placeholder="" clearable>
                                <el-option v-for="item in authentStateOptions" :key="item.value" :label="item.label" :value="item.value">
                                </el-option>
                            </el-select>
                        </li>
                        <li class="userManage-input" :class="{telError:telError}">
                            
                            <input  v-model="userManageQuery.mainUser.fphone"  class="userManage-tel" @blur="telBlur()" @input="telInput()"></input>
                            <span>11</span>
                        </li>
                        <li>
                            <el-button type="primary" icon="search" @click="query"></el-button>
                        </li>
                    </ul>
                    <!--end-->
                    <!--start-->
                    <el-table border ref="singleTable" :data="tableData" highlight-current-row  style="width: 100%"  :row-class-name="tableRowClassName" height="700">
                        <el-table-column  label="" width="70" align="center">
                            <template scope="scope">
                                <span>{{scope.$index + userManageQuery.pageSize*userManageQuery.pageNo+1}}</span>
                            </template>
                        </el-table-column>
                        <el-table-column property="faccount" label="" align="center">
                        </el-table-column>
                        <el-table-column property="fphone" label=""  align="center">
                        </el-table-column>
                        <el-table-column label="" align="center">
                            <template scope="scope">
                                <span  v-show="scope.row.feffect == 0" class="userManage-disableBtn" ></span>
                                <span  v-show="scope.row.feffect == 1" class="userManage-enableBtn"></span>
                            </template>
                        </el-table-column>
                        <el-table-column property="fcreateTime" label="" align="center" width="145">
                        </el-table-column>
                        <el-table-column  label="" align="center" >
                            <template scope="scope">
                                <!--<span  v-show="scope.row.fcertificationType == 0" ></span>-->
                                <!--<span  v-show="scope.row.fcertificationType == 10" ></span>-->
                                <!--<span  v-show="scope.row.fcertificationType == 11" ></span>-->
                                <!--<span  v-show="scope.row.fcertificationType == 12" ></span>-->
                                <!--<span  v-show="scope.row.fcertificationType == 20" ></span>-->
                                <!--<span  v-show="scope.row.fcertificationType == 21" ></span>-->
                                <!--<span  v-show="scope.row.fcertificationType == 22" ></span>-->
                                <!--10  12 20  22  -->
                                <span  v-show="scope.row.fcertificationType == 0" >/</span>
                                <span  v-show="scope.row.fcertificationType == 10" >/</span>
                                <span  v-show="scope.row.fcertificationType == 11" ></span>
                                <span  v-show="scope.row.fcertificationType == 12" >/</span>
                                <span  v-show="scope.row.fcertificationType == 23" ></span>
                                <span  v-show="scope.row.fcertificationType == 25" ></span>
                                <span  v-show="scope.row.fcertificationType == 20" >/</span>
                                <span  v-show="scope.row.fcertificationType == 21" ></span>
                                <span  v-show="scope.row.fcertificationType == 22" >/</span>
                                <span  v-show="scope.row.fcertificationType == 26" ></span>
                                <span  v-show="scope.row.fcertificationType == 27" ></span>
                            </template>
                        </el-table-column>
                        <el-table-column property="fcertificationName" label="" align="center">
                        </el-table-column>
                        <!--<el-table-column property="fbalance" label="" align="center" >
                        </el-table-column>
                        <el-table-column property="fcredit" label="" align="center" >
                        </el-table-column>-->
                        <el-table-column label="" align="center" >
                            <el-table-column  label="" align="center" width="80">
                                <template scope="scope">
                                    <span class="userManage-seeBtn" @click="seeInfo(scope.$index,scope.row)"></span>
                                </template>
                            </el-table-column>
                            <el-table-column  label="/" width="100" align="center">
                                <template scope="scope">
                                    <span class="userManage-enableBtn" v-show="scope.row.feffect == 0" @click="accountDisable(scope.$index,scope.row)"></span>
                                    <span class="userManage-disableBtn" v-show="scope.row.feffect == 1" @click="accountEnable(scope.$index,scope.row)"></span>
                                </template>
                            </el-table-column>
                            <el-table-column  label="" align="center"  width="80">
                                <template scope="scope">
                                    <span class="userManage-resetBtn" @click="resetPwd(scope.$index,scope.row)"></span>
                                </template>
                            </el-table-column>
                        </el-table-column>
                    </el-table>
                    <!--end-->
                </div>
            </div>
            <!--start-->
            <div class="userManage-resetWarp" v-show="resetWarpShow">
                <h3>
                    
                </h3>
                <div class="userManage-resetBtnWarp">
                    <div>
                        <button v-if="iseffect" :class="{'userManage-disabledBtn':!reset.resetLoginBtn}" @click="resetLoginPwd()" :disabled="!reset.resetLoginBtn">{{reset.resetLoginBtn?reset.loginContent:reset.loginCountDown+reset.seconds}}</button>
                        <button v-if="!iseffect" class="userManage-disabledBtn" ></button>
                    </div>
                    <div>
                        <button v-if="iseffect" :class="{'userManage-disabledBtn':!reset.resetPayBtn}" @click="resetPayPwd()" :disabled="!reset.resetPayBtn">{{reset.resetPayBtn?reset.payContent:reset.payCountDown+reset.seconds}}</button>
                        <button v-if="!iseffect" class="userManage-disabledBtn" ></button>
                    </div>
                </div>
                <div class="userManage-resetWarp-closeBtn" @click="closeReset"></div>
            </div>
            <!--end-->
            <!--start-->
            <userManageSee :userbaseinfo="userBaseInfo" :otherInfo="otherInfo":seeData="seeData" :contentShow="contentType" :infoShow="infoShow" @closeInfo="closeInfo()" :topdata="topData" ></userManageSee>
            <!--end-->
        </div>
        <div class="userManage-pagination">
            <el-pagination layout="total, sizes,prev, pager, next, jumper" :total="total"  :small="false" @current-change="handleCurrentChange" :current-page="userManageQuery.pageNo + 1"
                           :page-size="userManageQuery.pageSize"
                           @size-change="handleSizeChange"
                           :page-sizes="[15, 20, 30, 100]">
            </el-pagination>
        </div>
        <div class="userManage-masker" v-show="masker"></div>
        <!--start-->
        <userManageWindow :info="infoMsg" @windowClose="windowClose()" :windowShow="windowShow" @close="windowShow = false" @sure="sure()"></userManageWindow>
        <!--end-->
    </div>
</template>

<script>
    import userManageWindow from  './userManage-window.vue'
    import userManageSee from './userManage-see.vue'
    import tHeader from '../../../public/tHeader.vue'
    import {VERSION} from '@/util/constant.js'
    export default {
        created(){
            // this.masker = true;
            /*this.tableData = this.paging(0,this.size);*/
            this.listInit(true);
        },
        components:{
            tHeader,
            userManageWindow,
            userManageSee
        },
        data() {
            return {
                /**/
                userManageQuery: {
                    mainUser:{
                        feffect: '',
                        fcertification: '',
                        fphone:''
                    },
                    pageSize:15,
                    pageNo:0,
                    version:VERSION
                },
                total:0,
                queryJson:{

                },
                /**/
                accountStateOptions: [
                    {
                        value:'0',
                        label:''
                    },{
                        value:'1',
                        label:''
                    }
                ],
                /*
                 
                 * */
                authentStateOptions: [
                    {
                        value:0,
                        label:'/'
                    },{
                        value:11,
                        label:''
                    },{
                        value:21,
                        label:''
                    }
                ],

                /*
                 
                 */
                tableData: [],
                //
                contentType:-999,
                //id
                currentId:-999,
                currentType:-999,
                currentIndex:-999,
                //
                //
                resetWarpShow:false,
                //
                currentFphone:'', //
                telError:false,
                telErrorTimer:null,
                //
                windowShow:false,
                infoMsg:'',
                //
                reset:{
                    loginContent:'',
                    payContent:'',
                    loginCountDown:300,
                    payCountDown:300,
                    seconds:'s',
                    resetLoginBtn:true,
                    resetPayBtn:true,
                    loginTimer:null,
                    payTimer:null
                },
                //
                masker:false,
                //
                infoShow:false,
                //
                seeData:{}, // 
                otherInfo:{}, // 
                topData:{}, //
                userBaseInfo:{}, //
                iseffect:true //
            }
        },
        methods:{
            /*
             
             20177511:22:49
             */
            query(){
                if(this.userManageQuery.mainUser.fphone.length > 11){
                    this.telError = true;
                    clearInterval(this.telErrorTimer);
                    var _this  = this;
                    this.telErrorTimer = setTimeout(function(){
                        _this.telError = false;
                    },3000)
                    return;
                }else{
                    this.telError = false;
                }
                this.userManageQuery.pageNo = 0;
                this.listInit(true);
            },
            /*
             
             201762615:50:22
             */
            tableRowClassName(row, index){
                if (index%2 == 1) {
                    return 'gray-row';
                }
                return '';
            },
            /*
             
             201762214:01:03
             */
            resetPwd(index,row){
                this.currentId = row.fuserId;
                this.currentFphone = row.fphone;
                this.resetWarpShow = true;
                this.masker = true;
                if(row.feffect == 1){
                    this.iseffect = true;
                }else{
                    this.iseffect = false;
                }
            },
            /*
             
             201762214:00:17
             */
            closeReset(){
                this.resetWarpShow = false;
                this.masker = false;
                /*clearInterval(this.reset.loginTimer);
                 this.reset.resetLoginBtn = true;
                 this.reset.loginCountDown =300;
                 clearInterval(this.reset.payTimer);
                 this.reset.payCountDown =300;
                 this.reset.resetPayBtn = true;*/
            },
            /*
             
             201762215:43:30
             */
            closeInfo(){
                this.infoShow = false;
                this.masker = false;
                this.seeData  = {};
                this.userBaseInfo = {};
                this.otherInfo  = {};
                this.topData = {};
            },
            /*
             
             201762214:00:17
             */
            resetLoginPwd(){
                this.reset.resetLoginBtn = false;
                var _this = this;
                this.reset.loginTimer=setInterval(function(){
                    if(_this.reset.loginCountDown <= 1){
                        clearInterval(_this.reset.loginTimer);
                        _this.reset.resetLoginBtn = true;
                        _this.reset.loginCountDown =300;
                    }
                    _this.reset.loginCountDown--;
                },1000)
                this.$http.post('/outerUser/remakePasswd.do',{
                    version:VERSION, // 
                    passwordType:'password', // 
                    userId:this.currentId, // id
                    fphone:this.currentFphone // 
                }).then(res => {
                    var res = res.data;
                    if(res.success){
                        _this.msg('','success');
                    }else{
                        clearInterval(this.reset.loginTimer);
                        this.reset.resetLoginBtn = true;
                        this.reset.loginCountDown =300;
                        _this.msg('','error');
                    }
                })
            },
            /*
             
             201762214:10:41
             */
            resetPayPwd(){
                this.reset.resetPayBtn = false;
                var _this = this;
                this.reset.payTimer=setInterval(function(){
                    _this.reset.payCountDown--;
                    if(_this.reset.payCountDown <= 1){
                        clearInterval(_this.reset.payTimer);
                        _this.reset.payCountDown = 300;
                        _this.reset.resetPayBtn = true;
                    }
                },1000)
                this.$http.post('/outerUser/remakePasswd.do',{
                    version:VERSION, // 
                    passwordType:'payPassword', // 
                    userId:this.currentId, // id
                    fphone:this.currentFphone // 
                }).then(res => {
                    var res = res.data;
                    if(res.success){
                        _this.msg('','success');
                    }else{
                        _this.msg('','error');
                        clearInterval(this.reset.payTimer);
                        this.reset.payCountDown =300;
                        this.reset.resetPayBtn = true;
                    }
                })
            },
            /*
             
             201762215:45:58
             */
            seeInfo(index,row){
                this.$http.post('/outerUser/getOuterDetailInfo.do',{
                    userId:row.fuserId, // id
                    version:VERSION //
                }).then(res => {
                    res = res.data;
                    if(res.success){
                        this.infoShow = true;
                        this.masker = true;
                        // 
                        if(JSON.stringify(res.data) != '{}'){
                            if(res.data.certificationInfo){
                                // 
                              this.seeData = res.data.certificationInfo;
                            }
                            if(res.data.userBaseInfo){
                                // 
                                this.userBaseInfo = res.data.userBaseInfo;
                            }
                            if(res.data.otherInfo){
                                this.otherInfo = res.data.otherInfo; // 
                            }
                        }
                        this.topData = row;
                        if(row.fcertificationType == 0 ||row.fcertificationType == 10 ||
                            row.fcertificationType == 12 || row.fcertificationType == 20 || row.fcertificationType == 22){
                            this.contentType = 3; //<!---->
                        }else if(row.fcertificationType == 11 || row.fcertificationType == 23 || row.fcertificationType == 25 ){
                            this.contentType = 1; //<!-- -->
                        }else if(row.fcertificationType == 21 || row.fcertificationType == 26 || row.fcertificationType == 27){
                            this.contentType = 2;  //<!-- -->
                        }
                    }else{
                        this.msg('','error')
                    }
                })
            },
            telInput(){
                this.userManageQuery.mainUser.fphone = this.userManageQuery.mainUser.fphone.replace(/\D/g,'');
            },
            /**/
            telBlur(){
                if(this.userManageQuery.mainUser.fphone.length > 11){
                    this.telError = true;
                    clearInterval(this.telErrorTimer);
                    var _this  = this;
                    this.telErrorTimer = setTimeout(function(){
                        _this.telError = false;
                    },3000)
                }else{
                    this.telError = false;
                }
            },
            /**/
            /*
             * index
             * row 
             * return
             * */
            accountInit(index,row){
                this.windowShow = true;
                this.currentId = row.fuserId;
                this.currentIndex = index;
                this.currentType = row.feffect;
                this.masker = true;
            },
            accountDisable(index,row){
                this.infoMsg='';
                this.accountInit(index,row);
            },
            /**/
            accountEnable(index,row){
                this.infoMsg='';
                this.accountInit(index,row);
            },
            // 
            sure(){
                // 
                this.$http.post(this.currentType == 0 ?'/outerUser/enableAccount.do':'/outerUser/disableAccount.do',{
                    version:VERSION,userId:this.currentId
                }).then(res => {
                        var res = res.data;
                        if(res.success){
                            if(this.tableData[this.currentIndex].feffect){
                                this.tableData[this.currentIndex].feffect = 0;
                                this.msg('','success');
                            }else{
                                this.tableData[this.currentIndex].feffect = 1;
                                this.msg('','success');
                            }
                            this.windowShow = false;
                            this.masker = false;
                        }else{
                            this.msg('','error');
                        }
                    })
            },
            //
            windowClose(){
                this.masker = false;
                this.windowShow = false;
            },
            /*
             * msg
             * type iconclass
             * return
             * */
            msg(msg,type){
                this.$message({
                    message: msg,
                    type: type
                });
            },
            //
            handleCurrentChange(pageNum){
                //  1
                this.userManageQuery.pageNo = pageNum - 1;
                this.listInit(false);
            },
            handleSizeChange(size){
                this.userManageQuery.pageNo = 0;
                this.userManageQuery.pageSize = size;
                this.listInit(false);
            },
            listInit(bFalg){
                //  - 1  fuck
                if(bFalg){
                    this.queryJson = this.deepCopy(this.userManageQuery);
                }
                if(this.queryJson.mainUser.feffect === '' ){
                    this.queryJson.mainUser.feffect = -1;
                }else{
                    this.queryJson.mainUser.feffect = parseFloat(this.queryJson.mainUser.feffect);
                }
                if(this.queryJson.mainUser.fcertification === '' ){
                    this.queryJson.mainUser.fcertification = -1;
                }else{
                    this.queryJson.mainUser.fcertification = parseFloat(this.queryJson.mainUser.fcertification);
                }
                this.queryJson.pageNo = this.userManageQuery.pageNo;
                this.queryJson.pageSize = this.userManageQuery.pageSize;
                this.$http.post('/outerUser/loadLst.do',this.queryJson).then(res => {
                    var res = res.data;
                    if(res.success){
                        if(JSON.stringify(res.data) != '{}'){
                            this.tableData = res.data.result;
                            this.total = res.data.total;
                        }
                    }else{
                        this.msg('','error');
                    }
                })
            },
            //copy
            deepCopy(obj){
                if(typeof obj != 'object'){
                    return obj;
                }
                var newobj = {};
                for( var attr in obj){
                    newobj[attr] = this.deepCopy(obj[attr]);
                }
                return newobj;
            }
        }
    }
</script>

<style>
    /**/
    .userManage-warp-padding{
        padding:30px 30px 20px 30px;
    }
    .useManage-radius{
        padding-bottom: 30px;
        background: #fff;
        border-radius:8px;
        overflow: hidden;
    }
    .userManage-tableQuery-padding{
        padding: 0 10px;
    }
    #userManage-warp{
        width: 100%;
        height:100%;
        padding-bottom:50px;
    }
    #userManage-warp .gray-row {
        background: #f4f5fb;
    }
    /*userManage */
    .userManage-Query {
        padding: 20px 0;
    }
    .userManage-Query li {
        float: left;
    }
    .userManage-Query .el-select {
        width: 150px;
    }
    .userManage-Query .el-input .el-input__inner,
    .userManage-Query .userManage-input .el-input__inner {
        height: 32px;
        font-size: 12px;
    }
    .telError input{
        border-color: red;
    }
    .telError{
        position: relative;
    }
    .userManage-Query .telError span{
        display: block;
        position: absolute;
        left: 70px;
        bottom:-22px;
        color: red;
    }
    .userManage-Query .userManage-input {
        width: 280px;
    }
    .userManage-input span{
        display: none;
    }
    .userManage-tel{
        border-radius: 4px;
        border: 1px solid #bfcbd9;
        box-sizing: border-box;
        color: #1f2d3d;
        font-size: inherit;
        height: 32px;
        line-height: 1;
        padding: 3px 10px;
    }
    .userManage-Query li:nth-child(1),
    .userManage-Query li:nth-child(2)
    {
        width: 240px;
    }
    .userManage-Query .userManage-input .el-input {
        width: 200px;
    }
    /**/
    .el-table__body-wrapper,
    .el-table__header-wrapper {
        font-size: 12px;
    }
    #userManage-warp .userManage-seeBtn,
    #userManage-warp .userManage-resetBtn
    {
        color:#38adff;
    }
    #userManage-warp .el-table_1_column_10_column_11 .cell,
    #userManage-warp .el-table_1_column_10_column_12 .cell,
    #userManage-warp .el-table_1_column_10_column_13 .cell{
        font-weight: normal;
        color:#333;
    }
    #userManage-warp .userManage-disableBtn{
        color: red;
    }
    #userManage-warp .userManage-enableBtn{
        color: green;
    }
    #userManage-warp .userManage-seeBtn,
    #userManage-warp .userManage-disableBtn,
    #userManage-warp .userManage-resetBtn,
    #userManage-warp .userManage-enableBtn
    {
        cursor:pointer;
    }
    /**/
    .userManage-resetWarp{
        width:400px;
        height: 180px;
        padding: 20px 0 0 20px;
        background: #fff;
        position: fixed;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        z-index: 6010;
        border-radius: 4px;
    }
    .userManage-masker{
        position: fixed;
        left:0;
        top:0;
        background: rgba(0, 0, 0, 0.5);
        width: 100%;
        height: 100%;
        z-index: 6000;
    }
    .userManage-resetWarp h3{
        font-size: 16px;
        font-weight: bold;
        margin-bottom: 28px;
    }
    .userManage-resetWarp-closeBtn{
        position:absolute;
        width:26px;
        height: 26px;
        background: url(../../../../../static/images/publicImages/close.png) no-repeat 0 0;
        border-radius: 50%;
        right:-8px;
        top:-8px;
        cursor:pointer;
    }
    .userManage-resetWarp .userManage-resetBtnWarp div{
        padding-left: 102px;
    }
    .userManage-resetWarp button{
        width:80px;
        height: 34px;
        color:#fff;
        border: 0;
        background: #38adff;
        border-radius: 4px;
        margin:0 0 20px 20px;
        cursor:pointer;
    }
    .userManage-resetWarp .userManage-disabledBtn{
        background:#eef1f6;
        color:#afb1b5;
        outline: none;
    }
    /*
        
    */
    .userManage-pagination{
        text-align: center;
    }
    .userManage-pagination .el-pagination button,
    .userManage-pagination .el-pagination li{
        width:38px;
        height:38px;
        margin-right: 5px;
        line-height: 38px;
    }
    .userManage-pagination .el-pager li.btn-quicknext,
    .userManage-pagination .el-pager li.btn-quickprev
    {
        line-height: 38px;
    }
    .userManage-pagination .el-pagination .el-select .el-input input,
    .userManage-pagination .el-pagination span{
        height: 38px;
        line-height: 38px;
    }
    .userManage-pagination .el-pagination__editor{
        line-height: 26px;
        width: 38px;
        height: 36px;
    }

    #userManage-warp .el-table .cell, .el-table th>div{
        padding-left: 0px;
        padding-right: 0px;
    }
</style>



