<template>
    <div>
        <el-table border ref="singleTable" :data="tableData" highlight-current-row  style="width: 100%"   height="680">
            <el-table-column  label="" width="70" align="center">
                <template scope="scope">
                    <span>{{scope.$index + size*(currentPage - 1)+1}}</span>
                </template>
            </el-table-column>
            <el-table-column property="partnerAreaName" label="" align="center" width="150">
