            <el-table-column property="fphone" label="" align="center" width="180">
            </el-table-column>
            <el-table-column property="fcustName" label="" align="center" width="200">
            </el-table-column>
            <el-table-column property="ftypename" label="" align="center" width="150">
                <!--<template scope="scope">
                    <span v-if="scope.row.fintegraljournaltype == 1"> </span>
                    <span v-if="scope.row.fintegraljournaltype == 2"> </span>
                    <span v-if="scope.row.fintegraljournaltype == 3"> </span>
                    <span v-if="scope.row.fintegraljournaltype == 4"> </span>
                    <span v-if="scope.row.fintegraljournaltype == 5"></span>
                    <span v-if="scope.row.fintegraljournaltype == 6"></span>
                    <span v-if="scope.row.fintegraljournaltype == 7"></span>
                    <span v-if="scope.row.fintegraljournaltype == 8"></span>
                    <span v-if="scope.row.fintegraljournaltype == 9"></span>
                    <span v-if="scope.row.fintegraljournaltype == 10"></span>
                    <span v-if="scope.row.fintegraljournaltype == 11"></span>
                    <span v-if="scope.row.fintegraljournaltype == 12"></span>
                </template>-->
            </el-table-column>
            <el-table-column property="fintegral" label="" align="center" width="120">
            </el-table-column>
            <el-table-column property="forderid" label="" align="center" width="180">
            </el-table-column>
            <el-table-column property="createrName" label="" align="center" width="120">
            </el-table-column>
            <el-table-column  property="fcreattimestr"  label="" align="center" width="150">
            </el-table-column>
            <el-table-column property="fremark" label="" align="center" width="250">
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
    export default{
        props:['tableData','currentPage','size'],
        data(){
            return{

            }
        },
        mounted(){

        },
        methods: {

        },
        filters:{
            getTime(value){
                if(value == '' || value == undefined){
                    return '';
                }
                var time = new Date(value)
                , year = time.getFullYear()
                , month = time.getMonth() + 1
                , date = time.getDate()
                , hour = time.getHours() < 10 ? '0' + time.getHours() : time.getHours()
                , min = time.getMinutes() < 10 ? '0' + time.getMinutes() : time.getMinutes()
                , sec = time.getSeconds() < 10 ? '0' + time.getSeconds() : time.getSeconds();
                return `${year}-${month}-${date} ${hour}:${min}:${sec}`
            }
        }
    }
</script>
