<template>
    <div class='commodity-table'>
        <div class='commodity-table-top'>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
        </div>
        <div class='commodity-table-body' v-for='(item, index) in dataList' :key='item.orderId'>
            <p class='commodity-list-top'>
                <span>{{item.orderDate}}</span>
                <span>:</span>
                <span >{{item.orderId}}</span>
                <span v-show="item.dealStatus != '0' ? true :false">{{item.updateTime}}</span>
                <span v-show="item.dealStatus != '0' ? true :false">{{item.pUserName}}</span>
                <span>{{typeAry[item.dealStatus]}}</span>
            </p>
            <div class='commodity-table-list'>
                <div class='commodity-table-number'>
                    {{item.number}}
                </div>
                <div class='commodity-table-first'>
                    <img :src='item.url'>
                    <p :title='item.goodName'>{{item.goodName}}</p>
                    <!-- <p :title='item.spec' class='commodity-table-specName' v-if="item.spec == ':' ? false : true">:</p> -->
                    <p :title='item.spec' class='commodity-table-spec'>{{item.spec == ':' ? '' : item.spec}}</p>
                </div>
                <div class='commodity-table-span-two commodity-table-center' >
                    <p :title='item.integral' :class="[item.payType != '2' ? 'commodity-table-num maxlength-hidden' :'commodity-table-integral maxlength-hidden']" v-if="item.payType == '0' || item.payType == '2' ? true : false">:{{item.integral}}</p>
                    <p :title='item.balance' :class="[item.payType != '2' ? 'commodity-table-num maxlength-hidden' :'commodity-table-balance maxlength-hidden']" v-if="item.payType == '1' || item.payType == '2' ? true : false">:{{item.balance}}</p>
                </div>
                <div class='commodity-table-span commodity-table-center'>
                    <p class='commodity-table-num commodity-table-width' :title='item.amount'>{{item.amount}}</p>
                </div>
                <div class='commodity-table-span commodity-table-center'>
                    <p class='commodity-table-num'>{{shipType[item.shipType]}}</p>
                </div>
                <div class='commodity-table-span commodity-table-personal'>
                    <p class='commodity-table-phone':title='item.phone'>{{item.phone}}</p>
                    <p class='commodity-table-name' :title='item.certificationName'>{{item.certificationName}}</p>
                </div>
                <div class='commodity-table-span commodity-table-personal' v-if='shipTypes == 3 || shipTypes == 5 || item.shipType == 3 || item.shipType == 5 ? true : false'></div>
                <div class='commodity-table-span commodity-table-personal' v-if='shipTypes == 3 || shipTypes == 5 || item.shipType == 3 || item.shipType == 5 ? false : true'>
                    <p class='commodity-table-p-one' :title='item.consignee' >{{item.consignee}}</p>
                    <p class='commodity-table-p-two' :title='item.contactWay'>{{item.contactWay}}</p>
                    <p class='commodity-table-p-three maxlength-hidden' :title='item.codeProvince + item.addressDetail'>{{item.codeProvince + item.addressDetail}}</p>
                    <p class='commodity-table-p-four maxlength-hidden'  :title='item.addressRemark'>{{item.addressRemark !='' ? "(" + item.addressRemark + ")" : item.addressRemark}}</p>
                </div>
                <div class='commodity-table-span commodity-table-center'>
                    <p class='commodity-table-num'>{{orderType[item.status]}}</p>
                </div>
                <div class='commodity-table-span commodity-table-time' v-if="item.status == '4' && item.shipType != '5' && item.shipType != '3' && item.shipType != '4'? true : false">
                    <p class='commodity-table-sign'>:{{item.receiver}}</p>
                    <p class='commodity-table-signTime maxlength-hidden':title='item.receiptTime'>:{{item.receiptTime}}</p>
                </div>
                <div class='commodity-table-span commodity-table-time commodity-table-btn commodity-table-color' v-if="shipTypes != '0' && item.status == '1' && item.shipType != '4'? true : false">
                    <p @click='deliver(index)'></p>
                </div>
                <div class='commodity-table-span commodity-table-time commodity-table-btn commodity-table-color' v-if="item.status == '2' && item.shipType == '5' && shipTypes != '0' || item.status == '2' && item.shipType == '4' && shipTypes != '0' ? true : false">
                    <p @click='commonFun(index)'></p>
                </div>
                <div class='commodity-table-span commodity-table-time commodity-table-btn commodity-table-color' v-if="shipTypes != '0' && item.status == '2' && item.shipType == '3'? true : false">
                    <p @click='commonFun(index)'></p>
                </div>
                <div class='commodity-table-span commodity-table-time commodity-table-btn commodity-table-color' v-if="item.status == '3' ? true : false">
                    <p @click='sign(index)'></p>
                </div>
                <div class='commodity-table-span commodity-table-time commodity-table-btn ' v-if="shipTypes == '0' && item.status == '1' || shipTypes == '0' && item.status == '2' ? true : false">
                    <p @click='cancelOrder(index)'></p>
                </div>
                <div class='commodity-table-span commodity-table-time' v-if="item.status == '5' ? true : false">
                    <p class='commodity-table-people' :title='item.canceler'>:{{item.canceler}}</p>
                    <p class='commodity-table-calctime maxlength-hidden' :title='item.cancelTime'>:{{item.cancelTime}}</p>
                    <p class='commodity-table-calce maxlength-hidden' :title='item.cancelReasons'>:{{item.cancelReasons}}</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import {VERSION, IMGPATH} from '@/util/constant.js'
export default {
    props: ['listData','shipTypes'],
    data() {
        return {
           dataList:this.listData,
           typeAry:{
               '0':'',
               '1':''
           },
           shipType:{
               '1':'', 
               '2':'',
               '3':'',
               '4':'',
               '5':''
           },
           orderType:{
                '1':'',
                '2':'',
                '3':'',
                '4':'',
                '5':''
           }
        }
    },
    methods: {
         //.
         commonFun(index){
             this.$emit('commonFun', index)
         },
         //
         sign(index){
             this.$emit('sign', index)
         },
         //
         deliver(index){
             this.$emit('deliver', index) 
         },
         //
         cancelOrder(index){
            this.$emit('cancelOrder', index); 
         }
    },
    watch:{
       listData(arry){
           this.dataList = arry;
           for(let i=0; i<this.dataList.length; i++){
               this.dataList[i].url = IMGPATH + this.dataList[i].url;
           }
       }
    }
}
</script>


