<template>
   <div class="partnerManage">
        <tHeader></tHeader>
        <div class='common-bg'>
          <div class='address-table-position'>
            <div class='address-add-btn'>
                <address-select
                textName=':'
                :region='regionAry'
                @getSelect='getSelect'
                :regionName='defaultName'
                ></address-select>
                <el-button type="primary" @click='showCityProto'>
                    <i class='carous-add'></i>
                    <i class='carous-adds'></i>
                    
                </el-button>
            </div>
            <div class="prchase-list">
                <el-table :data="getlistInit" border>
                    <el-table-column prop="number" label="" width='100'>
                    </el-table-column>
                    <el-table-column prop="name" label="" >
                    </el-table-column>
                    <el-table-column prop="fcityname" label="" >
                    </el-table-column>
                    <el-table-column  label="" >
                        <template scope="scope">
                            <el-button @click.native.prevent="deleteRowTable(scope.$index, getlistInit)" type="text" size="small">
                                
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class='address-page'>
                    <pageing v-if='listLength > 10'
                        @changePage="changePage"
                        @changSize='changSize'
                        :showPageArry="listLength > 15 ? [10,15,20] : [10,15]"
                        :showPagelist="showPagelist"
                        :listLength="listLength"
                        :currentPage='currentPage'
                       >
                    </pageing>
                </div>
            </div>
          </div>
        </div>
        <div class='address-select'>
            <el-dialog title="" :visible.sync="addAddress" :show-close='false'  @open='openDialog' @close='closeDialog' :modal-append-to-body='false'>
                <img src='../../../../static/images/publicImages/close.png' class='address-propt-close' @click='addAddress = false'>
                <div class='address-select-list' @click='showCity'>
                    <span>:</span>
                    <el-input
                    placeholder="----"
                    icon="caret-bottom"
                    v-model="cityName"
                    :click="showCity"
                    :readonly='true'>
                    </el-input>
                    <div class='address-notice' v-show='saveError'>
                        <img src='../../../../static/images/notice.png' class='address-notice-icon'>
                        
                    </div>
                </div>
                <div class='address-city-city address-city-color' v-show='isShowCity'>
                    <el-tabs v-model="activeName">
                        <el-tab-pane :label="proviceName" name="first">
                            <div class='address-city-div' v-for='(provice, index) in letterAry' :key='provice.letter' @mouseenter='getIndex(index)'>
                                <span class='address-city-span'>{{provice.letter}}</span>
                                <div v-for="(city, index) in cityArry[index]" class='address-select-city':key='city.name' @click='selectPrvioce(index)'>
                                    <span>{{city.name}}</span>
                                </div>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="" name="second" :disabled="cityName === '' ? true : false">
                            <div v-for="(city, index) in cityAry" :key='city.fcityname' class='address-select-city address-city-postion'>
                                    <span :title='city.fcityname' @click='selectCity(index)'>{{city.fcityname}}</span>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </div>
                <div slot="footer" class="dialog-footer">
                   <el-button @click="addAddress = false"> </el-button>
                   <el-button type="primary" @click='saveCity'> </el-button>
                </div>
            </el-dialog>
        </div>
        <propt
            :dialogVisible = 'isDel'
            text=''
            :needCancel = 'true'
            @closeVisible='isDel = false'
            @sure='deleteRow'
        ></propt>
  </div>
</template>
<script>
import tHeader from '../../public/tHeader'
import pageing from '../../public/paging'
import addressSelect from '@/components/public/select'
import propt from '../../public/propt'
import {clippingProvice, clippingCity} from '@/util/common.js'
export default {
    created(){
        //
        this.initList();
        //
        this.getProvice()
    },
	data () {
		return {
            isDel:false,
            proviceName:'',
            defaultName:'',             //
            currentPage:1,              //
            showPagelist: 10,           //
            listLength:0,               //
            addAddress:false,           //
            isShowCity:false,
            activeName:'first',         //tab
            cityName:'',                //
            selectIndex:'',             //index
            letterAry:[                 //
               {letter:'A-G'},
               {letter:'H-K'},
               {letter:'L- S'},
               {letter:'T- Z'},
            ],
            regionAry:[],               //
            getlistInit:[],             //
            generalList:[],             //
            itemsArray:{                //
              arry0:[
                  {name:'',id:34},
                  {name:'',id:11},
                  {name:'',id:50},
                  {name:'',id:35},
                  {name:'',id:62},
                  {name:'',id:44},
                  {name:'',id:45},
                  {name:'',id:52}
              ],
              arry1:[
                  {name:'',id:46},
                  {name:'',id:13},
                  {name:'',id:41},
                  {name:'',id:42},
                  {name:'',id:43},
                  {name:'',id:36},
                  {name:'',id:32},
                  {name:'',id:22},
                  {name:'',id:23}
              ],
              arry2:[
                  {name:'',id:21},
                  {name:'',id:64},
                  {name:'',id:63},
                  {name:'',id:37},
                  {name:'',id:31},
                  {name:'',id:14},
                  {name:'',id:61},
                  {name:'',id:51},
                  {name:'',id:15}
              ],
              arry3:[
                  {name:'',id:12},
                  {name:'',id:65},
                  {name:'',id:54},
                  {name:'',id:53},
                  {name:'',id:33}
              ]
            },
            pagingAry:[],               //
            cityAry:[],                 //
            getCityId:'',               //ID
            saveError:false,            //
            selectCityId:'123',         //ID
            selectProviceId:''          //ID
		}
	},
	methods:{
        //
        getSelect(name){
            var getId = {}
            this.defaultName = name;
            for(let i=0; i<this.regionAry.length; i++){
                if(this.regionAry[i].regionName === name){
                    getId.code = this.regionAry[i].fprovincecode;
                    this.selectCityId = getId.code;
                }
            }
            if(getId.code === '123'){
                this.pagingAry = this.generalList;
                this.pageSort();
                this.refreshPage();
                this.currentPage = 1;
                return
            }
            this.getlistInit = [];
            this.pagingAry = [];
            //
            for(let i=0; i<this.generalList.length; i++){
                if(getId.code == this.generalList[i].fprovincecode){
                    this.pagingAry.push(this.generalList[i])
                }
            }
            this.pageSort();
            this.refreshPage();
        },
        //
        changePage(page){
            this.currentPage = page;
            var initNum = (page - 1) * this.showPagelist;
            this.getlistInit = [];
            for (let i = initNum; i < this.pagingAry .length; i++) {
                if (i < initNum + this.showPagelist) {
                    this.getlistInit.push(this.pagingAry [i]);
                }
            }
        },
        //
        changSize(num){
           this.getlistInit = [];
           this.showPagelist = num;
           if(this.currentPage > 1){
               var currentNum = this.currentPage * this.showPagelist - this.showPagelist
               for (let i = currentNum; i < this.pagingAry.length; i++) {
                   this.pagingAry[i].number = i + 1;
                   if (i - this.currentPage * this.showPagelist < this.showPagelist) {
                       this.getlistInit.push(this.pagingAry[i])
                       if (this.getlistInit.length === this.showPagelist) {
                           return
                       }
                   }
               }
           }else{
               this.refreshPage();
           }

        },
        //index
        deleteRowTable(index, dataAry){
            this.isDel = true;
            this.getCityId = dataAry[index].fcitycode;
        },
        //
        deleteRow(){
            this.$http.post('/address/updateAddressByCity.do', { code: this.getCityId, status:'0' }).then(res => {
                if(res.data.success) {
                    this.isDel = false;
                    var number
                    //
                    for (let i = 0; i < this.generalList.length; i++) {
                        if (this.getCityId === this.generalList[i].fcitycode) {
                            number = this.generalList[i].number;
                            this.generalList.splice(i, 1);
                        }
                    }
                    //
                    for(let i = 0; i < this.pagingAry.length; i++){
                       if(this.getCityId === this.pagingAry[i].fcitycode){
                           this.pagingAry.splice(i, 1);
                       }
                    }
                    this.listLength = this.pagingAry.length;
                    //0
                    if(this.getlistInit.length - 1 === 0 && this.generalList.length !== 0){
                        this.pagingAry = this.generalList;
                        this.pageSort();
                        this.refreshPage();
                        this.defaultName = '';
                        this.getProvice();
                        return
                    }
                    this.getlistInit = [];
                    var currentNum = this.currentPage * this.showPagelist - this.showPagelist;
                    //
                    for(let i=currentNum; i<this.pagingAry.length; i++){
                        this.pagingAry[i].number = i + 1;
                        if(i - this.showPagelist >= 0 ){
                            if(i - this.currentPage * this.showPagelist < this.showPagelist){
                               this.getlistInit.push(this.pagingAry[i]);
                               if(this.getlistInit.length === this.showPagelist){
                                  return
                               }
                            }
                        }else{
                           if(i < this.showPagelist){
                              this.getlistInit.push(this.pagingAry[i]);
                              if(this.getlistInit.length === this.showPagelist){
                                  return
                              }
                           }
                        }
                    }
                    this.getProvice();
                    this.$message({
                        message: '',
                        type: 'success',
                        duration: 1500
                    });
                }else{
                    this.$message({
                        message: '',
                        type: 'error',
                        duration: 1500
                    });
                }
            }).catch(function (error) {
                console.log(error);
            });

        },
        //
        showCity(){
           if(this.isShowCity){
               this.isShowCity = false;
           }else{
               this.isShowCity = true;
               this.activeName = 'first';
           }
        },
        //
        showCityProto(){
           this.cityName = '';
           this.addAddress = true;
           this.proviceName = '';
           this.isShowCity = false;
           this.saveError = false;
        },
        //
        selectPrvioce(index){
           let arry = 'arry' + this.selectIndex;
           let getName =  this.itemsArray[arry][index].name;
           this.cityName = '';
           this.cityName = getName + '/';
           var getId = {};
           getId.code = this.itemsArray[arry][index].id;
           this.selectProviceId = getId.code;
           getId.status = '0';
           this.$http.post('/address/findCity.do', getId).then(res => {
                if(res.data.success) {
                    this.cityAry = res.data.data;
                    for(var i=0; i<this.cityAry.length; i++){
                       this.cityAry[i].fcityname = clippingCity(this.cityAry[i].fcityname)
                    }
                }
            })
           setTimeout(()=>{
              this.activeName = 'second'
           },200)
           this.proviceName = getName
        },
        //
        selectCity(index){
           let getnumber = this.cityName.indexOf('/');
           this.cityName = this.cityName.slice(0, getnumber + 1);
           this.cityName = this.cityName + this.cityAry[index].fcityname;
           this.getCityId = this.cityAry[index].fcitycode;
           this.isShowCity = false;
           this.saveError = false;
        },
        //index
        getIndex(index){
           this.selectIndex = index;
        },
        //
        saveCity(){
            if(this.cityName === ''){
                this.saveError = true;
                return
            }
            var getProvice = this.cityName.indexOf('/');  //
            var proviceName = this.cityName.slice(0, getProvice);
            var cityName = this.cityName.slice(getProvice + 1);//
            if(cityName === ''){
                this.saveError = true;
                return
            }
            this.addAddress = false;
            let newCity={
                number:0,
                name:proviceName,
                fcityname:cityName,
                fcitycode:this.getCityId,
                fprovincecode:this.selectProviceId
            }
            this.$http.post('/address/updateAddressByCity.do', { code: this.getCityId, status:'1' }).then(res => {
                if(res.data.success) {
                    this.getlistInit = [];
                    for (let i = 0; i < this.generalList.length; i++) {
                        if (this.getCityId === this.generalList[i].fcitycode) {
                            newCity = this.generalList[i];
                            this.generalList.splice(i, 1);
                        }
                    }
                    //
                    this.generalList.unshift(newCity);
                    //
                    this.pagingAry = this.generalList;
                    //
                    this.pageSort();
                    this.refreshPage();
                    this.currentPage = 1;
                    this.defaultName = '';
                    this.getProvice();
                    this.$message({
                        message: '',
                        type: 'success',
                        duration: 2000
                    });
                }else{
                    this.$message({
                        message: '',
                        type: 'error',
                        duration: 2000
                    });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //
        refreshPage(){
            this.getlistInit = [];
            for (let i = 0; i < this.pagingAry.length; i++) {
                if (i < this.showPagelist) {
                    this.getlistInit.push(this.pagingAry[i]);
                }
            }
        },
        //
        initList(){
            this.$http.post('/address/listProvinceAndCity.do',{number:'1'}).then(res => {
                if (res.data.success) {
                    this.generalList = res.data.data
                    this.getRigion();
                }else{
                    console.log('')
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //
        getRigion(){
            for (var i = 0; i < this.generalList.length; i++) {
                this.generalList[i].number = i + 1;
                this.generalList[i].fcityname = this.generalList[i].fcityname.slice(0, this.generalList[i].fcityname.indexOf(''));
                this.generalList[i].fprovincename = this.generalList[i].fprovincename.slice(0, this.generalList[i].fprovincename.indexOf(''));
                this.generalList[i].name = this.generalList[i].fprovincename;
                if (i < this.showPagelist) {
                    this.getlistInit.push(this.generalList[i]);
                }
            }
            this.listLength = this.generalList.length
            this.pagingAry = this.generalList
        },
        //
        getProvice(){
            this.$http.post('/address/findProvince.do ', { code: "0", status: "1" }).then(res => {
                if (res.data.success) {
                    this.regionAry = [];
                    this.regionAry = res.data.data;
                    for (var i = 0; i < this.regionAry.length; i++) {
                        this.regionAry[i].regionName = this.regionAry[i].fprovincename;
                        this.regionAry[i].regionName = clippingProvice(this.regionAry[i].regionName)
                    }
                    this.regionAry.unshift({ regionName: '', fprovincecode: '123' });//
                }else{
                    console.log('')
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //
        pageSort(){
            for(let i=0; i<this.pagingAry.length; i++){
                this.pagingAry[i].number = i + 1
            }
            this.listLength = this.pagingAry.length;
        },
        openDialog(){
            this.$store.commit('openZindexModel');
        },
        closeDialog(){
            this.$store.commit('closeZindexModel');
        }
    },
    components:{
       tHeader,
       pageing,
       addressSelect,
       propt
    },
    computed:{
        //
        cityArry(){
            var cityArry = []
            for(var i=0; i<4; i++){
               cityArry.push(this.itemsArray['arry' + i])
            }
            return cityArry
        }
    }
}
</script>





