<template>
	<div id="order">
		<div class="order__main">
            <tHeader></tHeader>
            <div class="order__main__search">
                <ul class="clearfix">
                    <li>
                        <span style="color: #ff4949;">*</span><regional-choice :parterCode='-1' :isProduce="true" :isDisableds="isDisableds" :defaultRegion="defaultRegion" :defaultPlease="defaultPlease" @getRegionProvice="getRegion" @getPleaseProvice="getPlease"></regional-choice>
                    </li>
                    <li style="margin-left: 25px;">
                        <span style="color: #ff4949;">*</span>&nbsp;&nbsp;&nbsp;&nbsp;:
                        <el-select v-model="partnerIndex" :disabled="isDisableds" @change="handleUnlock(partnerIndex)" placeholder="" class="media-partner-width">
                            <el-option
                                v-for="(item, index) in partnerTable"
                                :key="index"
                                :label="item.oname"
                                :value="index">
                            </el-option>
                        </el-select>
                    </li>
                </ul>
                <ul class="clearfix">
					<li class="dayTime">
						&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; :
                        <button class="dayBtn" :style="isToday ? actived : ''" @click="doneToday"></button>
                        <button class="dayBtn" :style="isYesterday ? actived : ''" @click="doneYesterday"></button>
					</li>
					<li>
						 :
						<el-select v-model="search.fstatus" placeholder="" clearable style="width: 150px;">
							<el-option
							v-for="item in orderState"
							:key="item.value"
							:label="item.label"
							:value="item.value">
							</el-option>
						</el-select>
					</li>
					<li>
						 :
						<el-input v-model.trim="search.custName" placeholder="" style="width: 150px;"></el-input>
					</li>
				</ul>
				<ul class="clearfix">
					<li style="margin-right: 30px">
						&nbsp;&nbsp;&nbsp; :
						<el-input v-model.trim="search.fchildorderid" placeholder="" style="width: 150px;"></el-input>
					</li>
					<li style="margin-right: 30px">
						 :
						<el-input v-model.trim="search.fgroupgoodname" placeholder="" style="width: 300px;"></el-input>
					</li>
					<li>
						 :
						<el-input v-model.trim="search.fboxlength" placeholder="" style="width: 60px;" @blur="toFloat('fboxlength', search.fboxlength, 1)"></el-input>
						*
						<el-input v-model.trim="search.fboxwidth" placeholder="" style="width: 60px;" @blur="toFloat('fboxwidth', search.fboxwidth, 1)"></el-input>
						*
						<el-input v-model.trim="search.fboxheight" placeholder="" style="width: 60px;" @blur="toFloat('fboxheight',search.fboxheight, 1)"></el-input>
					</li>
				</ul>
				<ul class="clearfix">
					<li>
						 :
						<el-date-picker
						v-model="search.orderStartTime"
						type="datetime"
						placeholder=""
						:picker-options="pickerOptions"
                        :clearable="false"
						class="date-picker-width"
                        @change="changeTime"
                        >
						</el-date-picker>&nbsp;&nbsp;
					</li>
					<li style="margin-right:55px;">
						<el-date-picker
						v-model="search.orderEndTime"
						type="datetime"
						placeholder=""
						:picker-options="pickerOptions"
                        :clearable="false"
						class="date-picker-width"
                        @change="changeTime"
                        >
						</el-date-picker>
					</li>
					<li>
						 :
						<el-date-picker
						v-model="search.deliveryStartTime"
						type="datetime"
						placeholder=""
						:picker-options="pickerOptionsOther"
                        :clearable="false"
						class="date-picker-width"
                        >
						</el-date-picker>&nbsp;&nbsp;
					</li>
					<li style="margin-right:30px;">
						<el-date-picker
						v-model="search.deliveryEndTime"
						type="datetime"
						placeholder=""
						:picker-options="pickerOptionsOther"
                        :clearable="false"
						class="date-picker-width"
                        >
						</el-date-picker>
					</li>
					<li>
						<el-button type="primary" icon="search" @click="searchFilter" style="width: 110px; margin-top: -2px;"></el-button>
					</li>
				</ul>
                <ul>
                    <el-button style="width: 240px;" type="primary" :disabled="btnAuth" @click='allOrderHandout'></el-button>
                    <el-button  type="primary" @click="exportExcel">excel</el-button>
                </ul>
			</div>
			<div class="order__main__table">
                <el-table
                    v-loading="tableLoading"
                    ref="multipleTable"
                    :data="tableData"
                    border
                    tooltip-effect="dark"
                    style="width: 100%"
                    @selection-change="handleSelectionChange"
                    :row-class-name="tableRowClassName">
                    <el-table-column
                        align="center"
                        type="selection"
                        width="59">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        type="index"
                        label=""
                        width="60">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fchildorderid"
                        label=""
                        show-overflow-tooltip
                        width="130">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        label=""
                        show-overflow-tooltip
                        width="153">
                        <template scope="scope">{{ scope.row.fdeliveryString }}</template>
                    </el-table-column>
                    <el-table-column
                        align="center"
                        label=""
                        show-overflow-tooltip
                        width="153">
                        <template scope="scope">{{ scope.row.fcreatetimeString }}</template>
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fstatus"
                        label=""
                        show-overflow-tooltip
                        width="96">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fgroupgoodname"
                        label=""
                        show-overflow-tooltip
                        width="118">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fmaterialname"
                        label=""
                        show-overflow-tooltip
                        width="118">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="famount"
                        :render-header="numLabel"
                        show-overflow-tooltip
                        width="56">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="famountpiece"
                        :render-header="sheetLabel"
                        show-overflow-tooltip
                        width="56">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fseries"
                        label=""
                        show-overflow-tooltip
                        width="72">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fboxmodel"
                        label=""
                        show-overflow-tooltip
                        width="60">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fboxlength"
                        label=""
                        show-overflow-tooltip
                        width="65">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fboxwidth"
                        label=""
                        show-overflow-tooltip
                        width="65">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fboxheight"
                        label=""
                        show-overflow-tooltip
                        width="65">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fmateriallength"
                        :render-header="blankingLongLabel"
                        show-overflow-tooltip
                        width="66">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fmaterialwidth"
                        :render-header="blankingWidthLabel"
                        show-overflow-tooltip
                        width="66">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fstavetype"
                        label=""
                        show-overflow-tooltip
                        width="85">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fhlineformula"
                        label=""
                        show-overflow-tooltip
                        width="123">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fvlineformula"
                        label=""
                        show-overflow-tooltip
                        width="175">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fhline"
                        label=""
                        show-overflow-tooltip
                        width="183">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fvline"
                        label=""
                        show-overflow-tooltip
                        width="212">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fproductarea"
                        :render-header="areaLabel"
                        show-overflow-tooltip
                        width="92">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="funitprice"
                        :render-header="priceLabel"
                        show-overflow-tooltip
                        width="98">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="famountprice"
                        :render-header="moneyLabel"
                        show-overflow-tooltip
                        width="73">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fcodeprovince"
                        label=""
                        show-overflow-tooltip
                        width="198">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="manufacturerName"
                        label=""
                        show-overflow-tooltip
                        width="202">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fpusername"
                        label=""
                        show-overflow-tooltip
                        width="120">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fconsignee"
                        label=""
                        show-overflow-tooltip
                        width="120">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fcontactway"
                        label=""
                        show-overflow-tooltip
                        width="120">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="faddressdetail"
                        label=""
                        show-overflow-tooltip
                        width="282">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="foperator"
                        label=""
                        width="100">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        label=""
                        width="153">
                        <template scope="scope">{{ scope.row.foperatetimeString }}</template>
                    </el-table-column>
                    <el-table-column label="" align="center" fixed="right" width="105" class="is-show">
                        <!--<el-table-column
                            align="center"
                            label=""
                            width="104"
                        >
                            <template scope="scope">
                                <el-button type="text" size="small" @click="handleIssue(scope.$index, scope.row)" :disabled="scope.row.orderOut"></el-button>
                            </template>
                        </el-table-column>-->
                        <el-table-column
                            align="center"
                            label=""
                            width="104"
                            >
                            <template scope="scope">
                                <el-button type="text" size="small" @click="handleCancel(scope.$index, scope.row)" :disabled="scope.row.orderCancel"></el-button>
                            </template>
                        </el-table-column>
                    </el-table-column>
                </el-table>
			</div>
            <el-dialog title="" :visible.sync="dialogTableVisible" class="cancelDialog" :modal-append-to-body=false @close="$store.commit('closeZindexModel')" @open="$store.commit('openZindexModel')">
                <h4 class="title"><i class="el-icon-warning" style="color: #fe9702; font-size: 34px;"></i><span>,?</span></h4>
                <div class="content">
                    <p><span style="color: #fec996; font-size: 16px;">{{ cancel.famountprice }}</span>,<span style="color: #fec996; font-size: 16px">{{ cancel.fgiveintegral }}</span></p>
                    <p>:</p>
                    <p><span style="color: #b6b6b6;"></span>&nbsp;{{ cancel.forderId }}</p>
                    <p><span style="color: #b6b6b6;"></span>&nbsp;{{ cancel.fcreatetimeString }}</p>
                    <p>
                        <span style="color: #b6b6b6;"></span>&nbsp;
                        <i v-if="balanceToggle"><span style="color: #e84449;">{{ cancel.fbalance }}</span><span v-if="cancel.fpaytype != 0">+</span></i>
                        <i v-if="paymentToggle">{{ paymentName }}<span style="color: #e84449;">{{ cancel.fthirdpay }}</span></i>
                    </p>
                    <p><span style="color: #b6b6b6;"></span>&nbsp;<span style="color: #e84449;">{{ cancel.productCount }}</span></p>
                    <p><span style="color: #b6b6b6;"></span>&nbsp;<span style="color: #e84449;">{{ cancel.ftotalprice }}</span></p>
                    <div style="margin-top: 16px;">
                        <el-input v-model.trim="backMoney" style="width: 100px;" :maxlength="14" @blur="doneBackMoney"></el-input>,
                        <el-input v-model.trim="backIntegral" style="width: 100px;" :maxlength="14" @blur="doneBackIntegral"></el-input>
                    </div>
                </div>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogTableVisible = false"> </el-button>
                    <el-button type="primary" @click="sureOrder"> </el-button>
                </div>
            </el-dialog>
		</div>
        <div class="order__current-page">
            <el-pagination
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :page-size="pageSize"
                layout="total, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
	</div>
</template>

<script>
	import tHeader from '@/components/public/tHeader.vue'
    import regionalChoice from '@/components/public/regionalChoice.vue'
    import { get3MonthBefor } from '@/util/common.js'
	export default {
		name: 'order',
		data() {
			return {
                actived: {
                    color: '#1d90e6',
                    borderColor: '#1d90e6',
                },
                isToday: true,
                isYesterday: false,
                tableLoading: false,
                dialogTableVisible: false,
                defaultRegion: '',
                defaultPlease: '',
                balanceToggle: false,       //
                paymentToggle: false,       //
                paymentName: '',            //
                //
                isDisableds: false,
                btnAuth: true,
                orderBtnAuth: '',

		        orderState: [
                    {
                        value: '-1',
                        label: ''
                    },
			        {
			          value: '1',
			          label: ''
			        },
                    {
			          value: '2',
			          label: ''
			        },
                    {
			          value: '3',
			          label: ''
			        },
                    {
                        value: '4',
                        label: ''
                    },
                    {
                        value: '5',
                        label: ''
                    },
                    {
                        value: '6',
                        label: ''
                    },
                    {
                        value: '7',
                        label: ''
                    }
		        ],
                partnerTable: [],

                partnerIndex: '',

                search: {
                    fkeyarea: '',                             //
                    manufacturerName: '',                   // 
                    fprovince: '',                            //
                    fcity: '',                                 //
                    fmanufacturer: '',                        //id

                    "fstatus":'',                             //
                    "custName":"",                            //
                    "fchildorderid":"",                      //  
                    "fgroupgoodname":"",                     //
                    "fboxlength":'',                          //
                    "fboxwidth":'',                           //
                    "fboxheight":'',                          //
                    "orderStartTime": new Date(new Date().setHours(0, 0, 0)),  //     undefined
                    "orderEndTime": new Date(),                             //
                    "deliveryStartTime": "",                 //
                    "deliveryEndTime": ""                    //
                },
                cancel: {},
                sureOrderMsg: {},                       //
                backMoney: '',                          //
                backIntegral: '',                       //

                //3disabled
                pickerOptions: {
                    disabledDate(time) {
                        return time.getTime() < get3MonthBefor() || time.getTime() > Date.now() - 8.64e7 + 86400000;
                    }
                },
                pickerOptionsOther: {
                    disabledDate(time) {
                        return time.getTime() < get3MonthBefor()
                    }
                },

                tableData: [],                      //table
                multipleSelection: [],          //

                currentPage: 1,                  //
                pageSize: 15,                    //
                total: 0,                         //
			}
		},
		components: {
			tHeader,
            regionalChoice
		},
        created() {
            if(this.admin.ucompany != '100') { //
                this.isPartner()
            }
        },
        computed: {
		    admin() {
		        return this.$store.getters.doneAdmin
            }
        },
        mounted() {
		    let area = document.querySelector('.carouse-select>span')
            area.innerHTML = '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; :'
        },
		methods: {
		    //
            isPartner() {
                this.isDisableds = true
                this.defaultRegion = this.admin.oprovince
                this.defaultPlease = this.admin.ocity
                this.partnerIndex = this.admin.oname

                this.search.fkeyarea = this.admin.ocode
                this.search.manufacturerName = this.admin.oname
                this.search.fprovince = this.admin.oprovince
                this.search.fcity = this.admin.ocity
                this.search.fmanufacturer = this.admin.ucompany

                this.$http.post('/partners/findPartnersById.do', {"oid": this.admin.ucompany, "ocode": this.admin.ocode}).then(res => {
                    if(res.data.success) {
                        this.orderBtnAuth = res.data.data.configuration
                        if(this.orderBtnAuth == 1) {   //1 2
                            this.btnAuth = true
                        }
                        else {
                            this.btnAuth = false
                        }
                    }
                    else {
                        this.$message.error('');
                    }
                }).catch(error => {
                    this.$message.error('');
                    console.log(error)
                })
            },
		    //
            filtersParameter() {
                let childOrderModel = {...this.search}
                if(childOrderModel.fstatus === '') {
                    childOrderModel.fstatus = -1
                }
                if(childOrderModel.fboxlength === '') {
                    childOrderModel.fboxlength = -1
                }
                if(childOrderModel.fboxwidth === '') {
                    childOrderModel.fboxwidth = -1
                }
                if(childOrderModel.fboxheight === '') {
                    childOrderModel.fboxheight = -1
                }
                if(childOrderModel.orderStartTime != '') childOrderModel.orderStartTime = formatDate(new Date(childOrderModel.orderStartTime))
                if(childOrderModel.orderEndTime != '') childOrderModel.orderEndTime = formatDate(new Date(childOrderModel.orderEndTime))
                if(childOrderModel.deliveryStartTime != '') childOrderModel.deliveryStartTime = formatDate(new Date(childOrderModel.deliveryStartTime))
                if(childOrderModel.deliveryEndTime != '') childOrderModel.deliveryEndTime = formatDate(new Date(childOrderModel.deliveryEndTime))
                return childOrderModel
            },
		    //
            getPartnerList() {
                this.$http.post('/partners/findOption.do', {city: this.search.fcity}).then( res => {
                    if(res.data.success) {
                        this.partnerTable = res.data.data
                    }
                }).catch( error => {
                    console.log(':' +  error)
                })
            },
		    //filters 
            getOrderList() {
                this.tableLoading = true
                let search = {
                    version: '1.0',
                    pageNum: this.currentPage,
                    pageSize: this.pageSize,
                    childOrderModel: this.filtersParameter()
                }
                this.$http.post('/order/load.do', search).then( res => {
                    if(res.data.success) {
                        if(!res.data.data.length) {
                            this.$message({
                                message: '',
                                duration: 1000,
                                type: 'info'
                            });
                            this.tableLoading = false
                        }
                        this.tableData = res.data.data
                        this.total = res.data.totalCount
                        for(let item of  this.tableData) {
                            switch(item.fstatus) {
                                case 1:
                                    item.fstatus = ''
                                    item.orderOut = true
                                    item.orderCancel = true
                                    break
                                case 2:
                                    item.fstatus = ''
                                    if(this.admin.ucompany == '100') {  //
                                        item.orderOut = true
                                        item.orderCancel = false
                                    }
                                    else {  //
                                        if(this.orderBtnAuth == 1) { //
                                            item.orderOut = true
                                            item.orderCancel = false
                                        }
                                        else {      //
                                            item.orderOut = false
                                            item.orderCancel = false
                                        }
                                    }
                                    break
                                case 3:
                                    item.fstatus = ''
                                    item.orderOut = true
                                    item.orderCancel = false
                                    break
                                case 4:
                                    item.fstatus = ''
                                    item.orderOut = true
                                    item.orderCancel = false
                                    break
                                case 5:
                                    item.fstatus = ''
                                    item.orderOut = true
                                    item.orderCancel = false
                                    break
                                case 6:
                                    item.fstatus = ''
                                    item.orderOut = true
                                    item.orderCancel = true
                                    break
                                case 7:
                                    item.fstatus = ''
                                    item.orderOut = true
                                    item.orderCancel = true
                                    break
                                default:
                                    item.fstatus = ''
                                    item.orderOut = true
                                    item.orderCancel = true
                            }
                        }
                        for(let item of  this.tableData) {
                            switch(item.fboxmodel) {
                                case '1':
                                    item.fboxmodel = ''
                                    break
                                case '2':
                                    item.fboxmodel = ''
                                    break
                                case '3':
                                    item.fboxmodel = ''
                                    break
                                case '4':
                                    item.fboxmodel = ''
                                    break
                                case '5':
                                    item.fboxmodel = ''
                                    break
                                case '6':
                                    item.fboxmodel = ''
                                    break
                                default:
                                    item.fboxmodel = ''
                            }
                        }
                    }
                    else {
                        this.tableLoading = false
                        this.$message({
                            message: '',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                }).catch( error => {
                    this.$notify.error({
                        title: '',
                        message: ''
                    });
                    this.tableLoading = false
                    console.log('' + error)
                })
            },
            //
            distributeOrder(childOrderIdArr){
                let parameter = {
                    "version": "1.0",
                    "fmanufacturer": this.search.fmanufacturer,
                    "operator": '',                         //id   
                    "fkeyarea": this.search.fkeyarea,
                    "childOrderIds": childOrderIdArr
                }
                this.$http.post('/order/distributionOrder.do', parameter).then( res => {
                    if(res.data.success) {
                        this.$message({
                            message: '',
                            duration: 1000,
                            type: 'success'
                        });
                        this.getOrderList()
                    }
                    else {
                        this.$message({
                            message: '',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                }).catch( error => {
                    this.$notify.error({
                        title: '',
                        message: ''
                    });
                    console.log(error)
                })
            },
		    //
            getRegion(region) {
                this.search.fprovince = region.name
            },
            getPlease(please) {
                this.partnerIndex = ''
                if(please.name != '') {
                    this.search.fcity = please
                    this.getPartnerList()
                }
            },
            //
            handleUnlock(index) {
                try {
                    this.search.fmanufacturer = this.partnerTable[index].id
                     this.search.manufacturerName = this.partnerTable[index].oname
                     this.search.fkeyarea = this.partnerTable[index].ocode
                }
                catch(e) {}
            },
            /**
             *  
             */
            doneToday() {
                let start = new Date()
                let end = new Date()
                start = start.setHours(0, 0, 0)
                this.search.orderStartTime =  start
                this.search.orderEndTime =  end
                this.$nextTick(function () {
                    this.isToday = true
                    this.isYesterday = false
                })
            },
            doneYesterday() {
                let time = new Date()
                time = new Date(time.getTime()-8.64e7)
                let start = time.setHours(0, 0, 0)
                let end = time.setHours(23, 59, 59)
                this.search.orderStartTime =  start
                this.search.orderEndTime =  end
                this.$nextTick(function () {
                    this.isToday = false
                    this.isYesterday = true
                })
            },
            changeTime() {
                this.isToday = false
                this.isYesterday = false
            },
            toFloat(name, value, num) {
                if(value === '') return
                if(/^\d+(\.\d+)?$/.test(value)) {
                    this.search[name] = (+value).toFixed(num)
                }
                else {
                    this.search[name] = ''
                    this.$message({
                        message: '',
                        duration: 1000,
                        type: 'warning'
                    });
                }
            },
            doneToFixed(key, index) {
                let num = this[key]
                if(num === '') return
                let v = parseInt(this[key]) + ''
                if(!/^\d+(\.\d+)?$/.test(num)) {
                    this[key] = ''
                    this.$message({
                        message: '',
                        duration: 1000,
                        type: 'info'
                    });
                }
                else if(v.length > 10) {
                    this[key] = ''
                    this.$message({
                        message: '10',
                        duration: 1000,
                        type: 'info'
                    });
                }
                else {
                    if(index === 0) {
                        this[key] = num
                    }
                    else {
                        this[key] = (+num).toFixed(index)
                    }
                }
            },
            doneBackMoney() {
                this.doneToFixed('backMoney', 2)
            },
            doneBackIntegral() {
                this.doneToFixed('backIntegral', 0)
            },
            //
            tableRowClassName(row, index) {
                if (index%2 === 1) {
                    return 'active-row';
                }
                return '';
            },
            // multipleSelection
            handleSelectionChange(row) {
                this.multipleSelection = row;
            },
            //
            numLabel(h, { column, $index }) {
                return h('div',[
                    '',
                    //h('p', '()')
                ])
            },
            sheetLabel(h, { column, $index }) {
                return h('div',[
                    '',
                    //h('p', '()')
                ])
            },
            blankingLongLabel(h, { column, $index }) {
                return h('div',[
                    '',
                    //h('p', '(cm)')
                ])
            },
            blankingWidthLabel(h, { column, $index }) {
                return h('div',[
                    '',
                    //h('p', '(cm)')
                ])
            },
            areaLabel(h, { column, $index }) {
                return h('div',[
                    '',
                    //h('p', '()')
                ])
            },
            priceLabel(h, { column, $index }) {
                return h('div',[
                    '',
                    //h('p', '(/)')
                ])
            },
            moneyLabel(h, { column, $index }) {
                return h('div',[
                    '',
                    //h('p', '()')
                ])
            },

            // excel,  
            searchFilter() {
                if(this.admin.ucompany != '100') {
                    if(this.orderBtnAuth === '') {
                        return
                    }
                }
                if(this.partnerIndex !== '') {
                    this.getOrderList()
                }
                else {
                    this.$message({
                        message: '',
                        duration: 1000,
                        type: 'warning'
                    });
                }

            },
            allOrderHandout() {
                if(!this.tableData.length) {
                    this.$message({
                        message: '',
                        duration: 1000,
                        type: 'warning'
                    });
                    return
                }
                let text = ''
                if(this.multipleSelection.length) {
                    text = ', ?'
                }
                else {
                    text = ', ?'
                }
                this.$confirm(text, '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let childOrderIdArr = []
                    if(this.multipleSelection.length) {
                        //
                        for(let item of this.multipleSelection) {
                            if(item.fstatus != '') {
                                this.$message({
                                    message: '',
                                    duration: 1000,
                                    type: 'warning'
                                });
                                return
                            }
                        }
                        for(let item of this.multipleSelection) {
                            if(item.fstatus == '') {
                                childOrderIdArr.push({"childOrderId": item.fchildorderid})
                            }
                        }
                    }
                    this.distributeOrder(childOrderIdArr)
                })
            },
            exportExcel() {
                if(!this.tableData.length) {
                    this.$message({
                        message: '',
                        duration: 1000,
                        type: 'warning'
                    });
                    return
                }
                this.$confirm('excel, ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let ids = []
                    if(this.multipleSelection.length) {
                        for(let item of this.multipleSelection) {
                            ids.push(item.fchildorderid)
                        }
                    }
                    let childOrderModel = {ids, ...this.filtersParameter()}
                    let parameter = {
                        version: '1.0',
                        flag: 1,
                        childOrderModel
                    }
                    this.$http.post('/order/exportExcel.do', parameter).then( res => {
                        if(res.data.success) {
                            location.href = res.data.data.furl
                        }
                        else {
                            this.$message({
                                message: 'excel',
                                duration: 1000,
                                type: 'error'
                            });
                        }
                    }).catch( error => {
                        this.$notify.error({
                            title: '',
                            message: ''
                        });
                        console.log(error)
                    })
                })
            },
            handleIssue(index, row) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.distributeOrder([{"childOrderId": row.fchildorderid}])
                })
            },
            handleCancel(index, row) {
                //
                this.sureOrderMsg = {
                    version: '1.0',
                    childOrderId: row.fchildorderid,              //
                    operator: '',                                   //id   
                    fkeyarea: row.fgokeyarea,                       //fgokeyarea
                    fpartnerFkeyarea: row.fkeyarea,                //fgokeyarea
                    userId: row.fpuserid,                           //id
                    keyArea: row.fgokeyarea,                        //
                    amount: '',                                     //
                    forderId: row.fchildorderid,                  //
                    fuserid: row.fpuserid,                           //id
                    fintegeral: '',                                  //
                    fpartnerid: row.fmanufacturer,                  //id
                    forderid: row.fchildorderid,                    //
                    fmanufacturerkeyarea: row.fkeyarea,             //
                    fmktplanchangeid: row.fmarktingplanid,           //id
                    fsalevolume: row.fproductarea,                   //
                    fmanufacturer: row.fmanufacturer               //id
                }

                let parameter = {
                    "version": "1.0",
                    "childOrderId": row.fchildorderid,
                    "fkeyarea": row.fkeyarea
                }
                this.cancel = {}
                this.$http.post('/order/cancelOrderInfo.do', parameter).then( res => {
                    if(res.data.success) {
                        this.cancel = res.data.data
                        this.backMoney = res.data.data.famountprice
                        this.backIntegral = res.data.data.fgiveintegral
                        /**
                         * 
                         * 0
                         * 1
                         * 2
                         * 3
                         * 10+
                         * 20+
                         * 30+
                         */
                        if(this.cancel.fpaytype === 0) {
                            this.balanceToggle = true
                            this.paymentToggle = false
                            return
                        }
                        if(this.cancel.fpaytype === 1) {
                            this.paymentName = ''
                            this.balanceToggle = false
                            this.paymentToggle = true
                            return
                        }
                        if(this.cancel.fpaytype === 2) {
                            this.paymentName = ''
                            this.balanceToggle = false
                            this.paymentToggle = true
                            return
                        }
                        if(this.cancel.fpaytype === 3) {
                            this.paymentName = ''
                            this.balanceToggle = false
                            this.paymentToggle = true
                            return
                        }
                        if(this.cancel.fpaytype === 10) {
                            this.paymentName = ''
                            this.balanceToggle = true
                            this.paymentToggle = true
                            return
                        }
                        if(this.cancel.fpaytype === 20) {
                            this.paymentName = ''
                            this.balanceToggle = true
                            this.paymentToggle = true
                            return
                        }
                        if(this.cancel.fpaytype === 30) {
                            this.paymentName = ''
                            this.balanceToggle = true
                            this.paymentToggle = true
                            return
                        }
                    }
                    else {
                        this.$message({
                            message: '',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                }).catch( error => {
                    this.$notify.error({
                        title: '',
                        message: ''
                    });
                    console.log(error)
                })
                this.dialogTableVisible = true
            },
            sureOrder() {
                if(this.backMoney === '' || this.backIntegral === '') {
                    this.$message({
                        message: '',
                        duration: 1000,
                        type: 'warning'
                    });
                    return
                }
                this.sureOrderMsg.amount = this.backMoney
                this.sureOrderMsg.fintegeral = this.backIntegral
                this.$http.post('/order/cancelOrder.do', this.sureOrderMsg).then( res=> {
                    if(res.data.success) {
                        this.$message({
                            message: '',
                            duration: 1000,
                            type: 'success'
                        });
                        this.dialogTableVisible = false
                        this.getOrderList()
                    }
                    else {
                        this.$message({
                            message: '',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                }).catch( error => {
                    this.$notify.error({
                        title: '',
                        message: ''
                    });
                    console.log(error)
                })
            },
            //
            handleCurrentChange(page) {     //
                this.getOrderList()
            }
		},
		watch: {
            tableData() {
                this.tableLoading = false
            }
        }
	}
    function formatDate(now) {
        let year=now.getFullYear();
        let month=now.getMonth()+1;
        let date=now.getDate();
        let hour=now.getHours();
        let minute=now.getMinutes();
        let second=now.getSeconds();
        let arr = [month, date, hour, minute, second]
        if(month<10) {
            month = '0' + month
        }
        if(date<10) {
            date = '0' + date
        }
        if(hour<10) {
            hour = '0' + hour
        }
        if(minute<10) {
            minute = '0' + minute
        }
        if(second<10) {
            second = '0' + second
        }
        return year+"-"+month+"-"+date+" "+hour+":"+minute+":"+second;
    }
</script>

<style>
	@import '../../../assets/style/order.css';
</style>



