function number(value, callback) {
    if(!/^\d+$/.test(value)) {
        callback(new Error(''));
    }
    else {
        callback();
    }
}
function float(value, callback) {
    if(!/^\d+(\.\d+)?$/.test(value)) {
        callback(new Error(''));
    }
    else {
        callback();
    }
}
/**
 *
 */
const isNumber = (rule, value, callback) => {
    if(value === '') {
        callback();
        return
    }
    number(value, callback)
};
/**
 * 
 */
const isFloat = (rule, value, callback) => {
    if(value === '') {
        if(rule.required) {
            callback(new Error(''));
        }
        else {
            callback();
            return
        }
    }
    float(value, callback)
};
/**
 * 
 */
function doneLenFloat(value, callback, index) {
    let v = parseInt(value) + ''
    if(!/^\d+(\.\d+)?$/.test(value)) {
        callback(new Error(''));
    }
    else if(v.length > index) {
        callback(new Error(`${index}`));
    }
    else {
        callback();
    }
}
//
const isLenFloatTwo = (rule, value, callback) => {
    if(value === '') {
        if(rule.required) {
            callback(new Error(''));
        }
        else {
            callback();
            return
        }
    }
    doneLenFloat(value, callback, 2)
};

/**
 * 
 */
const isBankcard = (rule, value, callback) => {
    if(value === '') {
        callback(new Error(''));
        return
    }
    number(value, callback)
};
/**
 * 
 */
const isFax = (rule, value, callback) => {
    if(value === '') {
        callback();
        return
    }
    if( !/^(\d{3,4}(-|\s))?\d{7,8}$/.test(value)) {
        callback(new Error(''));
    }
    else {
        callback();
    }
};
/**
 * 
 */
const isWebSite = (rule, value, callback) => {
    if(value === '') {
        callback();
        return
    }
    if( !/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/.test(value)) {
        callback(new Error(''));
    }
    else {
        callback();
    }
};

const rules = {
    registercapital: [  //
        { validator: isNumber, trigger: 'blur' }
    ],
    annualsales: [      //
        { validator: isNumber, trigger: 'blur' }
    ],
    enterprisenumber: [     //
        { validator: isNumber, trigger: 'blur' }
    ],
    plantarea: [     //
        { validator: isNumber, trigger: 'blur' }
    ],
    linenumber: [     //
        { validator: isNumber, trigger: 'blur' }
    ],
    linedoor: [     //
        { validator: isNumber, trigger: 'blur' }
    ],
    productquantity: [     //
        { validator: isNumber, trigger: 'blur' }
    ],
    carnumber: [     //
        { validator: isNumber, trigger: 'blur' }
    ],
    managenumber: [     //
        { validator: isNumber, trigger: 'blur' }
    ],
    rdnumber: [     //
        { validator: isNumber, trigger: 'blur' }
    ],
    fmassnumber: [     //
        { validator: isNumber, trigger: 'blur' }
    ],
    salesnumber: [     //
        { validator: isNumber, trigger: 'blur' }
    ],
    logisticsnumber: [     //
        { validator: isNumber, trigger: 'blur' }
    ],
    onelinenumber: [     //
        { validator: isNumber, trigger: 'blur' }
    ],
    linespeed:  [     //
        { validator: isNumber, trigger: 'blur' }
    ],
    avgcapacity: [     //
        { validator: isNumber, trigger: 'blur' }
    ],
    maxcapacity: [     //
        // { validator: isLenFloatTen, trigger: 'blur' }
        { validator: isNumber, trigger: 'blur' }
    ],


    opentime: [     //
        { validator: isLenFloatTwo, trigger: 'blur' }
    ],


    bankcard: [         //
        { validator: isBankcard, required: true, trigger: 'blur' }
    ],
    bank: [             //
        { required: true, message: '', trigger: 'blur' }
    ],
    accountholder: [       //
        { required: true, message: '', trigger: 'blur' }
    ],
    configuration: [   //
        { required: true, message: '', trigger: 'change' }
    ],


    fax: [      //
        { validator: isFax, trigger: 'blur' }
    ],
    website: [  //
        { validator: isWebSite, trigger: 'blur' }
    ],

}

export default rules



