            handleDelete(index, row) {
                alert(1)
            }
        }
    }
</script>

<style lang="scss" scoped>
    #businessmen {
        padding: 30px 30px 44px;
        .main {
            background: #fff;
            border-width: 0 1px 2px 1px;
            border-style: solid;
            border-color: #ededed;
            border-bottom-color: #e2e2e2;
            border-radius: 0 0 6px 6px;
            padding: 0 20px 187px;
            .header {
                padding-top: 14px;
                height: 50px;
                text-align: right;
            }
            .el-table {
                .remark {

                }
            }
        }
    }
</style>



