<template>
    <div class="userManage-info" v-show="infoShow" :style="{height:contentShow == 3?'520px':'590px'}">
        <h2>
            
        </h2>
        <div>
            <h3>
                
            </h3>
            <div class="userManage-info-warp"><table>
                <tr>
                    <td>: {{topdata.faccount}}</td>
                    <td>: {{topdata.fphone}}</td>
                    <td>: {{userbaseinfo.balance}}</td>
                </tr>
                <tr>
                    <td>: {{userbaseinfo.integral}}</td>
                    <td v-if="topdata.feffect == 0">: </td>
                    <td v-else="topdata.feffect == 1">: </td>
                    <td>: <span v-if="topdata.fcreateTime">{{topdata.fcreateTime.substring(0,10)}}</span></td>
                </tr>
            </table></div>
            <h3>
                
            </h3>
            <div class="userManage-info-warp">
                <table v-if="contentShow == 2"> <!-- -->
                <tr>
                    <td>: {{seeData.fcompanyName}}</td>
                    <td colspan="2" class="userManage-differentId">/: {{seeData.fregistrationNumber}}</td>
                </tr>
                <tr>
                    <td>: {{seeData.frepresentative}}</td>
                    <td class="userManage-companyType">: {{seeData.fcompanyType}}</td>
                    <td>: <span v-if="seeData.fregisteredCapital">{{seeData.fregisteredCapital}}</span></td>
                </tr>
                <tr>
                    <td>: {{seeData.ftaxNumber}}</td>
                    <td>: <span v-if="seeData.festablishDate">{{seeData.festablishDate.substring(0,10)}}</span></td>
                    <td>:<span v-if="seeData.fbusinesstermStart || seeData.fbusinesstermEnd"> {{seeData.fbusinesstermStart.substring(0,10)}}  {{seeData.fbusinesstermEnd.substring(0,10)}}</span></td>
                </tr>
                <tr>
                    <td colspan="3">: {{seeData.fcompanyAddress}}</td>
                </tr>
            </table>
                <table v-else-if="contentShow == 1" > <!-- -->
                    <tr>
                        <td>: {{seeData.fname}}</td>
                        <td>: {{seeData.fidNumber}}</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td v-if="seeData.fsex == 1">: </td>
                        <td v-else-if="seeData.fsex == 2">: </td>
                        <td v-else="seeData.fsex == 3">: &nbsp;</td>
                        <td>: {{seeData.fnation}}</td>
                        <td>: {{seeData.fbirthday}}</td>
                    </tr>
                    <tr>
                        <td>: {{seeData.fvalidDate}}</td>
                        <td colspan="2">: {{seeData.faddress}}</td>
                    </tr>
                </table>
                <table v-else="contentShow == 3" class="see-noContent"><!---->
                </table>
            </div>
            <h3>
                
            </h3>
            <div class="userManage-info-warp">
                <table>
	                <tr>
	                    <td>:
                            <span v-if="otherInfo.fsex == 1"> </span>
                            <span v-else-if="otherInfo.fsex == 2"></span>
                            <span v-else>&nbsp;</span>
                        </td>
	                    <td>:<span v-if="otherInfo.fbirthday">{{otherInfo.fbirthday.substring(0,otherInfo.fbirthday.length -2)}}</span></td>
	                    <td>:{{otherInfo.fprovince}}{{otherInfo.fcity}}</td>
	                </tr>
	                <tr>
	                    <td>:{{otherInfo.femail}}</td>
	                    <td colspan="2">:{{otherInfo.ffax}}</td>
	                </tr>
	                <tr>
	                    <td colspan="3">:{{otherInfo.femail}}</td>
	                </tr>
	                <tr>
	                    <td>:{{otherInfo.fcompanyName}}</td>
	                    <td>:{{otherInfo.fname}}</td>
                        <td>:<span v-if="otherInfo.fcreateTime">{{otherInfo.fcreateTime.substring(0,otherInfo.fcreateTime.length -2)}}</span></td>
	                </tr>
	                <tr>
	                    <td>:</td>
	                    <td>:</td>
	                    <td>:</td>
	                </tr>
	                <tr>
	                    <td>:</td>
	                    <td colspan="2">:</td>
	                </tr>
	                <tr>
	                    <td>:</td>
	                    <td colspan="2">:</td>
	                </tr>
	                <tr>
	                    <td colspan="3">:</td>
	                </tr>
	            </table>
            </div>
        </div>
        <div class="userManage-info-closeBtn" @click="closeInfo"></div>
    </div>
</template>
<script>
    export default{

        props:[
            'infoShow',  // 
            'contentShow', // 
            'seeData', // 
            'topdata', //
            'userbaseinfo', // 
            'otherInfo' //
        ],
        data(){
            return {}
        },
        mounted(){

        },
        methods: {
            closeInfo(){
                this.$emit('closeInfo');
            }
        }
    }
</script>
<style>
    /*
        
    */
    .see-noContent{
        height: 10px;
    }
    .userManage-info-closeBtn{
        position:absolute;
        width:26px;
        height: 26px;
        background: url(../../../../../static/images/publicImages/close.png) no-repeat 0 0;
        border-radius: 50%;
        right:-8px;
        top:-8px;
        cursor:pointer;
    }
    .userManage-info{
        width:912px;
        background: #fff;
        position: fixed;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        z-index: 100000;
        border-radius: 8px;
    }
    .userManage-info h2{
        height: 59px;
        border-bottom: 1px #6c6c6c solid;
        line-height: 59px;
        padding-left: 30px;
        font-weight:bold;
    }
    .userManage-info-warp{
        margin-left: 48px;
        line-height: 24px;
        padding: 6px 0 28px 0;
    }
    .userManage-info table{
        display: block;
    }
    .userManage-info table tr:nth-child(1) td:nth-child(1){
        width:324px;
    }
    .userManage-info table tr:nth-child(1) td:nth-child(2){
        width:260px;
    }
    .userManage-info table tr:nth-child(1) td:nth-child(2).userManage-differentId{
        width: 520px;
    }
    .userManage-info table tr:nth-child(1) td:nth-child(3){
        width:240px;
    }
    .userManage-info h3:nth-child(1){
        margin-top: 20px;
    }
    .userManage-info h3{
        height: 24px;
        width:836px;
        line-height: 24px;
        background: #d7efff;
        padding-left: 10px;
        font-weight: bold;
        margin: 0 auto;
        font-size: 14px;
    }
    .userManage-companyType{
        width:260px;
    }
</style>



