<template>
    <div class="partnerManage partner-propt">
        <tHeader></tHeader>
        <div class='partner-optionPrice-bg'>
            <div class='partner-select-left'>
                <div class='partner-select-region'>
                    <regional-choice
                        @getPleaseProvice='getPlease'
                        @getRegionProvice='getRegion'
                        :parterCode='-1'
                        :defaultPlease='defaultPlease'
                        :defaultRegion='defaultRegion'
                        :isDisableds='admin.ucompany == 100 && getProduct == undefined ? false : true'
                    >
                    </regional-choice>
                </div>
                <div class='partner-select-Prices'>
                    <partner
                       @getSelect='getPatnerId'
                        :codeName='codeName'
                        :partName='partnerName'
                        :isDisableds='admin.ucompany == 100 && getProduct == undefined ? false : true'
                    ></partner>
                </div>
                <div class='prchase-list partner-table partner-select-option partner-table-height'>
                    <el-table :data="leftTable" style="width: 100%" ref='leftTable'
                    @row-click='searchRight'
                    :highlight-current-row='true'
                    >
                        <el-table-column prop="fgrouparea" label="" width='110'>
                        </el-table-column>
                        <el-table-column prop="fsuppliername" label="" >
                        </el-table-column>
                        <el-table-column prop="fproductname" label="" >
                        </el-table-column>
                    </el-table>
                </div>
            </div>
            <div class='partner-select-right'>
                <div class='partner-select-font'></div>
                <span class='partner-select-span'>(10)</span>
                <div class='partner-select-btn'>
                     <el-button type="primary" @click='addNewPrice' :disabled='rightTable.length == 10 || isAdd ? true : false'></el-button>
                </div>
                <div class='partner-table prchase-list partner-select-table'>
                <el-table ref="multipleTable" :data="rightTable" border tooltip-effect="dark" 
                :row-class-name="tableRowClassName" style="width: 100%" 
                >
                    <el-table-column prop="number" label="" width="60">
                    </el-table-column>
                    <el-table-column prop="fmarketingname" label="" width='130'>
                    </el-table-column>
                    <el-table-column prop="viewType" label="" width='100'>
                    </el-table-column>
                    <el-table-column prop="type" label="" width="120">
                    </el-table-column>
                    <el-table-column prop="fgroupprice" label="" >
                    </el-table-column>
                    <el-table-column prop="fmaxarea" label="" >
                    </el-table-column>
                    <el-table-column prop="flimitedtime" label="" >
                    </el-table-column>    
                    <el-table-column label="" width='200' fixed="right">
                        <template scope="scope">
                            <div class='partner-table-checked'> 
                                <el-checkbox v-model="scope.row.checked" @change='changCheck(scope.row)' v-if="scope.row.fauditstatus == '2' ? true : false" :disabled="scope.row.disabled"></el-checkbox>
                            </div> 
                            <el-button type="text" 
                            :disabled='scope.row.checked' 
                            size="small" v-show="scope.row.fauditstatus != '1' ? true : false"
                            @click='edit(scope.row)'
                            >
                                
                            </el-button>
                            <el-button type="text" 
                            :disabled='scope.row.checked' 
                            size="small" v-show="scope.row.fauditstatus != '1' ? true : false"
                            @click='delpropt(scope.row)'
                            >
                                
                            </el-button>
                            <el-button type="text" 
                            size="small"
                             v-show="scope.row.fauditstatus == '0' || scope.row.fauditstatus == '3' ? true : false"
                             @click='examine(scope.row)'
                             >
                                
                            </el-button>
                            <el-button type="text" 
                            size="small" v-show="scope.row.fauditstatus == '1' ? true : false"
                            @click='revoke(scope.row)'
                            >
                                
                            </el-button>  
                        </template>
                    </el-table-column>
                </el-table>
                </div>
            </div>    
        </div>
        <div class='commodity-propt partner-product-propt' v-if='newPrice'>
            <el-dialog title="" :visible.sync="newPrice" :show-close='false'  @open='openDialog' 
            @close='closeDialog' :modal-append-to-body='false' :close-on-click-modal='false'>
                <img src='../../../../../static/images/publicImages/close.png' class='partaner-product-close' @click='newPrice = false'>
                <div class='partner-product-bg'>
                    <div class='partner-price-top'>
                         <common-input textName=':' @setValue='getValue' ref='fmarketingname' :defaultValue='fmarketingname' defaultName='30' :maxlength='30' ></common-input>
                         <div class='partner-price-div'>
                             <span class='partner-price-span'><i class='partner-icon'>*</i>:</span>
                             <el-radio class="radio" v-model="radio" label="1"></el-radio>
                             <el-radio class="radio" v-model="radio" label="2"></el-radio>
                         </div>
                    </div>
                    <div class='partner-plan-font partner-plan-margin'>
                        <span>(</span>
                        <i class='partner-icon'>*</i>
                        <span>
                            )
                        </span>
                    </div>
                    <div class='partner-input-left'>
                        <common-input textName=':' :error='errorAry[0].error' :errorPropt='errorAry[0].errorPropt' @setValue='getValue' defaultName='()' ref='fminarea' :defaultValue='fminarea'></common-input><span class='partner-input-company'></span>
                        <div v-show="radio == '1' ? true : false "><common-input textName=':' :error='errorAry[1].error' :errorPropt='errorAry[1].errorPropt' @setValue='getValue' defaultName='()' ref='fmaxarea' :defaultValue='fmaxarea' :maxlength='10'></common-input><span class='partner-input-total'></span></div>
                        <div class="block partner-selectTime " v-if="radio == '2' ? true : false ">
                            <i>*</i>
                            <span class="demonstration">:</span>
                            <el-time-select
                                v-model="endTime"
                                :picker-options="{
                                    start: startTime,
                                    step: '00:15',
                                    end: endTimes
                                }"
                                placeholder="">
                                </el-time-select>
                        </div>
                        <div class='partner-option-error'>
                            <common-input textName=':' :error='errorAry[3].error' :errorPropt='errorAry[3].errorPropt' @setValue='getValue' defaultName='(0100)' ref='funitprice' :defaultValue='funitprice' :maxlength='6'></common-input><span class='partner-input-market'>/</span>
                            <common-input textName=':' :error='errorAry[4].error' :errorPropt='errorAry[4].errorPropt' @setValue='getValue' defaultName='(0100)' ref='fsmallprice' :defaultValue='fsmallprice' :maxlength='6'></common-input><span class='partner-input-smallPrice'>/</span>
                        </div>
                    </div>
                    <div class='partner-input-right'>
                        <div class='partner-right-one'>
                            <common-input textName=':' :error='errorAry[2].error' :errorPropt='errorAry[2].errorPropt' @setValue='getValue' defaultName='()' ref='fnormalarea' :defaultValue='fnormalarea'></common-input><span class='partner-input-company'></span>
                        </div>
                        <div class='partner-right-two'>
                        <common-input textName=':':error='errorAry[5].error' :errorPropt='errorAry[5].errorPropt' @setValue='getValue' defaultName='(0100)' ref='fgroupprice' :defaultValue='fgroupprice' :maxlength='6'></common-input><span class='partner-input-group'>/</span>
                        </div>
                    </div>
                    <div class='partner-plan-font'>
                        <span>(</span>
                        <i class='partner-icon'>*</i>
                        <span>
                            )
                        </span>
                    </div>
                    <div class='partner-img-two'>
                        <div class='partner-plan-img partner-price-img'>
                            <p class='partner-add-img'>
                                <i class='partner-icon'>*</i>&nbsp;:
                            </p>
                            <div class='partner-upload partner-upload-first' v-if='!IE'>
                                <div class='partner-upload-font-first' >
                                    <p>jpg/png</p>
                                    <p>RGB, < 2M </p>
                                    <p>1:1</p>
                                </div>
                                <el-upload 
                                    class="avatar-uploader"
                                    :show-file-list="false" 
                                    :auto-upload='false'
                                    action=''
                                    :on-change="pricelist"
                                    >
                                    <img v-if="productUrl" :src="productUrl" class="avatar" >
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                            </div>
                            <div class='IE-upload IE-option' v-if='IE'>
                                <upload-ie :checkData='checkIEData' 
                                :uploadDate='uploadDateFirst' 
                                :uploadSrc='uploadSrcFirst'
                                @addImgSuccess='addFirstSuccess' 
                                uploadFormsId='uploadForm'
                                action='djcrm/product/uploadFigure.do'
                                ></upload-ie>
                                <div class='partner-upload-font-first'>
                                    <p>jpg/png</p>
                                    <p>RGB < 2M </p>
                                    <p>1:1</p>
                                </div>
                                <i class="el-icon-plus avatar-uploader-icon"></i>
                            </div>
                        </div>
                        <div class='partner-plan-endImg partner-price-img'>
                            <p class='partner-add-img'>
                                <i class='partner-icon'>*</i>&nbsp;:
                            </p>
                            <div class='partner-upload partner-upload-first' v-if='!IE'>
                                <div class='partner-upload-font-first' >
                                    <p>jpg/png</p>
                                    <p>RGB < 2M </p>
                                    <p>360px*360px</p>
                                </div>
                                <el-upload 
                                    class="avatar-uploader"
                                    :show-file-list="false" 
                                    :auto-upload='false'
                                    action=''
                                    :on-change="endGroup"
                                    >
                                    <img v-if="endGroupUrl" :src="endGroupUrl" class="avatar" >
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                            </div>
                            <div class='IE-upload IE-option' v-if='IE'>
                                <upload-ie :checkData='checkIEData' 
                                :uploadDate='uploadDateTwo' 
                                :uploadSrc='uploadSrcTwo'
                                @addImgSuccess='addTwoSuccess' 
                                uploadFormsId='uploadFormsId'
                                action='djcrm/product/uploadFigure.do'
                                ></upload-ie>
                                <div class='partner-upload-font-first'>
                                    <p>jpg/png</p>
                                    <p>RGB < 2M </p>
                                    <p>360*360</p>
                                </div>
                                <i class="el-icon-plus avatar-uploader-icon"></i>
                            </div>
                        </div>
                    </div>
                </div>
                <div class='partner-btn-propt partner-new-price'>
                    <el-button @click='newPrice = false'></el-button>
                    <el-button type="primary" @click="save('0')"></el-button>
                    <el-button type="primary" @click="save('1')"></el-button>
                </div>
            </el-dialog>
        </div>
        <propt
            :dialogVisible = 'isDel'
            text=''
            :needCancel = 'true'
            @closeVisible='isDel = false'
            @sure='delData'
        ></propt>
    </div>
</template>

<script>
import carouseSelect from '@/components/public/select'
import tHeader from '@/components/public/tHeader'
import regionalChoice from '@/components/public/regionalChoice'
import partner from '@/components/public/Partner'
import commonInput from './input'
import {VERSION, IMGPATH} from '@/util/constant.js'
import propt from '@/components/public/propt' 
import uploadIe from '@/components/public/uploadIE'
import {clippingProvice, clippingCity, checkNumber, checkDecimal} from '@/util/common.js'
export default {
    props:['getProductData'],
    created(){
        this.admin = this.$store.getters.doneAdmin;
        this.fcreator = this.admin.ucompany;
        this.parterId = this.admin.ucompany;    //id
        //
        this.fkeyarea = this.admin.ocode;
        //
        if(this.admin.ucompany != 100){
           this.defaultRegion = clippingProvice(this.admin.oprovince);
           this.defaultPlease = clippingCity(this.admin.ocity);
           setTimeout(()=> {
               this.partnerName = this.admin.oname;    //
           }, 1);
           this.searchLeft();
        }
        if(this.getProduct != undefined){
            this.parterId = this.getProduct.fsupplierid;
            this.fkeyarea = this.getProduct.fkeyarea;
            var index = this.getProduct.fgrouparea.indexOf('-');             
            this.defaultRegion = this.getProduct.fgrouparea.slice(0,index);
            this.defaultPlease =this.getProduct.fgrouparea.slice(index + 1, this.getProduct.fgrouparea.length);
            setTimeout(()=> {
                this.partnerName = this.getProduct.fsuppliername;
            }, 1);
            let data = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.parterId,
                pagenum:0,
                pagesize:1000
            }
            this.searchList(data);
            this.searchRight(this.getProduct);
        }
        if(navigator.appName == "Microsoft Internet Explorer" && 
            navigator.appVersion .split(";")[1].replace(/[ ]/g,"") == "MSIE9.0"){
            this.IE = true;  
            return
        } 
        let data = {
            version:VERSION,
            fkeyarea:this.fkeyarea
        }
        //
        this.$http.post('/product/getGroupOpenCycle.do', data).then(res => {
            if(res.data.success){
                let data = res.data.data
                this.startTime = data.fstarttime;
                this.endTimes = data.fendtime 
            }else{
                 this.$message({
                    message: res.data.msg,
                    type: 'error',
                    duration: 2000
                 });
             }
        }).catch(function (error) {
             console.log(error);
        });
    },
	data () {
		return {
            startTime:'',
            endTimes:'',
            IE:false,
            uploadSrcFirst:'',
            uploadSrcTwo:'',
            checkIEData:{
                size:2,
                Proportion:1,
                minWidth:360,
                minHeight:360,
                needPropt:false
            },
            uploadDateFirst:[],
            uploadDateTwo:[],
            errorAry:[
                {
                    error:false,
                    errorPropt:'',
                },
                {
                    error:false,
                    errorPropt:'',
                },
                {
                    error:false,
                    errorPropt:'',
                },
                {
                    error:false,
                    errorPropt:'(0100),',
                },
                {
                    error:false,
                    errorPropt:'(0100),',
                },
                {
                    error:false,
                    errorPropt:'(0100),',
                }
            ],
            showMasksed:false,
            showMask:false,
            partnerName:'',
            defaultPlease:'',
            defaultRegion:'',
            getProduct:this.getProductData,
            admin:{},
            isDel:false,
            newPrice:false,
            radio:'1',
            codeName:'',               //
            fkeyarea:'',               //
            parterId:'',               //ID
            productUrl: '',            //
            endGroupUrl:'',            //
            endTime:'',                //
            fmarketingname:'',         //     
            fminarea:'',               //
            fmaxarea:'',               //
            funitprice:'',             //
            fsmallprice:'',            //
            fnormalarea:'',            //
            fgroupprice:'',            //
            isPricelist:true,          //
            fmarketingplanid:'',       //ID
            fgroupgoodid:'',           //ID
            fcreator:'',               //ID
            fid:'',                    //ID
            deleteData:{},             //
            imgEndFid:'',
            imgStartFid:'',
            rightTable: [],
            leftTable: [],
            startfigure:{},
            endfigure:{},
            typeArry:{
                '0':'',
                '1':'',
                '2':'',
                '3':''
            },
            isAdd:true,
            checkAry:[
                "fmarketingname",
                'fminarea',
                'fnormalarea',
                'funitprice',
                'fsmallprice',
                'fgroupprice'
            ]
		}
	},
	methods:{
        //
        tableRowClassName(row, index) {
            if (((index + 1) % 2) === 0) {
                return 'info-row';
            }
            return '';
        },
        //code
        getPlease(please){
             this.codeName = please;
             this.rightTable = [];
             this.leftTable = [];
        },
        //
        getRegion(){
            this.codeName = '';
            this.rightTable = [];
            this.leftTable = [];
        },
        //
        getPatnerId(data){
            if(data.id == undefined && data.code == undefined){
                return
            }
            this.parterId = data.id;
            this.fkeyarea = data.code;
            this.searchLeft();
            this.rightTable = [];
        },
        //
        getValue(){
           var ref = this.$refs
           this.fmarketingname = ref.fmarketingname.input//

          
           this.errorAry[0].error = checkNumber(ref.fminarea.input);
           this.errorAry[1].error = checkNumber(ref.fmaxarea.input);
           this.errorAry[2].error = checkNumber(ref.fnormalarea.input);

           this.fminarea = ref.fminarea.input//
           this.fmaxarea = ref.fmaxarea.input//
           this.fnormalarea = ref.fnormalarea.input//

     
           this.errorAry[3].error = checkDecimal(ref.funitprice.input, 100, true);
           this.errorAry[4].error = checkDecimal(ref.fsmallprice.input, 100, true);
           this.errorAry[5].error = checkDecimal(ref.fgroupprice.input, 100, true);
           
           this.funitprice = ref.funitprice.input//
           this.fsmallprice = ref.fsmallprice.input //
           this.fgroupprice = ref.fgroupprice.input //
        },
        //
        pricelist(files){
           this.isPricelist = true;
           this.beforeAvatarUpload(files);
        },
        //
        endGroup(files){
           this.isPricelist = false;
           this.beforeAvatarUpload(files);
        },
        //
        beforeAvatarUpload(files) {
            var file = files.raw;
            var img = new Image();
            img.src = files.url;
            const isJPG = file.type === 'image/jpeg';
            const isPNG = file.type === 'image/png';
            const isLt2M = file.size / 1024 / 1024 < 2;
            if (!isJPG && !isPNG) {
                this.$message.error(' JPG  PNG !');
                return 
            }
            if (!isLt2M) {
                this.$message.error(' 2MB!');
                return
            }
            var q = this;
            img.onload = function(){
                if(img.width < 360 || img.height < 360 || img.width !== img.height){
                    q.$message.error('360! 1:1');
                    return
                }
                var imgData = new FormData();
                imgData.append('file', file);
                imgData.append('moduleid', 'P11000');
                imgData.append('version', VERSION);
                imgData.append('fkeyarea', q.fkeyarea);
                q.$http.post('/product/uploadFigure.do', imgData).then(res => {
                    if(res.data.success){
                        if(q.isPricelist){
                            q.productUrl = files.url;
                        }else{
                            q.endGroupUrl = files.url;
                        }
                        if(q.isPricelist){
                            q.startfigure={
                                fuserid: q.parterId,
                                ftype: 1,
                                ffileid: res.data.data.fileid,
                                furl: res.data.data.fileurl,
                                fkeyarea: q.fkeyarea
                            }
                        }else{
                            q.endfigure={
                                fuserid: q.parterId,
                                ftype: 2,
                                ffileid: res.data.data.fileid,
                                furl: res.data.data.fileurl,
                                fkeyarea: q.fkeyarea
                            }
                        }
                    }else{
                        this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                    }
                }).catch(function (error) {
                        console.log(error);
                });
            }
        },
        //
        save(num){
            let data ={
                 version:VERSION,
                 fkeyarea:this.fkeyarea,
                 fmarketingplanid:this.fmarketingplanid,
                 fgroupgoodid:this.fmarketingplanid != '' ? '' : this.fgroupgoodid,
                 fmarketingname:this.fmarketingname,
                 funitprice:this.funitprice,
                 fsmallprice:this.fsmallprice,
                 fgroupprice:this.fgroupprice,
                 fnormalarea:this.fnormalarea,
                 fminarea:this.fminarea,
                 fgrouptype:this.radio,
                 fmaxarea:this.fmaxarea,
                 fsupplierid:this.parterId,
                 fcreator:this.fcreator,
                 flimitedtime:this.endTime,
                 startfigure:this.startfigure,
                 endfigure:this.endfigure,
                 fauditstatus:num,
            }
            for(let i=0; i<this.checkAry.length; i++){
               if(data[this.checkAry[i]] == '' || 
               this.radio == '1' && this.fmaxarea == '' || 
               this.radio == '2' && this.endTime == ''){
                   this.$message({
                        message: '',
                        type: 'error',
                        duration: 2000
                    });
                    return
               }
            }
            for(let i=0; i<this.errorAry.length; i++){
                if(this.errorAry[i].error){
                    this.$message({
                        message: '',
                        type: 'warning',
                        duration: 1500
                    });
                    return
                }
            }
            if(!this.startfigure.fuserid || !this.endfigure.fuserid){
                    this.$message({
                        message: '',
                        type: 'warning',
                        duration: 1500
                    });
                return
            }
            this.$http.post('/product/addOrEditProgramme.do', data).then(res => {
                     if(res.data.success){
                        this.$message({
                            message: '',
                            type: 'success',
                            duration: 1500
                        });
                        this.newPrice =false;
                        let data={
                            version:VERSION,
                            fkeyarea:this.fkeyarea,
                            fid:this.fid,
                        }
                        this.searchRightList(data);
                     }else{
                         this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                     }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //
        searchLeft(){
            let data = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.parterId,
                pagenum:0,
                pagesize:1000
            }
            if(this.fkeyarea == undefined || this.parterId == undefined){
                return
            }
            this.searchList(data);
        },
        //
        searchList(data){
            this.$http.post('/product/loadProducts.do', data).then(res => {
                if(res.data.success) {
                    this.leftTable = res.data.data;
                    if(this.getProduct != undefined){
                        for(let i=0; i<this.leftTable.length; i++){
                            if(this.leftTable[i].fid == this.getProduct.fid){
                                setTimeout(()=> {
                                    this.$refs.leftTable.setCurrentRow(this.leftTable[i])
                                }, 10);
                            }
                        }
                    }
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error',
                        duration: 2000
                    });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //
        searchRight(row){
            this.fgroupgoodid = row.fid;
            this.fcreator = row.fcreatorid;
            this.fid = row.fid;
            let data={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fid:row.fid,
            }
            this.searchRightList(data);
        },
        //
        searchRightList(data){
           this.$http.post('/product/loadProgrammes.do', data).then(res => {
                if(res.data.success){
                    this.rightTable = res.data.data;
                    this.isAdd = false;
                    var disabledNumber = 0;
                    for(let i=0; i<this.rightTable.length; i++){
                        this.rightTable[i].number = i + 1;
                        this.rightTable[i].disabled = true;
                        this.rightTable[i].type = this.rightTable[i].fgrouptype == '1' ? '' : this.rightTable[i].fgrouptype == '2' ? '' :'';
                        this.rightTable[i].viewType = this.typeArry[this.rightTable[i].fauditstatus];
                        this.rightTable[i].checked = this.rightTable[i].feffect == '0' ? false : true; 
                        if(this.rightTable[i].checked){
                            this.rightTable[i].disabled = false;
                            disabledNumber = 1;
                        }
                        this.rightTable[i].flimitedtime = this.rightTable[i].fgrouptype == '1' ? '/' : this.rightTable[i].flimitedtime;
                        this.rightTable[i].fmaxarea = this.rightTable[i].fgrouptype == '2' ? '/' : this.rightTable[i].fmaxarea;
                    }
                    if(disabledNumber == 0){
                        for(let i=0; i<this.rightTable.length; i++){
                            this.rightTable[i].disabled = false;
                        }
                    }
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error',
                        duration: 2000
                    });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //
        edit(datas){
            for(let i=0; i<this.errorAry.length; i++){
                this.errorAry[i].error = false;
            }
            this.productUrl = '';
            this.endGroupUrl= '';
            this.uploadSrcFirst='';
            this.uploadSrcTwo='';
            let detalData ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fmarketingplanid:datas.fmarketingplanid
            }
            var data
            this.$http.post('/product/loadProgrammesDetails.do', detalData).then(res => {
                if(res.data.success){
                    data=res.data.data;
                    this.newPrice = true;
                    this.fmarketingplanid = data.fmarketingplanid;
                    this.endTime= data.flimitedtime;            //
                    this.fmarketingname= data.fmarketingname;   //     
                    this.fminarea= data.fminarea;                //
                    this.fmaxarea= data.fmaxarea;                //
                    this.funitprice= data.funitprice;            //
                    this.fsmallprice= data.fsmallprice;          //
                    this.fnormalarea= data.fnormalarea;          //
                    this.fgroupprice= data.fgroupprice;          //
                    this.radio = data.fgrouptype + '';
                    this.productUrl = IMGPATH + data.startfigure.furl;      //
                    this.endGroupUrl = IMGPATH + data.endfigure.furl;        //
                    this.startfigure = data.startfigure;
                    this.endfigure = data.endfigure;
                    this.imgEndFid = data.endfigure.fid;
                    this.imgStartFid = data.startfigure.fid;
                    //IE9
                    this.uploadSrcFirst = IMGPATH + data.startfigure.furl;
                    this.uploadSrcTwo = IMGPATH + data.endfigure.furl;
                }
            }).catch(function (error) {
                console.log(error);
            });
            
        },
        //
        addNewPrice(){
            for(let i=0; i<this.errorAry.length; i++){
                this.errorAry[i].error = false;
            }
            this.newPrice = true;
            this.productUrl = '';      //
            this.endGroupUrl= '';         //
            this.endTime='';              //
            this.fmarketingname= '';    //     
            this.fminarea= '';                //
            this.fmaxarea= '';                //
            this.funitprice= '';            //
            this.fsmallprice= '';          //
            this.fnormalarea='';          //
            this.fgroupprice= '';          //
            this.radio  = '1';
            this.fmarketingplanid = '';    //ID
            this.imgStartFid = ''; 
            this.imgEndFid = '';
            this.startfigure = {};
            this.endfigure= {};
            this.uploadSrcFirst='';
            this.uploadSrcTwo='';
        },
        //
        changCheck(datas){
            if(datas.checked){
                datas.feffect = '1';
            }else{
                datas.feffect = '0';
            }
            let data ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fmarketingplanid:datas.fmarketingplanid,
                fgroupgoodid:this.fgroupgoodid,
                operate:datas.checked
            }
            this.$http.post('/product/activateProgrammes.do', data).then(res => {
                if(res.data.success){
                    var text = datas.feffect == '0' ?  '' : ''
                    this.$message({
                        message: text,
                        type: 'success',
                        duration: 1500
                    });
                    let data={
                        version:VERSION,
                        fkeyarea:this.fkeyarea,
                        fid:this.fid,
                    }
                    this.searchRightList(data);
                }else{
                    if(res.data.msg == ''){
                        this.$message({
                            message: '',
                            duration: 1500
                        });
                    }
                    datas.feffect = '0';
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //
        examine(datas){
            let data ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fmarketingplanid:datas.fmarketingplanid
            }
           this.$http.post('/product/applyeExamineProgrammes.do', data).then(res => {
                if(res.data.success){
                    this.$message({
                        message: '',
                        type: 'success',
                        duration: 1500
                    });
                    let data={
                        version:VERSION,
                        fkeyarea:this.fkeyarea,
                        fid:this.fid,
                    }
                    this.searchRightList(data);
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error',
                        duration: 2000
                    });
                }
           }).catch(function (error) {
                console.log(error);
           });
        },
        //
        delpropt(datas){
            this.isDel = true;
            this.deleteData ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fmarketingplanid:datas.fmarketingplanid
            }
        },
        //
        delData(){
            this.$http.post('/product/deleteProgrammes.do', this.deleteData).then(res => {
                if(res.data.success){
                    this.$message({
                        message: '',
                        type: 'success',
                        duration: 1500
                    });
                    this.isDel = false;
                    let data={
                        version:VERSION,
                        fkeyarea:this.fkeyarea,
                        fid:this.fid,
                    }
                    this.searchRightList(data);
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error',
                        duration: 2000
                    });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //
        revoke(datas){
            let data ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fmarketingplanid:datas.fmarketingplanid
            }
            this.$http.post('/product/revokeApplyProgramme.do', data).then(res => {
                if(res.data.success){
                    this.$message({
                        message: '',
                        type: 'success',
                        duration: 1500
                    });
                    let data={
                        version:VERSION,
                        fkeyarea:this.fkeyarea,
                        fid:this.fid,
                    }
                    this.searchRightList(data);
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error',
                        duration: 2000
                    });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
