        openDialog(){
            this.$store.commit('openZindexModel');
        },
        closeDialog(){
            this.$store.commit('closeZindexModel');
        },
        //IE9
        addFirstSuccess(){
            this.uploadDateFirst=[
                {name:'moduleid', data:'P11000'},
                {name:'version', data:VERSION},
                {name:'fkeyarea', data:this.fkeyarea}
            ]
            setTimeout(()=> {
                document.getElementById("uploadForm").submit();
            }, 200);
            setTimeout(()=> {
                var str = document.getElementById("uploadFrame").contentWindow.document.body.textContent;
                var res = JSON.parse(str);
                if(res.success){
                    this.startfigure={
                        fuserid: this.parterId,
                        ftype: 1,
                        ffileid: res.data.fileid,
                        furl: res.data.fileurl,
                        fkeyarea: this.fkeyarea
                    }
                }
            }, 500);
        },
        //IE9
        addTwoSuccess(){
            this.uploadDateTwo=[
                {name:'moduleid', data:'P11000'},
                {name:'version', data:VERSION},
                {name:'fkeyarea', data:this.fkeyarea}
            ]
            setTimeout(()=> {
                document.getElementById("uploadFormsId").submit();
            }, 200);
            setTimeout(()=> {
                var str = document.getElementById("uploadFrame").contentWindow.document.body.textContent;
                var res = JSON.parse(str);
                if(res.success){
                    this.endfigure={
                        fuserid: this.parterId,
                        ftype: 2,
                        ffileid: res.data.fileid,
                        furl: res.data.fileurl,
                        fkeyarea: this.fkeyarea
                    }
                }
            }, 500);
        }
